import runAfterSCisSetMixin from '../runAfterSCisSetMixin'
import ContactsTable from './ContactsTable/index'
import HoldingTab from './HoldingTab/index'
import RelatedPartiesTable from './RelatedPartiesTable/index'
import InvitationsTable from './InvitationsTable/index'
import InvitationForm from './InvitationForm/index'
import InvitationMyCompaniesForm from './InvitationMyCompaniesForm/index'


export default {
  name: 'ContactsPage',
  components: {
    ContactsTable,
    HoldingTab,
    RelatedPartiesTable,
    InvitationsTable,
    InvitationForm,
    InvitationMyCompaniesForm,
  },
  mixins: [runAfterSCisSetMixin],
  data() {
    return {
      showInvModal: false,
      showInvMyCompanyModal: false,
      loading: false,
      activeTabId: 0,
    }
  },
  computed: {
    hasContacts() {
      return !!this.$store.getters.contacts.length
    },
    hasInvitations() {
      return !!this.$store.getters.companySentInvitations.length
    },
  },
  methods: {
    onInvModalOk(event) {
      event.preventDefault()
      this.$refs.invitationForm.submit()
    },
    onInvMyCompanyModal(event) {
      event.preventDefault()
      this.$refs.invitationMyCompaniesForm.submit()
    },
    openInvModal() {
      this.$refs.invitationForm && this.$refs.invitationForm.resetForm()
      this.showInvModal = true
    },
    openInvMyCompanyModal() {
      this.showInvMyCompanyModal = true
    },
    onSentInvitation(invitation) {
      this.showInvModal = false
    },
    onSentMyCompaniesInvitation(invitation) {
      this.showInvMyCompanyModal = false
      this.loadContacts()
    },
    onTabChange(index) {
      this.activeTabId = index
      switch (index) {
        case 1:
          console.log('')
          break
        case 2:
          this.$nextTick(() => {
            this.$refs.relatedPartiesTable.loadRelatedParties()
          })
          break
        case 3:
          this.runAfterSCisSet(this.loadInvitations)
          break
        default:
          this.runAfterSCisSet(this.loadContacts)
      }
    },
    loadContacts() {
      this.loadStore('loadSCContacts')
    },
    loadInvitations() {
      this.loadStore('loadSCSentInvitations')
    },
    loadStore(action) {
      this.loading = true
      this.$store.dispatch(action)
        .finally(() => {
          this.loading = false
        })
    },
  },
}



