import rcRecordsModal from './rcRecordsModal/index'

export default {
  components: {
    rcRecordsModal,
  },
  data() {
    return {
      selectedSet: null,
    }
  },
  computed: {
    sets() {
      return this.$store.getters.recognizedCompaniesSets
    },
    context() {
      return this.$store.getters.statementTypeContext
    },
  },
  methods: {
    show(set) {
      this.selectedSet = set
    },
    remove(set) {
      this.$store.dispatch('removeValidSet', set)
        .then(this.reloadData)
    },
    reloadData() {
      this.$store.dispatch('loadSummary')
    },
  },
}



