<template>
  <cp-form :save="save"
           :remove="remove"
           :cancel="cancel"
           :validate="validate"
           :edit-mode="!cProp.num"
           @edit="onEdit">

    <CountrySelect
      v-model="cpModel.country"
      :disabled="!editMode"
      componentClass="cp-row"
    />

    <div class="cp-row">
      <label>{{ (cpModel.countryId === 1) ? $t('VAT ID') : $t('Sales Tax ID') }}:</label>
      <input
        type="text"
        class="form-control"
        v-model="cpModel.num"
        name="num"

        v-validate="'required'"
        :class="[{ 'is-invalid': errors.has('num')}]"
        :disabled="!editMode" />
    </div>

    <div class="cp-row">
      <label>{{$t('Reporting Regularity')}}:</label>
      <RecurringDateModal
        v-model="cpModel.reportingRecurringDate"
        name="repReg"
        v-validate="'required'"
        :class="[{'form-control': true, 'is-invalid': errors.has('repReg'), 'inactive': !editMode}]"
        :disabled="!editMode"
      ></RecurringDateModal>
    </div>

  </cp-form>
</template>


<script>
  import merge from 'lodash.merge'
  import basePropMixin from './basePropMixin'
  import Multiselect from 'vue-multiselect'
  import RecurringDateModal from '@/components-app/modals/RecurringDateModal'
  import CountrySelect from '@/components-app/selects/CountrySelect'

  const vatVueConfig = {
    components: {
      Multiselect,
      RecurringDateModal,
      CountrySelect,
    },
    props: {
      cProp: {
        required: false,
        default() {
          return {}
        },
      },
    },
    data() {
      return {
        cpModel: Object.assign({}, this.cProp),
        editMode: !this.cProp.num,
      }
    },
    methods: {
      save() {
        const action = this.cProp.num ? 'updateSCVat' : 'createSCVat'
        this.loading = true
        return this.$store.dispatch(action, this.cpModel)
          .then(() => {
            this.$emit('success')
          })
      },
      remove() {
        return this.$store.dispatch('deleteSCVat', this.cProp)
          .then(() => {
            this.$emit('remove')
          })
      },
    },
    watch: {
      cProp: {
        handler(value) {
          this.cpModel = Object.assign({}, value)
          this.loading = false
        },
        deep: true,
      },
    },
  }

  export default merge({}, basePropMixin, vatVueConfig)
</script>



