import Datepicker from 'vuejs-datepicker'
import {en, bg} from 'vuejs-datepicker/dist/locale'

export default {
  components: {
    Datepicker,
  },
  data() {
    return {
      locales: {
        en: en,
        bg: bg,
      },
      question: 'Are you sure ?',
      advanced: false,
      date: Date.now(),
    }
  },
  methods: {
    openModal(question) {
      this.question = (question !== null) ? question : this.$t('Are you sure ?')
      this.$refs.modal.show()
    },
    openQuestion(question) {
      this.advanced = false
      this.openModal(question)
    },
    openDateQuestion(question, options) {
      this.options = options
      this.advanced = true
      this.openModal(question)
    },
    confirm() {
      this.$emit('confirm', this.date)
      this.$refs.modal.hide()
    },
    cancel() {
      this.$emit('cancel')
      this.$refs.modal.hide()
    },
  },
}



