import axios from 'axios'
import fileSaver from 'file-saver'

import dateUtils from '@/helpers/dateUtils'


export default {

  downloadRecords(filters, title) {
    const url = [
      'ExportExcel',
      'Companies',
      filters.selectedCompany.id,
      'StatementTypes',
      filters.statementTypeId,
      'StartDate',
      filters.startDate ? dateUtils.formatDate(filters.startDate) : '1900-01-01',
      'EndDate',
      filters.endDate ? dateUtils.formatDate(filters.endDate) : null,
    ].join('/')

    const queryParams = [
      'oppositeCompanyId=' + filters.oppCompany.id,
      'payables=' + !!filters.areIN,
      'receivables=' + !!filters.areOUT,
      'matched=' + !!filters.matched,
      'unmatched=' + !!filters.exceptions,
    ].join('&')

    return axios.get(url + '?' + queryParams, {responseType: 'arraybuffer'})
      .then(function(data) {
        var blob = new Blob([data], {type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'})
        fileSaver.saveAs(blob, title)
      })
  },

}



