import notificationService from '@/services/notificationService'

function toggleReadStatus(notification, id, multiple) {
  if (multiple) {
    notification.isRead = true
  } else if (notification.id === id) {
    notification.isRead = !notification.isRead
  }
}

const state = {
  userNotifications: [],
  nextUserNotifications: [],
  userNotificationsCount: 0,
  userNotificationsCountTimerActive: false,
  companyNotifications: [],
  nextCompanyNotifications: [],
  notificationTimer: null,
}

const getters = {
  userNotifications: (state) => state.userNotifications,
  nextUserNotifications: (state) => state.nextUserNotifications,
  userNotificationsCount: (state) => state.userNotificationsCount,
  companyNotifications: (state) => state.companyNotifications,
  nextCompanyNotifications: (state) => state.nextCompanyNotifications,

}

const mutations = {
  setUserNotifications(state, payload) { state.userNotifications = payload },
  addUserNotifications(state, payload) { state.userNotifications = state.userNotifications.concat(payload) },
  setNextUserNotifications(state, payload) { state.nextUserNotifications = payload },
  setNextCompanyNotifications(state, payload) { state.nextCompanyNotifications = payload },
  setUserNotificationsCount(state, payload) { state.userNotificationsCount = payload },
  setCompanyNotifications(state, payload) { state.companyNotifications = payload },
  setUserNotificationsCountTimerActive(state, payload) { state.userNotificationsCountTimerActive = payload },
  addCompanyNotifications(state, payload) { state.companyNotifications = state.companyNotifications.concat(payload) },
  switchUserNotificationStatus(state, payload) {
    state.userNotifications.forEach(item => toggleReadStatus(item, payload[0], payload[1]))
    state.nextUserNotifications.forEach(item => toggleReadStatus(item, payload[0], payload[1]))
  },
  switchCompanyNotificationStatus(state, payload) {
    state.companyNotifications.forEach(current => toggleReadStatus(current, payload[0], payload[1]))
    state.nextCompanyNotifications.forEach(current => toggleReadStatus(current, payload[0], payload[1]))
  },
}

const actions = {
  loadUserNotifications({commit}, data) {
    return notificationService.getUserNotifications(data)
      .then(payload => commit('setUserNotifications', payload))
  },
  loadCompanyNotifications({commit}, data) {
    return notificationService.getCompanyNotifications(data)
      .then(payload => commit('setCompanyNotifications', payload))
  },
  loadAllUserNotifications({commit}) {
    return notificationService.getAllUserNotifications()
      .then(payload => commit('setUserNotifications', payload))
  },
  switchUserNotificationReadStatus({commit}, data) {
    return notificationService.switchNotificationReadStatus(data)
      .then(res => {
        commit('switchUserNotificationStatus', [data, false])
      })
  },
  switchCompanyNotificationReadStatus({commit}, data) {
    return notificationService.switchNotificationReadStatus(data)
      .then(commit('switchCompanyNotificationStatus', [data, false]))
  },
  markUserNotificationsRead({commit}) {
    return notificationService.markUserNotificationsRead()
      .then(res => {
        commit('switchUserNotificationStatus', [0, true])
      })
  },
  markUserNotificationsOld() {
    return notificationService.markUserNotificationsOld()
  },
  markCompanyNotificationsRead({commit}, data) {
    return notificationService.markCompanyNotificationsRead(data)
      .then(commit('switchCompanyNotificationStatus', [0, true]))
  },
  markCompanyNotificationsOld({commit}, data) {
    return notificationService.markCompanyNotificationsOld(data)
  },
  loadNextUserNotifications({commit}, data) {
    return notificationService.getUserNotifications(data)
      .then(payload => commit('setNextUserNotifications', payload))
  },
  loadNextCompanyNotifications({commit}, data) {
    return notificationService.getCompanyNotifications(data)
      .then(payload => commit('setNextCompanyNotifications', payload))
  },
  addUserNotifications({commit}, data) {
    commit('addUserNotifications', data)
  },
  addCompanyNotifications({commit}, data) {
    commit('addCompanyNotifications', data)
  },
  seeNotifications({commit}) {
    return commit('setUserNotificationsCount', 0)
  },
  loadUserNotificationsCount({commit}) {
    return notificationService.getUserNotificationsCount()
      .then(res => commit('setUserNotificationsCount', +res.count))
  },
  userNotificationsCountTimer({commit, dispatch, state}, payload) {
    commit('setUserNotificationsCountTimerActive', payload)
    if (state.notificationTimer === null) {
      state.notificationTimer = setInterval(() => {
        console.log(state.userNotificationsCountTimerActive ? 'User Notifications Timer IS active.' : 'Timer is Deactivating ...')
        if (state.userNotificationsCountTimerActive) {
          dispatch('loadUserNotificationsCount')
        } else {
          clearInterval(state.notificationTimer)
        }
      }, 1000 * 30)
    }
  },
}


export default {
  state,
  getters,
  mutations,
  actions,
}



