<template>
  <div class="position-relative">

    <select
      v-show="!showInput"
      v-model="innerValue.value"
      @change="onSelect($event)"
      :disabled="disabled"
      @blur="onBlur(!showInput)"
      @focus="$emit('focus')"
      ref="select"
      :class="[hasError ? errorClass : '', 'form-control']">
      <option value="" disabled>{{selectPlaceholderText}}</option>
      <option v-for="option in options"
              :value="option[valueKey]"
              :key="option[valueKey]">
              {{option[labelKey]}}
      </option>
      <option value="input">{{inputLabel}}</option>
    </select>

    <input
      v-show="showInput"
      :value="innerValue.customValue"
      :disabled="disabled"
      type="text"
      :placeholder="inputPlaceholderText"
      @input="onInput($event)"
      @blur="onBlur(showInput)"
      @focus="$emit('focus')"
      :class="[hasError ? errorClass : '', 'form-control']"
      ref="input">

    <button
      type="button"
      class="btn btn-default close-custom-input-btn"
      v-if="showInput && !disabled"
      @click="onTriggerClick()">&times;</button>

  </div>
</template>


<script>
const INPUT = 'input'

export default {
  $_veeValidate: {
    name() {
      return this.name
    },
    value() {
      return this.innerValue.value
    },
  },
  data() {
    return {
      innerValue: {
        value: (this.value.key.id === null ? 'input' : this.value.key.id),
        customValue: this.value.customKey,
      },
      showInput: this.value.key.id === null,
    }
  },
  props: {
    options: {
      default: () => {
        return []
      },
    },
    name: {
      default: 'customSelect',
    },
    value: {
      default: () => {
        return {}
      },
    },
    selectPlaceholderText: {
      default: 'Please, choose...',
    },
    inputPlaceholderText: {
      default: 'Enter value...',
    },
    valueKey: {
      default: 'id',
    },
    labelKey: {
      default: 'label',
    },
    inputLabel: {
      default: 'Custom option',
    },
    hasError: {
      default: false,
    },
    errorClass: {
      default: 'is-invalid',
    },
    disabled: {
      default: false,
    },
  },
  methods: {
    emitValue() {
      let label = this.options ? this.options.filter((option) => { return option[this.valueKey] === Number(this.innerValue.value) }) : []
      label = (label.length !== 0 ? label[0][this.labelKey] : null)
      this.$emit('input', {
        key: {
          id: (this.innerValue.value === '' ? null : this.innerValue.value),
          label: label,
        },
        customKey: this.innerValue.customValue,
      })
    },
    onSelect(event) {
      if (event.target.value === INPUT) {
        this.showInput = true
        this.$nextTick(() => this.$refs.input.focus())
      }
      this.emitValue()
    },
    onInput(event) {
      this.innerValue.customValue = event.target.value
      this.emitValue()
    },
    onTriggerClick() {
      this.innerValue.value = ''
      this.showInput = false
      this.$nextTick(() => this.$refs.select.focus())
    },
    onBlur(condition) {
      if (condition) {
        this.$emit('blur')
      }
    },
    onSelectBlur() {
      if (!this.showInput) {
        this.$emit('blur')
      }
    },
  },
  watch: {
    value: {
      handler(val) {
        this.innerValue = {
          value: (val.key.id === null ? 'input' : val.key.id),
          customValue: this.value.customKey,
        }
        this.showInput = (val.key.id === null)
      },
      deep: true,
    },
  },
}
</script>


<style>
.close-custom-input-btn {
    position: absolute;
    top: 1px;
    bottom: 1px;
    right: 1px;
    padding: 5px 12px;
}
</style>



