import RecordsForm from './RecordsForm/index'
import RecordsTable from './RecordsTable/index'
import merge from 'lodash.merge'


export default {
  components: {
    RecordsForm,
    RecordsTable,
  },

  data() {
    return {
      formData: {},
      showTable: false,
      loading: false,
    }
  },
  methods: {

    formSubmitted(formData) {
      this.showTable = true
      this.formData = merge({}, formData)
    },
  },
}



