import AutoFxSwitchModal from './AutoFxSwitchModal'
import tableTools from '@/components-lib/table-tools2'
import comparators from '@/helpers/comparators'
import runAfterSCisSetMixin from '../../runAfterSCisSetMixin'


var vueComponentOptions = {}

var vueComponentFn = function(tableStore) {
  vueComponentOptions.name = 'AutoFxSettings'

  vueComponentOptions.mixins = [tableTools.tableStoreMixin(tableStore), runAfterSCisSetMixin]

  vueComponentOptions.directives = {
    Sort: tableTools.SortDirective(tableStore),
  }

  vueComponentOptions.components = {
    SelectFilter: tableTools.SelectFilter(tableStore),
    AutoFxSwitchModal,
  }

  vueComponentOptions.data = function() {
    return {
      pauseChange: false,
      currentData: null,
      loading: false,
      itemsPerPage: 20,
      showFilters: false,
      compare: {
        string: comparators['string'],
      },
    }
  }

  vueComponentOptions.created = function() {
    this.runAfterSCisSet(this.loadCSAutoFxRates)
  }

  vueComponentOptions.computed = {
    selectedItems() {
      return tableStore.getters.selectedItems()
    },
    allItems() {
      return tableStore.getters.items()
    },
  }

  vueComponentOptions.methods = {
    loadCSAutoFxRates() {
      this.loading = true
      this.$store.dispatch('loadCSAutoFxRates')
        .then(() => {
          tableStore.mutations.setItems(this.$store.getters.getAutoFxRates)
        })
        .finally(() => {
          this.loading = false
        })
    },
    toggleFilters() {
      this.showFilters = !this.showFilters
      if (!this.showFilters) {
        tableStore.mutations.removeAllFilters()
      }
    },
    disable(data) {
      this.currentData = data
      this.multipleActive = false
      this.$refs.questionModal.openQuestion(this.$t('Are you sure you want to STOP auto Fx Rate ?'))
    },
    multiple(data) {
      this.multipleActive = true
      this.$refs.questionModal.openQuestion(this.$t('Are you sure you want to STOP auto for selected Fx Rates ?'))
    },
    enable(data) {
      this.currentData = data
      this.multipleActive = false
      this.$refs.questionModal.openDateQuestion(this.$t('You are going to start auto Fx Rate for ') + data.currency.isoCode)
    },
    change(data) {
      this.currentData = data
      this.multipleActive = false
      this.$refs.questionModal.openDateQuestion(this.$t('You are going to change auto Fx Rate for ') + data.currency.isoCode)
    },
    modalConfirm(date) {
      if (this.multipleActive) {
        this.$store.dispatch('massDeactivateAutoFxRate', this.selectedItems)
          .then(() => {
            this.loadCSAutoFxRates()
          })
      } else {
        let current = this.allItems.filter((obj) => {
          return obj.currency.id === this.currentData.currency.id
        })[0]
        if (this.currentData.sourceID !== null) {
          current.date = new Date(date)
          if (this.currentData.oldSourceID === null) {
            this.$store.dispatch('activateAutoFxRate', current)
              .then(() => {
                this.loadCSAutoFxRates()
              })
          } else {
            this.$store.dispatch('switchAutoFxRate', current)
              .then(() => {
                this.loadCSAutoFxRates()
              })
          }
        } else {
          current.date = ''
          this.$store.dispatch('deactivateAutoFxRate', current)
            .then(() => {
              this.loadCSAutoFxRates()
            })
        }
        current.oldSourceID = current.sourceID
      }
    },
    modalCancel() {
      let current = this.allItems.filter((obj) => {
        return obj.currency.id === this.currentData.currency.id
      })[0]
      current.sourceID = current.oldSourceID
    },
    turnSelected(bool) {
      this.multiple(this.selectedItems)
    },
    changeCurrent(current) {
      setTimeout(() => {
        if (!this.pauseChange) {
          if (current.oldSourceID === null) {
            this.enable(current)
          } else {
            if (current.sourceID === null) {
              this.disable(current)
            } else {
              this.change(current)
            }
          }
        }
      }, 25)
    },
  }

  return vueComponentOptions
}

vueComponentFn.options = vueComponentOptions

let store = tableTools.TableStore('AutoFxSettings')

export default vueComponentFn(store)



