import axios from 'axios'
import {apiToUserNotification, userNotificationsToApi, companyNotificationsToApi} from './converters/notificationsConverters'

export default {
  getUserNotifications(data) {
    let payload = userNotificationsToApi(data)
    return axios.get('/Log?' + payload)
      .then(data => data.map(apiToUserNotification))
  },
  getCompanyNotifications(data) {
    let payload = companyNotificationsToApi(data)
    return axios.get('/Log/Companies/' + payload.id + '?' + payload.data)
      .then(data => data.map(apiToUserNotification))
  },
  getAllUserNotifications() {
    return axios.get('/Log')
      .then(data => data.map(apiToUserNotification))
  },
  switchNotificationReadStatus(id) {
    return axios.post('/Log/ReadStatus/' + id)
  },
  markUserNotificationsOld() {
    return axios.post('/Log/MarkAllAsOld')
  },
  markUserNotificationsRead() {
    return axios.post('/Log/MarkAllAsRead')
  },
  markCompanyNotificationsRead(data) {
    let payload = companyNotificationsToApi(data)
    return axios.post('/Log/Companies/' + payload.id + '/MarkAllAsRead')
  },
  markCompanyNotificationsOld(data) {
    let payload = companyNotificationsToApi(data)
    return axios.post('/Log/Companies/' + payload.id + '/MarkAllAsOld')
  },
  getUserNotificationsCount() {
    return axios.get('/Log/Count')
      .then()
  },
}



