import contactService from '@/services/contactService'
import relatedPartiesService from '@/services/relatedPartiesService'
import stringUtils from '@/helpers/stringUtils'
import {thenHandler} from '../storeUtils'


const INITIAL_STATE = {
  contacts: [],
  holdingTree: [],
  companyRelatedParties: [],
  companySentInvitations: [],
}

var state = {...INITIAL_STATE}

var getters = {}

var mutations = {
  addInvitation(state, payload) { state.companySentInvitations.push(payload) },
  removeInvitation(state, payload) {
    const index = state.companySentInvitations.indexOf(payload)
    if (index > -1) {
      state.companySentInvitations.splice(index, 1)
    }
  },
}

// Generate standard getters and mutations
Object.keys(state).forEach(key => {
  getters[key] = (state) => state[key]
  mutations['set' + stringUtils.capitalizeFirstLetter(key)] = (state, payload) => { state[key] = payload }
})


const actions = {
  resetContacts({ state }) {
    Object.assign(state, INITIAL_STATE)
  },

  loadSCContacts({ commit, getters }) {
    return contactService.getCompanyContacts(getters.selectedCompany)
      .then(thenHandler(commit, 'setContacts'))
  },
  loadSCSentInvitations({ commit, getters }) {
    return contactService.getCompanySentInvitations(getters.selectedCompany)
      .then(thenHandler(commit, 'setCompanySentInvitations'))
  },
  inviteContact({ commit, getters }, formData) {
    return contactService.invite(getters.selectedCompany, formData)
      .then(thenHandler(commit, 'addInvitation'))
  },
  inviteMyCompanyContacts({ commit, getters }, ids) {
    return contactService.inviteMyCompany(getters.selectedCompany, ids)
  },
  cancelInvitation({ commit }, invitation) {
    return contactService.cancel(invitation)
      .then(() => {
        commit('removeInvitation', invitation)
      })
  },
  loadSCRelatedParties({ commit, getters }, payload) {
    return relatedPartiesService.getCompanyRelatedParties(getters.selectedCompany, payload)
      .then(thenHandler(commit, 'setCompanyRelatedParties'))
  },
  addRelatedParty({ commit, getters }, payload) {
    return relatedPartiesService.addRelatedParty(getters.selectedCompany, payload)
  },
  editRelatedParty({ commit }, payload) {
    return relatedPartiesService.editRelatedParty(payload)
  },
  deleteRelatedParty({ commit }, payload) {
    return relatedPartiesService.deleteRelatedParty(payload)
  },

  // Holding Tree
  loadHoldingTree({ commit, getters }) {
    return contactService.loadHoldingTree(getters.selectedCompany)
      .then(thenHandler(commit, 'setHoldingTree'))
  },
}


export default {
  state,
  mutations,
  actions,
  getters,
}



