<template>
  <div v-if="company.logo" :class="['logo', 'logo-' + size]" :style="logoStyle">
    <div v-if="!company.logo.URL" class="logo-initials">{{ company.logo.initials }}</div>
    <div :style="flagStyle"></div>
  </div>
</template>


<script>
  export default {
    props: {
      company: {
        required: true,
      },
      size: {
        required: true,
      },
    },
    computed: {
      logoStyle() {
        if (!this.company.logo.URL) {
          return {}
        }

        return {
          'background-image': 'url(' + this.company.logo.URL + ')',
        }
      },
      flagStyle() {
        return {
          'background-image': 'url(/static/flags/' + this.company.country.code + '.svg)',
        }
      },
    },
  }
</script>



