import axios from 'axios'
import {apiToUser, userToApi} from './converters/userConverters'
import { Base64 } from 'js-base64'

const ACCOUNT_API = '/Account/'

export default {
  getUser() {
    return axios.get(ACCOUNT_API)
      .then(apiToUser)
  },
  updateUser(data) {
    return axios.patch(ACCOUNT_API, data)
  },
  updateUserSettings(data) {
    return axios.post(ACCOUNT_API + 'Settings', data)
      .then()
  },
  // AUTH
  register(formData) {
    const postData = userToApi(formData)
    return axios.post(ACCOUNT_API, postData)
      .then(apiToUser)
  },
  login: function({email, password}) {
    const requestConfig = {
      headers: {
        'Authorization': 'Basic ' + Base64.encode(email + ':' + password),
      },
    }
    return axios.post(ACCOUNT_API + 'Login', null, requestConfig)
      .then(apiToUser)
  },
  logout() {
    return axios.get(ACCOUNT_API + 'Logout')
  },
  requestPassReset(email) {
    return axios.post(ACCOUNT_API + 'Recover', {email})
  },
  resetPass(token, password) {
    return axios.post(ACCOUNT_API + 'Recover/' + token, {password: Base64.encode(password)})
  },
  changePass(oldPassword, password) {
    let postData = { oldPassword: Base64.encode(oldPassword), newPassword: Base64.encode(password) }
    return axios.post(ACCOUNT_API + 'Password', postData)
  },
  uploadAvatar(file) {
    let options = {
      headers: {
        'Content-Type': 'multipart/form-data',
      },
    }
    return axios.post(ACCOUNT_API + 'ProfileImage', file, options)
  },
  deleteAvatar() {
    return axios.delete(ACCOUNT_API + 'ProfileImage')
  },
  confirmAccount(payload) {
    return axios.get('/account/confirmation/' + payload)
  },
}



