<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-md-5 mx-auto pt-5">
        Confirming account ....
      </div>
    </div>
  </div>
</template>


<script>
export default {
  beforeMount() {
    if (this.$route.query && this.$route.query.c !== undefined) {
      this.makeApiCall()
    }
    this.afterConfirmation()
  },
  methods: {
    makeApiCall() {
      let payload = this.$route.query
      this.$store.dispatch('confirmAccount', payload)
        .then(this.$customMsg.showSuccessMessage('Success'))
        .catch(err => this.$customMsg.showErrorMessage(err.messageDetail))
    },
    afterConfirmation() {
      this.$router.push('/')
    },
  },
}
</script>



