import Tree from './Tree/Tree'
import CompanyInfo from './CompanyInfo'

export default {
  components: {
    Tree,
    CompanyInfo,
  },
  data() {
    return {
      currentSelectedNode: null,
    }
  },
  computed: {
    nodes() {
      return this.$store.getters.holdingTree
    },
  },
  created() {
    this.$store.dispatch('loadHoldingTree')
      .then((result) => {
        console.log(result)
      })
  },
  methods: {
    changeSelected(item) {
      this.currentSelectedNode = item
    },
  },
}




