import { required, sameAs, minLength } from 'vuelidate/lib/validators'
import authService from '@/services/authService'

export default {
  validations: {
    formData: {
      oldPassword: {
        required,
      },
      password: {
        required,
        minLength: minLength(8),
      },
      repPassword: {
        required,
        minLength: minLength(8),
        sameAsPass: sameAs('password'),
      },
    },
  },
  data() {
    return {
      formData: {
        oldPassword: null,
        password: null,
        repPassword: null,
      },
      errMsg: null,
      loading: false,
    }
  },
  methods: {
    submit() {
      if (!this.$v.formData.$invalid) {
        this.loading = true
        authService.changePass(this.formData.oldPassword, this.formData.password)
          .then(res => {
            this.$customMsg.showSuccessMessage('Password succesfuly updated for account: ' + res.email)
            this.formData.oldPassword = null
            this.formData.password = null
            this.formData.repPassword = null
            this.$emit('success')
          })
          .catch(errRes => {
            console.log(errRes)
            this.errMsg = this.$t(errRes.messageDetail)
          })
          .finally(() => {
            this.loading = false
          })
      }
    },
  },
}



