import Multiselect from 'vue-multiselect'

export default {
  components: {
    Multiselect,
  },
  data() {
    return {
      loading: false,
    }
  },
  props: {
    disabled: {
      type: Boolean,
      default: false,
    },
    value: {},
    componentClass: {
      type: String,
    },
  },
  created() {
    this.loading = true
    this.$store.dispatch('loadCountries')
      .then(() => {
        this.loading = false
      })
  },
  computed: {
    internalValue: {
      get() {
        return this.value
      },
      set(newValue) {
        this.$emit('input', newValue)
      },
    },
    countries() {
      return this.$store.getters.countries ? this.$store.getters.countries : []
    },
  },
  methods: {
    validate() {
      return this.$validator.validateAll()
    },
    countryLabel(option) {
      return `${option.name}`
    },
  },
}



