import ReconciliationSummaryTable from './ReconciliationSummaryTable/index'
import ReconciliationMessages from './ReconciliationMessages/index'
import runAfterSCisSetMixin from '../../runAfterSCisSetMixin'
import dateUtils from '@/helpers/dateUtils'

export default {
  name: 'ReconciliationsPage',
  components: {
    ReconciliationSummaryTable,
    ReconciliationMessages,
  },
  mixins: [runAfterSCisSetMixin],
  props: ['recId'],
  data() {
    return {
      loading: false,
      activeTabId: 0,
      options: [
        {text: 'Open', value: 1},
        {text: 'Finished', value: 2},
        {text: 'Rejected', value: 3},
      ],
    }
  },
  computed: {
    selectedReconciliation() {
      return this.$store.getters.selectedReconciliation || {}
    },
    dates() {
      const reconciliation = this.$store.getters.selectedReconciliation
      if (reconciliation) {
        var dates = dateUtils.formatDate(reconciliation.endDate)
        if (reconciliation.statement.id > 1) {
          dates = dateUtils.formatDate(reconciliation.startDate) + ' - ' + dates
        }
        return dates
      }
    },
  },
  created() {
    this.runAfterSCisSet(this.loadReconciliation)
  },
  methods: {
    loadReconciliation() {
      this.loading = true
      Promise.all([
        this.$store.dispatch('loadSelReconciliation', this.recId),
        this.$store.dispatch('loadSelReconciliationSummary', this.recId),
      ])
        .finally(() => {
          this.loading = false
        })
    },
    goToUpload() {
      let uploadData = {
        statementID: this.selectedReconciliation.statement.id,
        startDate: this.selectedReconciliation.startDate,
        endDate: this.selectedReconciliation.endDate,
      }
      let historyComponent = this.$route.path
      this.$router.push({name: 'Upload', params: { uploadData: uploadData, historyComponent: historyComponent }})
    },
    goBack() {
      this.$router.go(-1)

    },
    changeRecStatus(value) {
      this.selectedReconciliation.statusId = value
      this.$store.dispatch('editSelReconciliationStatus', this.selectedReconciliation)
    },
    onTabChange(index) {
      this.activeTabId = index
    },
  },
}



