export default {
  data() {
    return {
      toggle: false,
      avatarFile: null,
      imageData: '',
    }
  },
  methods: {
    previewImage(event) {
      var input = event.target
      if (input.files && input.files[0]) {
        var reader = new FileReader()
        reader.onload = (e) => {
          this.imageData = e.target.result
        }
        reader.readAsDataURL(input.files[0])
      }
    },
    confirm() {
      this.toggle = false
      if (this.imageData !== '') {
        let formData = new FormData()
        formData.append('file', this.avatarFile)
        this.$emit('file', formData)
        this.$emit('image', { image: this.imageData, formData: formData })
      }
    },
    open() {
      this.toggle = true
    },
    close() {
      this.toggle = false
      this.$emit('close')
    },
  },
}



