import importService from '@/services/importService'
import {thenHandler} from '../storeUtils'
import {STATEMENT_TYPES} from '@/nomenclaturesConstants'


const INITIAL_STATE = {
  importReceipt: null,
  summary: null,
  rcSetsRecords: {},
  ucSetsRecords: {},
  invalidRecords: null,
  outOfScopeRecords: null,
}

var state = {...INITIAL_STATE}

const getters = {
  importReceipt: (state) => state.importReceipt,
  importSummary: (state) => state.summary,
  recognizedCompaniesSets: (state) => {
    if (state.summary) {
      return state.summary.recognizedCompaniesSets
    }
  },
  recognizedCompanyRecords: (state) => (set) => {
    if (state.rcSetsRecords && set) {
      return state.rcSetsRecords[set.company.name]
    }
  },
  unrecognizedCompaniesSets: (state) => {
    if (state.summary) {
      return state.summary.unrecognizedCompaniesSets
    }
  },
  unrecognizedCompanyRecords: (state) => (set) => {
    if (state.ucSetsRecords && set) {
      return state.ucSetsRecords[set.company.name]
    }
  },
  recordsCount: (state) => {
    if (state.summary) {
      return state.summary.recordsCount
    }
  },
  invalidRecords: (state) => state.invalidRecords,
  outOfScopeRecords: (state) => state.outOfScopeRecords,
  statementType: (state) => {
    if (state.importReceipt) {
      return STATEMENT_TYPES[+state.importReceipt.statementType.id - 1]
    }
  },
  statementTypeContext: (state) => {
    if (state.importReceipt) {
      return STATEMENT_TYPES[+state.importReceipt.statementType.id - 1].context
    }
  },
}


const mutations = {
  setImportReceipt(state, payload) { state.importReceipt = payload },
  setSummary(state, payload) { state.summary = payload },
  setUCRecords(state, payload) {
    const ucSet = state.summary.unrecognizedCompaniesSets.find(set => set.company.name === payload.set.company.name)
    var obj = {
      ...state.ucSetsRecords,
    }
    obj[ucSet.company.name] = payload.records
    state.ucSetsRecords = obj
  },
  setRCRecords(state, payload) {
    const rcSet = state.summary.recognizedCompaniesSets.find(set => set.company.name === payload.set.company.name)
    var obj = {
      ...state.rcSetsRecords,
    }
    obj[rcSet.company.name] = payload.records
    state.rcSetsRecords = obj
  },
  setInvalidRecords(state, payload) { state.invalidRecords = payload },
  setOutOfScopeRecords(state, payload) { state.outOfScopeRecords = payload },
  clearImport(state) {
    Object.assign(state, INITIAL_STATE)
  },
}


function loadCompanyRecords({ commit, getters }, getter, api, commitName, set) {
  const cashedRecords = getters[getter](set)
  if (cashedRecords) {
    return Promise.resolve(cashedRecords)
  }

  const importReceipt = getters.importReceipt
  return importService[api](importReceipt, set)
    .then(records => {
      commit(commitName, {set, records})
    })
}

function loadRecords({ commit, getters }, getter, api, commitName) {
  // const cashedRecords = getters[getter]
  // if (cashedRecords) {
  //   return Promise.resolve(cashedRecords)
  // }

  const importReceipt = getters.importReceipt
  return importService[api](importReceipt)
    .then(thenHandler(commit, commitName))
}

const actions = {
  resetImports({ state }) {
    Object.assign(state, INITIAL_STATE)
  },
  uploadExcel({ commit, getters }, formData) {
    return importService.importExcel(getters.selectedCompany, formData)
      .then(thenHandler(commit, 'setImportReceipt'))
  },
  saveValid({ commit, getters }) {
    return importService.saveValidRecords(getters.importReceipt)
  },
  removeValidSet({ commit, getters }, set) {
    return importService.removeValidSet(getters.importReceipt, set)
  },
  removeValidRecord({ commit, getters }, record) {
    return importService.removeValidRecord(getters.importReceipt, record)
  },
  loadSummary({ commit, getters }) {
    const importReceipt = getters.importReceipt
    if (importReceipt) {
      return importService.getSummary(importReceipt)
        .then(thenHandler(commit, 'setSummary'))
    } else {
      return Promise.resolve()
    }
  },
  loadRecognizedCompanyRecords(context, set) {
    return loadCompanyRecords(context, 'recognizedCompanyRecords', 'getRecognizedCompanyRecords', 'setRCRecords', set)
  },
  loadUnrecognizedCompanyRecords(context, set) {
    return loadCompanyRecords(context, 'unrecognizedCompanyRecords', 'getUnrecognizedCompanyRecords', 'setUCRecords', set)
  },
  loadInvalidRecords(context) {
    return loadRecords(context, 'invalidRecords', 'getInvalidRecords', 'setInvalidRecords')
  },
  loadOutOfScopeRecords(context) {
    return loadRecords(context, 'outOfScopeRecords', 'getOutOfScopeRecords', 'setOutOfScopeRecords')
  },
}


export default {
  state,
  mutations,
  actions,
  getters,
}



