<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-md-6 mx-auto pt-5">

        <div class="card">
          <h2 class="card-header text-center">{{$t("Add Company")}}</h2>
          <div class="card-body">
            <!-- LOGO -->
            <b-button size="sm" variant="primary" @click="$refs.logoModal.open()">{{$t("Select Logo")}}</b-button>
            <b-button size="sm" v-if="companyLogo.image !== null" variant="danger" @click="deleteLogo">X</b-button>
            <AvatarChangeModal ref="logoModal" @image="setLogo"></AvatarChangeModal>
            <!-- FORM -->
            <my-company-form :logo="companyLogo" @success="onAddedCompany" @cancel="cancel"></my-company-form>
          </div>
        </div><!-- .card -->

      </div>
    </div>
  </div>
</template>


<script>
import MyCompanyForm from '@/components-app/forms/MyCompanyForm'
import AvatarChangeModal from '@/components-app/modals/AvatarChangeModal'

export default {
  name: 'AddCompany',
  components: {
    MyCompanyForm,
    AvatarChangeModal,
  },
  data() {
    return {
      companyLogo: {
        image: null,
        formData: null,
      },
    }
  },
  methods: {
    onAddedCompany(company) {
      this.$router.push({name: 'CompanyProfile', params: {id: company.id}})
    },
    cancel() {
      this.$router.go(-1)
    },
    setLogo(file) {
      this.companyLogo = file
    },
    deleteLogo() {
      this.companyLogo = {
        image: null,
        formData: null,
      }
    },
  },
}
</script>



