import MatchedTable from './MatchedTable/'
import dateUtils from '@/helpers/dateUtils'
import tableTools from '@/components-lib/table-tools'
import FullPageModal from '@/components-app/modals/FullPageModal/index'

let tableStore = tableTools.TableStore()

export default {
  name: 'matchedModal',
  components: {
    FullPageModal,
    MatchedTable: MatchedTable(tableStore),
  },
  props: {
    IN: {
      type: Boolean,
      required: true,
    },
    open: {
      type: Boolean,
      required: true,
    },
  },
  data: function() {
    return {
      hasSets: false,
      loading: false,
    }
  },
  computed: {
    selectedCompany() {
      return this.$store.getters.selectedCompany
    },
    selectedReconciliation() {
      return this.$store.getters.selectedReconciliation
    },
    dates() {
      const reconciliation = this.$store.getters.selectedReconciliation
      if (reconciliation) {
        var dates = dateUtils.formatDate(reconciliation.endDate)
        if (reconciliation.statement.id > 1) {
          dates = dateUtils.formatDate(reconciliation.startDate) + ' - ' + dates
        }
        return dates
      }
    },
  },
  methods: {
    close() {
      this.$emit('close')
    },
    exportFile() {
      var self = this
      self.loading = true
      const payload = {
        areIN: self.IN,
        areOUT: self.OUT,
        matched: true,
        exceptions: false,
      }
      self.$store.dispatch('exportRecords', payload)
        .then(() => {
          // self.showSuccess()
        })
        .catch(() => {
          // self.showError()
        })
        .finally(() => {
          self.loading = false
        })
    },
    onUnmatch(id) {
      this.loading = true
      this.$store.dispatch('unmatchSet', id)
        .then(() => {
          this.loadSets()
        })
        .finally(() => {
          this.loading = false
        })
    },
    loadSets() {
      var self = this
      self.loading = true
      var payload = {
        areIN: self.IN,
        selectedReconciliation: this.selectedReconciliation,
      }
      self.$store.dispatch('loadMatchedSets', payload)
        .then((sets) => {
          self.hasSets = !!sets.length
          if (self.hasSets) {
            tableStore.commit('setItems', sets)
          }
        })
        .finally(() => {
          self.loading = false
        })
    },
  },
  watch: {
    open(value) {
      if (value) {
        this.$refs.modal.open()
        this.loadSets()
      } else {
        this.$refs.modal.close()
        this.hasSets = false
      }
    },
  },
}



