import Avatar from '@/components-lib/Avatar'

export default {
  components: {
    Avatar,
  },
  props: {
    type: {},
  },
  data() {
    return {
      userNotification: false,
      companyId: null,
    }
  },
  mounted() {
    this.$store.dispatch('loadAllUserNotifications')
  },
  computed: {
    userNotifications() {
      return this.$store.getters.userNotifications
    },
    companyNotifications() {
      return this.$store.getters.companyNotifications
    },
    notifications() {
      return this.userNotification ? this.userNotifications : this.companyNotifications
    },
    notificationTypeText() {
      if (this.type === 'user') {
        this.userNotification = true
        return 'User Notifications'
      } else {
        this.companyId = this.type
        this.userNotification = false
        return 'Company Notifications (for company id:' + this.type + ')'
      }
    },
  },
  methods: {
  },
}



