// import {ROLES} from '@/nomenclaturesConstants'


// * * * API TO MODEL * * *
function apiToCollaborator(data, user) {
  return {
    name: data.userFullName || '',
    role: {
      id: data.roleId,
      label: data.roleLabel,
    },
    company: {
      id: data.companyId,
    },
    email: data.userEmail,
    isPending: data.isPending,
    isInherited: data.isInherited,
    isCurrent: (user && data.userEmail === user.email),
  }
}


// * * * MODEL TO API * * *
function collaboratorToApi(model) {
  var data = {
    roleid: model.role.id,
    userEmail: model.email,
  }

  if (model.isInherited) {
    data.isInherited = model.isInherited
  }

  if (model.name) {
    data.userFullName = model.name
  }

  if (model.company) {
    data.companyId = model.company.id
  }

  return data
}


export {
  // API TO MODEL
  apiToCollaborator,
  // MODEL TO API
  collaboratorToApi,
}



