import tableTools from '@/components-lib/table-tools'
import MessageRow from './ReconciliationMessageRow'
import MessageForm from './ReconciliationMessagеForm'
import runAfterSCisSetMixin from '../../../runAfterSCisSetMixin'
import tableStoreMixin from '@/components-lib/table-tools/tableStoreMixin'

let tableStore = tableTools.TableStore()

export default {
  name: 'ReconciliationMessages',
  components: {
    MessageRow,
    MessageForm,
  },
  mixins: [runAfterSCisSetMixin, tableStoreMixin(tableStore)],
  created() {
    this.runAfterSCisSet(this.loadSRMessages)
  },
  computed: {
    thread() {
      return this.$store.getters.SRMessages
    },
    messages() {
      return (this.thread.messages) ? this.thread.messages : []
    },
  },
  props: ['recId'],
  data() {
    return {
      itemsPerPage: 5,
      loading: true,
    }
  },

  methods: {
    loadSRMessages() {
      this.loading = true
      this.$store.dispatch('loadSRMessages', this.recId)
        .then(() => {
          this.loading = false
        })
    },
  },

  watch: {
    messages(value) {
      tableStore.commit('setItems', value)
    },
  },
}



