import dateUtils from '@/helpers/dateUtils'
import {FX_RATES_TYPES} from '@/nomenclaturesConstants'

// FX RATES

function apiToFxRate(data) {
  let rateType = {}
  Object.keys(FX_RATES_TYPES).map((key) => {
    if (data.typeId === FX_RATES_TYPES[key].id) {
      rateType = FX_RATES_TYPES[key]
    }
  })
  let manual = data.manualRate === null ? null : {
    units: data.manualRate.units,
    rate: data.manualRate.value,
  }
  let auto = data.autoRate === null ? null : {
    units: data.autoRate.units,
    rate: data.autoRate.value,
  }
  return {
    id: data.manualRate === null ? null : data.manualRate.id,
    rateType: rateType,
    date: new Date(data.date),
    manual: manual,
    auto: auto,
    toCurrency: {
      id: data.refCurrencyId,
      code: data.refCurrencyIsoCode,
    },
  }
}

function fxRateToId(data) {
  return data.id
}

function editFxRateToApi(data) {
  return {
    id: data.id,
    value: data.manual.rate,
  }
}

function fxRateToApi(data, selectedCompany) {
  return {
    companyId: selectedCompany.id,
    refCurrencyId: data.currencyId,
    typeId: data.recordType,
    date: data.recordDate,
    value: data.recordRate,
    units: 1,
  }
}

function fxFormDataToApi(data, selectedCompany) {
  return {
    companyId: selectedCompany.id,
    periodMonth: dateUtils.formatDate(data.data),
    parameter: '?get' + (data.average ? 'Average' : 'Daily') + '=true',
  }
}
// Auto Fx Rates

function apiToAutoFxRates(data) {
  return {
    sourceID: data.usedSourceFxRateSourceId,
    oldSourceID: data.usedSourceFxRateSourceId,
    date: data.usedSourceFromDate ? new Date(data.usedSourceFromDate) : null,
    usedSourceId: data.usedSourceId,
    usedSourceFxRateSourceId: data.usedSourceFxRateSourceId,
    usedSourceFromDate: data.usedSourceFromDate,
    currency: {
      id: data.currency.id,
      isoCode: data.currency.isoCode,
      name: data.currency.name,
    },
    sources: data.sources,
  }
}

function autoFxToApi(data, selectedCompany) {
  return {
    companyId: selectedCompany.id,
    refCurrencyId: data.currency.id,
    dateFrom: data.date.toISOString(),
    fxRateSourceId: data.sourceID,
  }
}

function deactivateFxToApi(data) {
  return data.usedSourceId
}

function switchFxToApi(data) {
  return {
    id: data.usedSourceId,
    dateFrom: data.date.toISOString(),
    fxRateSourceId: data.sourceID,
  }
}

function massDeactivateFxToApi(data) {
  let payload = ''
  data.forEach((current) => {
    if (current.usedSourceId !== null) { payload += ((payload.length === 0 ? '?' : '&') + 'ids=' + current.usedSourceId) }
  })
  return payload
}

export {
  apiToFxRate,
  fxRateToId,
  editFxRateToApi,
  fxRateToApi,
  fxFormDataToApi,
  apiToAutoFxRates,
  autoFxToApi,
  deactivateFxToApi,
  switchFxToApi,
  massDeactivateFxToApi,
}



