const actions = {
  resetCompanyStore({ dispatch }) {
    return Promise.all([
      dispatch('resetRecords'),
      dispatch('resetReconciliation'),
      dispatch('resetReconciliations'),
      dispatch('resetImports'),
      dispatch('resetFxRates'),
      dispatch('resetContacts'),
      dispatch('resetCompanies'),
      dispatch('resetCollaborators'),
    ])
  },
}


export default {
  actions,
}



