import runAfterSCisSetMixin from '../../runAfterSCisSetMixin'
import FxRatesTable from './FxRatesTable/index'
import FxRatesForm from './FxRatesForm/index'

import tableTools from '@/components-lib/table-tools'

export default {
  name: 'FxRates',
  components: {
    FxRatesTable,
    FxRatesForm,
  },
  mixins: [runAfterSCisSetMixin],
  created() {
    this.runAfterSCisSet(this.loadCurrentFxRates)
  },
  props: {
    averageFxRates: {default: false},
    selectedDate: {},
  },
  data() {
    return {
      formData: {},
      showInvModal: false,
      loading: false,
      // currentFxRates: [],
    }
  },
  computed: {
    selectedCompany() {
      return this.$store.getters.selectedCompany
    },
    currentFxRates() {
      return this.$store.getters.getCurrentFxRates || []
    },
  },
  methods: {
    deleteFxRate(fxRate) {
      if (fxRate.auto === null) {
        this.currentFxRates = this.currentFxRates.filter((rate) => {
          return rate.id !== fxRate.id
        })
      } else {
        let temp = this.currentFxRates.filter((rate) => {
          return rate.auto !== null && rate.auto.id === fxRate.auto.id
        })[0]
        temp.manual = null
      }
    },
    changeSelectedDate(date) {
      this.$emit('onDateChange', date)
      this.loadCurrentFxRates(date)
    },
    reValidateOnChange() {
      this.loadCurrentFxRates(this.selectedDate)
    },
    onModalOk(event) {
      event.preventDefault()
      if (!this.formData.recordDate || !this.formData.currencyId || !this.formData.recordRate) {
        this.$refs.fxRatesForm.showError({messageDetail: this.$t('Please fill all info!')})
      } else {
        this.$store.dispatch('addFxRate', this.formData)
          .then((response) => {
            this.$customMsg.showSuccessMessage(this.$t('Successfully created ') + response.typeName + this.$t(' for ') + response.refCurrencyIsoCode)
            this.$refs.fxRatesForm.resetForm()
            this.showInvModal = false
            this.loadCurrentFxRates()
          })
          .catch(error => {
            this.$customMsg.showErrorMessage(this.$t('Error in creating FxRate :') + error.messageDetail)
            this.$refs.fxRatesForm.showError(error)
          })
      }
    },
    onModalCancel(event) {
      event.preventDefault()
      this.$refs.fxRatesForm.resetForm()
      this.showInvModal = false
    },
    loadCurrentFxRates(date) {
      this.loading = true
      let payload = {
        data: date || this.selectedDate,
        average: this.averageFxRates,
      }
      this.$store.dispatch('loadCurrentFxRates', payload)
        .finally(() => {
          this.loading = false
        })
    },
    updateFormData(data) {
      this.formData = data
    },
  },
  // watch: {
  //   currentFxRates(value) {
  //     tableStore.commit('setItems', value)
  //   },
  // },
}



