import { ModelSelect } from 'vue-search-select'
import Datepicker from 'vuejs-datepicker'
import runAfterSCisSetMixin from '../../runAfterSCisSetMixin'
import {bg, en} from 'vuejs-datepicker/dist/locale'

export default {
  components: {
    ModelSelect,
    Datepicker,
  },
  mixins: [runAfterSCisSetMixin],
  created() {
    this.runAfterSCisSet(this.loadContacts)
  },
  data() {
    return {
      formData: {
        current: true,
      },
      errMsg: null,
      loading: false,
      SCContactsOptions: [],
      locales: {
        en: en,
        bg: bg,
      },
    }
  },
  computed: {
    hasContacts() {
      return !!this.$store.getters.contacts.length
    },
    contacts() {
      return this.$store.getters.contacts
    },
  },
  methods: {
    validate() {
      return this.$validator.validateAll()
    },
    save() {
      return this.$store.dispatch('addRelatedParty', this.formData)
        .then((invitation) => {
          this.$customMsg.showSuccessMessage(this.$t('Added new relation.'))
          this.$emit('success', invitation)
        })
    },
    submit() {
      this.errMsg = null
      this.loading = true
      this.validate()
        .then(result => {
          if (result) {
            return this.save()
          }
          return Promise.reject(new Error('Please, fill the required fields.'))
        })
        .catch(errRes => {
          this.errMsg = this.$t(errRes.messageDetail)
        })
        .finally(() => {
          this.loading = false
        })
    },
    resetForm() {
      this.$nextTick()
        .then(() => {
          this.$validator.reset()
          this.errors.clear()
          this.formData = {current: true}
        })
    },
    loadContacts() {
      this.$store.dispatch('loadSCContacts')
        .then(loadedItems => this.SCContactToOptions(loadedItems))
    },
    SCContactToOptions(input) {
      let output = []
      input.forEach(contact => {
        let aliasString = ''
        contact.aliases.forEach(alias => {
          aliasString = alias.aname
        })
        output.push({
          value: contact.id,
          text: contact.name + ' - ' + aliasString,
        })
      })
      this.SCContactsOptions = output
    },
  },
}



