import axios from 'axios'
import {matchedListToApi, apiToMessageThread, exceptionListToApi, apiToRecordList, apiToMatchedList, autoMatchListToApi, manualMatchListToApi} from './converters/reconciliationConverters'

export default {

  getExceptionsRecords(payload, selectedCompany) {
    let getData = exceptionListToApi(payload, selectedCompany)
    let getString = '/ReportTransactions/Companies/' + getData.companyId + '/RefCompanies/' + getData.refCompanyId
    getString += '/StatementTypes/' + getData.statementTypeId + '/StartDate/' + getData.startDate + '/EndDate/'
    getString += getData.endDate + getData.queryString
    return axios.get(getString)
      .then(data => data.map(apiToRecordList))
  },

  getSRMessages(payload) {
    return axios.get('/Conversations/Reconciliations/' + payload)
      .then(apiToMessageThread)
  },

  sendSRMessage(payload) {
    let postData = {
      body: payload.message,
    }
    return axios.post('/Conversations/Reconciliations/' + payload.recId + '/Messages', postData)
      .then()
  },

  deleteMessage(payload) {
    return axios.delete('/Conversations/Messages/' + payload.id)
      .then()
  },

  editMessage(payload) {
    console.log(payload)
    let patchData = {
      body: payload.body,
    }
    return axios.patch('/Conversations/Messages/' + payload.id, patchData)
      .then()
  },

  getMatchedSets(payload, selectedCompany) {
    let getData = matchedListToApi(payload, selectedCompany)
    let getString = '/ReportTransactions/Companies/' + getData.companyId + '/RefCompanies/' + getData.refCompanyId
    getString += '/StatementTypes/' + getData.statementTypeId + '/StartDate/' + getData.startDate + '/EndDate/'
    getString += getData.endDate + getData.queryString
    return axios.get(getString)
      .then(apiToMatchedList)
  },

  matchAuto(payload, selectedCompany) {
    let postData = autoMatchListToApi(payload, selectedCompany)
    let postString = '/MatchingSets/AutoMatching/Companies/' + postData.companyId + '/RefCompanies/' + postData.refCompanyId
    postString += '/StatementTypes/' + postData.statementTypeId + '/StartDate/' + postData.startDate + '/EndDate/'
    postString += postData.endDate + postData.queryString
    return axios.post(postString)
      .then()
  },

  manualMatch(payload, selectedCompany) {
    let postData = manualMatchListToApi(payload, selectedCompany)
    let postString = '/MatchingSets/ManualMatching/Companies/' + postData.companyId + '/StatementTypes/' + postData.statementTypeId + postData.queryString
    return axios.post(postString, postData.payload)
      .then()
  },

  unmatchSet(payload) {
    return axios.delete(/MatchingSets/ + payload)
      .then()
  },

}



