import runAfterNomenclatureIsSetMixin from '../../runAfterNomenclatureIsSetMixin'
import CountrySelect from '@/components-app/selects/CountrySelect'

export default {
  components: { CountrySelect },
  mixins: [runAfterNomenclatureIsSetMixin],
  created() {
    this.$store.dispatch('loadCountries')
  },
  mounted() {
    this.loading = true
    this.runAfterNomenclatureIsSet('countries', () => {
      this.loading = false
    })
  },
  data() {
    return {
      formData: {},
      errMsg: null,
      loading: false,
    }
  },
  computed: {
  },
  methods: {
    validate() {
      this.$refs.invitationCountrySelect.validate()
      return this.$validator.validateAll()
    },
    save() {
      return this.$store.dispatch('inviteContact', this.formData)
        .then((invitation) => {
          this.$customMsg.showSuccessMessage(this.$t('Invitation successfully send.'))
          this.$emit('success', invitation)
        })
    },
    submit() {
      this.errMsg = null
      this.loading = true
      this.validate()
        .then(result => {
          // result comes from vee-validate
          if (result) {
            return this.save()
          }
          return Promise.reject(new Error('Please, fill the required fields.'))
        })
        .catch(errRes => {
          this.errMsg = this.$t(errRes.messageDetail)
        })
        .finally(() => {
          this.loading = false
        })
    },
    resetForm() {
      this.$nextTick()
        .then(() => {
          this.$validator.reset()
          this.errors.clear()
          this.formData = {}
        })
    },
  },
}




