<template>
  <div class="form-check">
    <input class="form-check-input" type="checkbox" v-model="isSelected" @change="changeHandler">
  </div>
</template>

<script>
var vueComponentOptions = {}

var vueComponentFn = function(store) {
  vueComponentOptions.props = {
    item: {
      required: true,
    },
    disable: {
      type: Boolean,
    },
  }

  vueComponentOptions.methods = {
    changeHandler(event) {
      store.commit('toggleItemSelection', this.item)
    },
  }

  vueComponentOptions.computed = {
    isSelected: {
      get: function() {
        return store.getters.isItemSelected(this.item)
      },
      set: function() {},
    },
  }

  vueComponentOptions.watch = {
    disable(isDisabled) {
      if (isDisabled && this.isSelected) {
        store.commit('toggleItemSelection', this.item)
      }
    },
  }

  return vueComponentOptions
}

vueComponentFn.options = vueComponentOptions

export default vueComponentFn

</script>



