<template>
  <div :class="['company-card', 'company-card-' + size]">
    <logo-flag :company="company" :size="size"></logo-flag>
    <div class="company-text">
      <div class="company-card-name">{{company.name}}</div>
      <div class="company-card-currency" v-if="extend">{{$t('Currency')}}: {{company.currency ? company.currency.code : ''}}</div>
    </div>
    <!--<div v-if="extend">{{$t('Reg Number')}}: {{company.regNum}}</div>-->
  </div>
</template>


<script>
import LogoFlag from './LogoFlag'

export default {
  components: {
    LogoFlag,
  },
  props: {
    company: {
      required: true,
    },
    size: {
      required: true,
    },
    extend: {
      default: false,
    },
  },
}
</script>



