// NOMENCLATURES

function apiToCountry(data) {
  return {
    id: data.id,
    name: data.name,
    code: data.code,
    order: data.sortOrder,
  }
}

function apiToCurrency(data) {
  return {
    id: data.id,
    name: data.name,
    code: data.isoCode,
    order: data.sortOrder,
  }
}

export {
  apiToCountry,
  apiToCurrency,
}



