import ucRecordsModal from './ucRecordsModal/index'
import runAfterSCisSetMixin from '../../runAfterSCisSetMixin'

export default {
  components: {
    ucRecordsModal,
  },
  mixins: [runAfterSCisSetMixin],
  created() {
    this.runAfterSCisSet(this.loadContacts)
  },
  data() {
    return {
      selectedSet: null,
      selectedContact: {},
    }
  },
  computed: {
    selectContactsOptions() {
      return this.contacts.map((contact) => {
        return {
          text: contact.name,
          value: contact,
        }
      })
    },
    contacts() {
      return this.$store.getters.contacts
    },
    sets() {
      return this.$store.getters.unrecognizedCompaniesSets
    },
    context() {
      return this.$store.getters.statementTypeContext
    },
  },
  methods: {
    recognize(set, contact) {
      let refCompany = contact
      let aname = set.company.name
      this.$store.dispatch('createAlias', {refCompany, aname})
        .then(this.$store.dispatch('loadSummary'))
    },
    show(set) {
      this.selectedSet = set
    },
    loadContacts() {
      this.loadStore('loadSCContacts')
    },
    loadStore(action) {
      this.loading = true
      this.$store.dispatch(action)
        .finally(() => {
          this.loading = false
        })
    },
  },
}



