// USER

import { Base64 } from 'js-base64'

function apiToUser(data) {
  let randomNumber = Math.floor(Math.random() * 10001)
  return {
    email: data.email,
    firstName: data.firstName,
    lastName: data.lastName,
    fullName: data.fullName,
    phone: data.phone,
    profileImageUrl: (data.profileImageUrl !== '') ? data.profileImageUrl + '?rand=' + randomNumber : '',
    settings: {
      language: data.settings.language ? data.settings.language : 'en',
    },
  }
}

function userToApi(user) {
  return {
    email: user.email,
    password: Base64.encode(user.password),
    firstName: user.firstName,
    lastName: user.lastName,
    phone: user.phone,
  }
}


export {
  apiToUser,
  userToApi,
}



