import Avatar from '@/components-lib/Avatar'
import {MESSAGE_TTL} from '@/nomenclaturesConstants'

export default {
  name: 'ReconciliationMessageRow',
  components: {
    Avatar,
  },
  data() {
    return {
      editActive: false,
      internalMessage: this.message.body,
    }
  },
  props: {
    'message': {},
  },
  computed: {
    user() {
      return this.$store.getters.user
    },
    selfLiveMessage() {
      if (!this.message.from || !this.message.date) {
        return false
      }
      let messageTime = Date.parse(this.message.date)
      let currentTime = Date.now()
      let timeToLiveInMS = MESSAGE_TTL * 60000
      return (this.message.from.email === this.user.email && messageTime > currentTime - timeToLiveInMS)
    },
  },
  methods: {
    deleteMessage() {
      this.$store.dispatch('deleteMessage', this.message)
        .then(() => {
          this.$emit('reload')
        })
    },
    switchEditMessage() {
      this.editActive = !this.editActive
    },
    saveMessage() {
      this.editActive = false
      let payload = Object.assign({}, this.message)
      payload.body = this.internalMessage
      this.$store.dispatch('editMessage', payload)
        .then(() => {
          this.$emit('reload')
        })
    },
  },
}



