<template>
  <body :class="{ 'main-wrapper': true, debug: isDev }">

    <header>
      <b-navbar class="navigation" toggleable="md" id="top-bar">
        <b-navbar-brand to="/"><img src="/static/images/logo.png" alt="Balance share"></b-navbar-brand>

        <b-navbar-toggle target="nav_collapse"></b-navbar-toggle>

        <b-collapse is-nav id="nav_collapse">

          <!-- GUEST -->
          <b-navbar-nav v-if="!user">
            <b-nav-item to="/login">Login</b-nav-item>
            <b-nav-item to="/register">Register</b-nav-item>
          </b-navbar-nav>

          <!-- LOGGED USER -->
          <b-navbar-nav class="ml-auto">
            <b-nav-item-dropdown text="DEV PAGES" right>
              <b-dropdown-item to="/prototype/nomenclatures" exact>Nomenclatures</b-dropdown-item>
              <b-dropdown-item to="/matching-tool" exact>Matching Tool</b-dropdown-item>
              <!--<b-dropdown-item to="/forgot">Forgot pass</b-dropdown-item>-->
              <!--<b-dropdown-item to="/reset">Reset pass</b-dropdown-item>-->
            </b-nav-item-dropdown>

            <b-nav-item-dropdown :text="i18n.lang" right>
              <b-dropdown-item @click="$i18n.locale = 'en'">English</b-dropdown-item>
              <b-dropdown-item @click="$i18n.locale = 'bg'">Български</b-dropdown-item>
            </b-nav-item-dropdown>

            <template v-if="user">

              <b-nav-item to="/received-invitations">{{$t('Invitations')}}</b-nav-item>

              <b-nav-item-dropdown :text="i18n.lang" right>
                <template slot="button-content">
                  <Avatar
                    :username="user.fullName ? user.fullName : ''"
                    :src="user.profileImageUrl ? user.profileImageUrl : ''"
                    :size="25"
                    :customStyle="{ display: 'inline-flex', border: '1px solid lightgray', marginRight: '10px' }"
                  ></Avatar>
                  <div class="d-inline-block">{{ user.fullName }}</div>
                </template>
                <b-dropdown-item to="/user#profile">{{$t('Profile')}}</b-dropdown-item>
                <b-dropdown-item to="/user#settings">{{$t('Settings')}}</b-dropdown-item>
                <b-dropdown-item to="/user#password">{{$t('Change Password')}}</b-dropdown-item>
                <b-dropdown-divider></b-dropdown-divider>
                <b-dropdown-item @click="logout">{{$t("Logout")}}</b-dropdown-item>
              </b-nav-item-dropdown>

              <UserNotifications></UserNotifications>

            </template>
          </b-navbar-nav>


        </b-collapse>

      </b-navbar>
    </header>

    <router-view></router-view>

    <!--<footer>-->
    <!--</footer>-->


    <div v-if="isDev" id="dev-toolbox">
      <b-btn v-b-toggle.collapseDevToolbox variant="outline-danger">* Dev Toolbox</b-btn>
      <b-collapse id="collapseDevToolbox" class="mt-2">
        <b-card>
          <button type="button" class="btn btn-danger btn-sm" @click="test">Test Action</button>
          <button type="button" class="btn btn-warning btn-sm" @click="showTestToasted">Test Toast Notification</button>
          <div slot="footer">
            {{ buildTimestamp }}
          </div>
        </b-card>
      </b-collapse>
    </div>

  </body>
</template>


<script>
import UserNotifications from './pages/UserPage/UserNotifications'
import Avatar from '@/components-lib/Avatar'

export default {
  components: {
    Avatar,
    UserNotifications,
  },
  data() {
    return {
      i18n: {
        lang: this.$t('lang'),
      },
      isDev: process.env.APP_DEBUG,
      buildTimestamp: process.env.BUILD_TIMESTAMP,
    }
  },
  methods: {
    test() {
      console.log('TEST')
    },
    showTestToasted() {
      this.$customMsg.showInfoMessage('Info Message')
      this.$customMsg.showErrorMessage('Error Message')
      this.$customMsg.showSuccessMessage('Success Message')
    },
    logout() {
      this.$store.dispatch('logout')
    },
  },
  computed: {
    user() {
      return this.$store.getters.user
    },
  },
  watch: {
    '$i18n.locale': function() {
      this.i18n.lang = this.$t('lang')
    },
  },
}
</script>



