import dateUtils from '@/helpers/dateUtils'
import importFPMMixin from '../../importFPMMixin'
import tableTools from '@/components-lib/table-tools'
import comparators from '@/helpers/comparators'
import tableStoreMixin from '@/components-lib/table-tools/tableStoreMixin'

var vueComponentOptions = {}

var vueComponentFn = function(tableStore) {
  vueComponentOptions.name = 'rcRecordsModal'

  vueComponentOptions.mixins = [importFPMMixin, tableStoreMixin(tableStore)]
  vueComponentOptions.directives = {
    Sort: tableTools.SortDirective(tableStore),
  }

  vueComponentOptions.components = {
    InputFilter: tableTools.InputFilter(tableStore),
    SelectFilter: tableTools.SelectFilter(tableStore),
    DateFilter: tableTools.DateFilter(tableStore),
    BulkSelector: tableTools.BulkSelector(tableStore),
    CheckboxSelect: tableTools.CheckboxSelect(tableStore),
    MarkedFilter: tableTools.MarkedFilter(tableStore),
  }

  vueComponentOptions.data = function() {
    return {
      dataChanged: false,
      itemsPerPage: 5,
      showFilters: false,
      compare: {
        string: comparators['string'],
        numAsString: comparators['numAsString'],
        number: comparators['number'],
        day: comparators['day'],
      },
    }
  }

  vueComponentOptions.computed = {
    records() {
      return this.$store.getters.recognizedCompanyRecords(this.value)
    },
    statementType() {
      return this.$store.getters.statementType
    },
    dates() {
      const importReceipt = this.$store.getters.importReceipt
      var dates = dateUtils.formatDate(importReceipt.toDate)
      if (importReceipt.fromDate) {
        dates = dateUtils.formatDate(importReceipt.fromDate) + ' - ' + dates
      }
      return dates
    },
    myCompany() {
      return this.$store.getters.selectedCompany
    },
  }

  vueComponentOptions.methods = {
    load(newSet) {
      this.dataChanged = false
      this.contractorName = newSet.company.name
      return this.$store.dispatch('loadRecognizedCompanyRecords', newSet)
    },
    toggleFilters: function() {
      this.showFilters = !this.showFilters
      if (!this.showFilters) {
        tableStore.commit('removeAllFilters')
      }
    },
    deleteSelected() {
      tableStore.getters.selectedItems.forEach(record => this.deleteCurrent(record))
    },
    deleteCurrent(record) {
      this.$store.dispatch('removeValidRecord', record)
        .then(() => {
          this.dataChanged = true
          tableStore.commit('removeItem', record)
        })
    },
    close() {
      if (this.dataChanged) { this.$emit('dataChanged') }
    },
  }

  vueComponentOptions.watch = {
    records(value) {
      tableStore.commit('setItems', value)
    },
  }

  return vueComponentOptions
}

vueComponentFn.options = vueComponentOptions

let store = tableTools.TableStore('rcRecordsModal')

export default vueComponentFn(store)



