import 'babel-polyfill'
import 'es6-promise/auto'
import 'core-js/modules/es7.promise.finally'
import Vue from 'vue'

// !! Import always before all components.
import '@/styles/main-begin.scss'

// NODE MODULES
import Vuelidate from 'vuelidate'
import BootstrapVue from 'bootstrap-vue'
// import PortalVue from 'portal-vue'

// APP CONFIGS
import '@/filters'
import i18n from '@/i18n'
import store from '@/store'
import router from '@/router'
import '@/config/axios'
import '@/config/veeValidate'

// APP COMPONENTS
import Loading from '@/components-app/Loading'
import cFormSelect from '@/components-lib/custom-bootstrap-vue/components/form-select'
import Toasted from 'vue-toasted'
import customMessages from '@/helpers/customMessages'
import App from '@/App'

// !! Import always after all components.
import '@/styles/main-end.scss'


// VUE CONFIG
Vue.config.productionTip = !process.env.APP_DEBUG

// USE PLUGINS
Vue.use(Vuelidate)
Vue.use(BootstrapVue)
// Vue.use(PortalVue)
Vue.use(Toasted)
Vue.use(customMessages)

// GLOBAL COMPONENTS
Vue.component('loading', Loading)
Vue.component('c-form-select', cFormSelect)

store.dispatch('loadUserCash')


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  i18n,
  render: h => h(App),
})



