import nomenclatureService from '@/services/nomenclatureService'
import stringUtils from '@/helpers/stringUtils'


var state = {}
var getters = {}
var mutations = {}
var actions = {}


function generateStoreMethods(name, api) {
  state[name] = null

  getters[name] = (state) => state[name]

  mutations[name] = (state, payload) => { state[name] = payload }

  actions['load' + stringUtils.capitalizeFirstLetter(name)] = function({ commit, state }) {
    if (state[name]) {
      return Promise.resolve(state[name])
    }

    return api()
      .then(arr => {
        commit(name, arr)
        return arr
      })
  }
}

// Generate full set of store methods of every nomenclature
Object.keys(nomenclatureService).forEach(nomenclature => {
  generateStoreMethods(nomenclature, nomenclatureService[nomenclature])
})


export default {
  state,
  getters,
  mutations,
  actions,
}



