import axios from 'axios'
import {apiToFxRate, apiToAutoFxRates, autoFxToApi, deactivateFxToApi, fxRateToId, fxRateToApi, fxFormDataToApi, editFxRateToApi, switchFxToApi, massDeactivateFxToApi} from './converters/fxRatesConverters'

const MANUAL_FX_API = '/ManualFxRates/'
const AUTO_FX_API = '/AutoFxRates/'
const FX_API = '/FxRates/'

export default {
  // FX Rates
  getCurrentFxRates(payload, selectedCompany) {
    var getData = fxFormDataToApi(payload, selectedCompany)
    return axios.get(FX_API + 'Companies/' + getData.companyId + '/periodMonth/' + getData.periodMonth + getData.parameter)
      .then(data => data.map(apiToFxRate))
  },
  removeFxRate(payload) {
    return axios.delete(MANUAL_FX_API + fxRateToId(payload))
  },
  addFxRate(payload, selectedCompany) {
    var postData = fxRateToApi(payload, selectedCompany)
    return axios.post(MANUAL_FX_API, postData)
      .catch(error => {
        return Promise.reject(error)
      })
  },
  editFxRate(payload) {
    var postData = editFxRateToApi(payload)
    return axios.patch(MANUAL_FX_API + postData.id, postData)
      .catch(error => {
        return Promise.reject(error)
      })
  },
  // Auto FX Rates
  getCSAutoFxRates(selectedCompany) {
    return axios.get(AUTO_FX_API + 'CompanyCurrencies/' + selectedCompany.id)
      .then(data => data.map(apiToAutoFxRates))
  },
  activateAutoFxRate(payload, selectedCompany) {
    var postData = autoFxToApi(payload, selectedCompany)
    return axios.post(AUTO_FX_API + 'UsedSources/', postData)
  },
  deactivateAutoFxRate(payload) {
    var id = deactivateFxToApi(payload)
    return axios.delete(AUTO_FX_API + 'UsedSources/' + id)
  },
  switchAutoFxRate(payload) {
    var postData = switchFxToApi(payload)
    return axios.patch(AUTO_FX_API + 'UsedSources/' + postData.id, postData)
  },
  massDeactivateAutoFxRate(payload) {
    var postData = massDeactivateFxToApi(payload)
    return axios.delete(AUTO_FX_API + 'UsedSources' + postData)
  },
}



