const MS_IN_MIN = 60 * 1000


// Make the dates so when they are parsed the correct date will be send to the server
function prepareForUTCString(date) {
  return new Date(date.getTime() - date.getTimezoneOffset() * MS_IN_MIN)
}

function formatDate(date) {
  if (!date) {
    return ''
  }
  const year = date.getUTCFullYear()
  const month = addLeadingZero(date.getUTCMonth() + 1)
  const day = addLeadingZero(date.getUTCDate())

  return year + '-' + month + '-' + day
}

function formatDateTime(date) {
  if (!date) {
    return ''
  }
  const fDate = formatDate(date)
  const hours = addLeadingZero(date.getUTCHours())
  const min = addLeadingZero(date.getUTCMinutes())
  const sec = addLeadingZero(date.getUTCSeconds())
  return fDate + ', ' + hours + ':' + min + ':' + sec
}

function addLeadingZero(n) {
  return (n < 10) ? '0' + n : n
}

// From Angular version:
let months = ['Jan', 'Feb', 'March', 'April', 'May', 'June', 'July', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec']

function isFirstMonthDate(date) {
  return date.getDate() === 1
}

// https://stackoverflow.com/questions/6355063/how-to-validate-date-if-is-the-last-day-of-the-month-with-javascript
function isLastMonthDay(date) {
  return new Date(date.getTime() + 86400000).getDate() === 1
}

function formatYear(year) {
  return '`' + year.toString().substr(-2)
}

function beautifyPeriod(fromDate, toDate) {
  var month1 = fromDate.getMonth()
  var year1 = fromDate.getFullYear()

  var month2 = toDate.getMonth()
  var year2 = toDate.getFullYear()

  var date1 = ''
  var date2 = months[month2] + ' ' + formatYear(year2)
  var isSameYear = year1 === year2

  if (isFirstMonthDate(fromDate) && isLastMonthDay(toDate)) {
    if (month1 !== month2 || !isSameYear) {
      date1 = months[month1]
    }
  } else {
    date1 = fromDate.getDate() + ' ' + months[month1]
    date2 = toDate.getDate() + ' ' + date2
  }

  date1 = isSameYear ? date1 : date1 + ' ' + formatYear(year1)
  date1 = date1 ? date1 + ' - ' : date1

  return date1 + date2
}

function beautifyDate(date) {
  return date.getDate() + ' ' + months[date.getMonth()] + ' ' + formatYear(date.getFullYear())
}

function beautifyDates(fromDate, toDate) {
  if (arguments.length === 1) {
    return beautifyDate(fromDate)
  } else if (!fromDate) {
    return beautifyDate(toDate)
  } else if (!toDate) {
    return beautifyDate(fromDate)
  } else {
    return beautifyPeriod(fromDate, toDate)
  }
}

export default {
  beautifyDates,
  prepareForUTCString,
  formatDate,
  formatDateTime,
}



