<template>
  <cp-form :save="save"
           :remove="remove"
           :cancel="cancel"
           :validate="validate"
           :edit-mode="!hasAnyData"
           @edit="onEdit">

    <div class="cp-row">
      <label for="auditorName">{{$t('Name')}}:</label>
      <input id="auditorName"
             type="text"
             class="form-control"
             v-model="cpModel.name"
             :disabled="!editMode" />
    </div>

    <div class="cp-row">
      <label for="auditorEmail">{{$t('Email')}}:</label>
      <input id="auditorEmail"
             type="text"
             class="form-control"
             v-model="cpModel.email"
             :disabled="!editMode" />
    </div>

    <div class="cp-row">
      <label for="auditorPhone">{{$t('Phone')}}:</label>
      <input id="auditorPhone"
             type="text"
             class="form-control"
             v-model="cpModel.phone"
             :disabled="!editMode" />
    </div>

    <div class="cp-row">
      <label for="auditorDdl">{{$t('Deadline')}}:</label>
      <RecurringDateModal
        id="auditorDdl"
        v-model="cpModel.auditDeadlineDate"
        name="auditorDdl"
        v-validate="'required'"
        :class="[{'form-control': true, 'is-invalid': errors.has('auditorDdl'), 'inactive': !editMode}]"
        :disabled="!editMode"
      ></RecurringDateModal>
    </div>

  </cp-form>
</template>


<script>
import RecurringDateModal from '@/components-app/modals/RecurringDateModal'
import singlePropSetMixin from './singlePropSetMixin'

export default {
  components: {
    RecurringDateModal,
  },
  mixins: [singlePropSetMixin('audit')],
}
</script>



