import axios from 'axios'
import {apiToContact, apiToReceivedInvitation, apiToSentInvitation, invitationToApi, apiToHoldingTree} from './converters/companyConverters'

const CONTACTS_API = '/Contacts/'


export default {
  // Contacts
  getCompanyContacts(company) {
    return axios.get(CONTACTS_API + 'Companies/' + company.id)
      .then(data => data.map(apiToContact))
  },

  // Holding Tree
  loadHoldingTree(company) {
    return axios.get('/CompaniesTree/' + company.id)
      .then(data => data.map(apiToHoldingTree))
  },

  // Invitations - Company
  getCompanySentInvitations(company) {
    return axios.get(CONTACTS_API + 'Companies/' + company.id + '/PendingSentInvitations')
      .then(data => data.map(apiToSentInvitation))
  },
  invite(senderCompany, invitation) {
    var postData = invitationToApi(invitation)
    return axios.post(CONTACTS_API + 'Companies/' + senderCompany.id, postData)
      .then(apiToSentInvitation)
  },
  inviteMyCompany(senderCompany, ids) {
    return axios.post(CONTACTS_API + 'Companies/' + senderCompany.id + '/OwnCompanies', ids)
  },
  cancel(invitation) {
    return axios.delete(CONTACTS_API + 'Invitations/' + invitation.id)
  },

  // Invitations - User
  getUserReceivedInvitations() {
    return axios.get(CONTACTS_API + 'PendingReceivedInvitations')
      .then(data => data.map(apiToReceivedInvitation))
  },
  accept(receiverCompany, invitation) {
    return axios.post(CONTACTS_API + 'Invitations/' + invitation.id + '/Companies/' + receiverCompany.id + '/Acceptance')
  },
  reject(invitation) {
    return axios.post(CONTACTS_API + 'Invitations/' + invitation.id + '/Rejection')
  },
}



