import stringUtils from '@/helpers/stringUtils'
import {COMPANY_PROPERTY_CATEGORIES} from '@/nomenclaturesConstants'


// * * * API TO MODEL * * *

function apiToPublicCompany(data) {
  var company = {
    id: data.id,
    name: data.name,
    country: {
      id: data.country.id,
      code: data.country.code,
      name: data.country.name,
    },
    regNum: data.registrationNumber,
    logo: apiToLogo(data.name, data.logoUrl),
  }

  return company
}

function apiToContact(data) {
  var company = {
    ...apiToPublicCompany(data),
    aliases: data.companyAliases.map(apiToAlias),
  }

  return company
}

function apiToCompanyShort(data) {
  var company = {
    ...apiToPublicCompany(data),
    currency: {
      id: data.currency.id,
      code: data.currency.code,
      name: data.currency.name,
    },
    address: data.addressText,
    parent: {
      id: data.parent.id,
      name: data.parent.name,
      fromDate: new Date(data.parent.fromDate),
    },
    aliases: data.ownCompanyAliases ? data.ownCompanyAliases.map(apiToAlias) : [],
    userSettings: data.userSettings,
    userRole: {
      id: data.userRole.id,
      label: data.userRole.label,
    },
    notificationsCount: data.notificationsCount,
  }

  if (data.topMostParent) {
    company.topParent = {
      id: data.topMostParent.id,
      name: data.topMostParent.name,
    }
  }
  return company
}

function apiToCompany(data) {
  var model = {
    ...apiToCompanyShort(data),
    corpTax: {
      num: data.taxNumber,
      // fillingDate: data.taxFillingDate,
      // dueDate: data.taxDueDate,
      // advancedPayments: data.taxAdvancedPaymentsRecurringDate,
    },
    audit: {
      name: data.auditorName,
      email: data.auditorEmail,
      phone: data.auditorPhone,
      // ddl: data.auditDeadlineDate,
    },
    personnel: data.companyPersonnels.map(apiToPersonnel),
    vats: data.companyVatNumbers.map(apiToVat),
    taxes: [],
    software: [],
    misc: [],
  }

  data.companyPropertyValues.reduce((obj, item) => {
    const cpPair = apiToCompanyProperty(item)

    switch (cpPair.category.id) {
      case COMPANY_PROPERTY_CATEGORIES.tax:
        obj.taxes.push(cpPair)
        // cpPair.category.name = 'tax'
        break
      case COMPANY_PROPERTY_CATEGORIES.software:
        obj.software.push(cpPair)
        // cpPair.category.name = 'software'
        break
      default:
        obj.misc.push(cpPair)
        // cpPair.category.name = 'misc'
    }

    return obj
  }, model)

  data.recurringDates.forEach((recDate) => {
    switch (recDate.typeId) {
      case 2:
        model.corpTax.fillingDate = recDate
        break
      case 3:
        model.corpTax.dueDate = recDate
        break
      case 4:
        model.corpTax.advancedPayments = recDate
        break
      case 5:
        model.audit.auditDeadlineDate = recDate
        break
    }
  })

  console.log('* api-to-company: ', model)

  return model
}


// * * * API TO MODEL * * *

function apiToAlias(data) {
  return {
    id: data.id,
    companyid: data.companyId,
    refCompanyid: data.refCompanyId,
    aname: data.aliasText,
  }
}

function apiToVat(data) {
  return {
    id: data.id,
    num: data.vatNumber,
    country: {
      id: data.countryId,
      name: data.countryName,
      code: data.countryCode,
    },
    reportingRecurringDate: data.reportingRecurringDate,
  }
}

function apiToPersonnel(data) {
  return {
    id: data.id,
    key: {
      id: data.typeKeyId,
      label: data.typeKeyText,
    },
    customKey: data.customKeyValue,
    name: data.name,
    email: data.email,
    phone: data.phone,
  }
}

function apiToCompanyProperty(data) {
  return {
    id: data.id,
    category: {
      id: data.typeId,
      name: data.typeName,
    },
    key: {
      id: data.keyId,
      label: data.keyName,
    },
    customKey: data.customKey,
    value: data.value,
  }
}

function setProp(objDest, objDestProp, objSource, objSourceProp) {
  if (typeof objSource[objSourceProp] !== 'undefined') {
    objDest[objDestProp] = objSource[objSourceProp]
  }
}

function companyToApi(company) {
  var data = {}

  setProp(data, 'id', company, 'id')
  setProp(data, 'name', company, 'name')
  setProp(data, 'registrationNumber', company, 'regNum')
  setProp(data, 'addressText', company, 'address')

  if (company.aliases) { data.ownCompanyAliases = company.aliases.map(aliasToApi) }
  if (company.personnel) { data.companyPersonnels = company.personnel.map(personnelToApi) }
  if (company.vats) { data.companyVatNumbers = company.vats.map(vatToApi) }

  if (company.taxes) { data.taxes = company.taxes.map(companyPropertyToApi) }
  if (company.software) { data.software = company.software.map(companyPropertyToApi) }
  if (company.misc) { data.companyPropertyValues = company.misc.map(companyPropertyToApi) }

  if (company.country) { data.countryId = company.country.id }
  if (company.currency) { data.currencyId = company.currency.id }
  if (company.parent) { data.parentId = company.parent.id }
  setProp(data, 'fromDate', company, 'fromDate')

  let recurringDates = []

  // Corporate Tax
  if (company.corpTax && company.corpTax.num !== null) {
    data.taxNumber = company.corpTax.num
    let fillingDate = {
      months: company.corpTax.fillingDate.months,
      typeId: 2,
    }
    let dueDate = {
      months: company.corpTax.dueDate.months,
      typeId: 3,
    }
    let advancedPayments = {
      months: company.corpTax.advancedPayments.months,
      typeId: 4,
    }
    if (company.corpTax.fillingDate !== null) {
      fillingDate.dayOfMonth = company.corpTax.fillingDate.dayOfMonth
    }
    if (company.corpTax.dueDate !== null) {
      dueDate.dayOfMonth = company.corpTax.dueDate.dayOfMonth
    }
    if (company.corpTax.advancedPayments !== null) {
      advancedPayments.dayOfMonth = company.corpTax.advancedPayments.dayOfMonth
    }
    recurringDates.push(fillingDate)
    recurringDates.push(dueDate)
    recurringDates.push(advancedPayments)
  } else if (company.corpTax && company.corpTax.num === null) {
    data.taxNumber = null
    let fillingDate = {
      months: '000000000000',
      typeId: 2,
    }
    let dueDate = {
      months: '000000000000',
      typeId: 3,
    }
    let advancedPayments = {
      months: '000000000000',
      typeId: 4,
    }
    recurringDates.push(fillingDate)
    recurringDates.push(dueDate)
    recurringDates.push(advancedPayments)
  }

  // Audit
  if (company.audit && company.audit.name !== null && company.audit.email !== null && company.audit.phone !== null) {
    data.auditorName = company.audit.name
    data.auditorEmail = company.audit.email
    data.auditorPhone = company.audit.phone
    let auditDeadlineDate = {
      months: company.audit.auditDeadlineDate.months,
      typeId: 5,
    }
    if (company.audit.auditDeadlineDate !== null) {
      auditDeadlineDate.dayOfMonth = company.audit.auditDeadlineDate.dayOfMonth
    }
    recurringDates.push(auditDeadlineDate)
  } else if (company.audit && company.audit.name === null && company.audit.email === null && company.audit.phone === null) {
    data.auditorName = null
    data.auditorEmail = null
    data.auditorPhone = null
    let auditDeadlineDate = {
      months: '000000000000',
      typeId: 5,
    }
    recurringDates.push(auditDeadlineDate)
  }

  if (recurringDates.length !== 0) { data.recurringDates = recurringDates }
  return data
}

function aliasToApi(model) {
  return {
    id: model.id,
    companyid: model.companyId,
    refCompanyid: model.refCompanyId,
    aliasText: model.aname,
  }
}

function personnelToApi(model) {
  let data = {
    id: model.id,
    typeKeyid: model.key.id,
    customKeyValue: model.customKey,
    name: model.name,
    email: model.email,
    phone: model.phone,
  }
  if (model.company) {
    data.companyId = model.company.id
  }
  return data
}

function vatToApi(model) {
  var data = {
    vatNumber: model.num,
    countryId: model.country.id,
    reportingRecurringDate: {
      typeId: 1,
      months: model.reportingRecurringDate.months,
      dayOfMonth: model.reportingRecurringDate.dayOfMonth,
    },
  }

  if (model.id) {
    data.id = model.id
    data.reportingRecurringDate.targetId = model.id
  }

  if (model.company) {
    data.companyId = model.company.id
    data.reportingRecurringDate.companyId = model.company.id
  }

  return data
}

function companyPropertyToApi(model) {
  var data = {
    typeid: model.category.id,
    value: model.value,
    customKey: model.customKey,
    keyId: model.key.id,
  }

  if (model.company) {
    data.companyId = model.company.id
  }

  if (model.id) {
    data.id = model.id
  }

  // if (model.keyId === null) {
  //   data.customKey = model.customKey
  //   data.keyId = null
  // } else {
  //   data.customKey = null
  //   data.keyId = model.key.id
  // }

  console.log(data)
  return data
}


function apiToLogo(name, url) {
  let randomNumber = Math.floor(Math.random() * 10001)
  var logo = {}
  if (url) {
    logo.URL = url + '?rand=' + randomNumber
  } else {
    logo.initials = name ? stringUtils.getFirstLetter(name) : ''
  }
  return logo
}


// * * * INVITATION * * *
function apiToReceivedInvitation(data) {
  var invitation = apiToInvitation(data)
  invitation.sender = apiToInvitationSender(data)
  invitation.receiver = apiToInvitationReceiver(data)
  return invitation
}

function apiToSentInvitation(data) {
  var invitation = apiToInvitation(data)
  invitation.sender = {
    user: apiToInvitationUser(data),
  }
  invitation.receiver = apiToInvitationReceiver(data)
  return invitation
}

function apiToInvitation(data) {
  return {
    id: data.id,
    // isActive: data.isActive,
    ddl: new Date(data.expirationDate),
  }
}

function apiToInvitationReceiver(data) {
  return {
    email: data.invitedUserEmail,
    name: data.invitedCompanyName,
    regNum: data.invitedCompanyRegistrationNumber,
    country: {
      id: data.invitedCompanyCountry ? data.invitedCompanyCountry.id : '',
      code: data.invitedCompanyCountry ? data.invitedCompanyCountry.code : '',
      name: data.invitedCompanyCountry ? data.invitedCompanyCountry.name : '',
    },
    logo: apiToLogo(data.invitedCompanyName),
  }
}

function apiToInvitationSender(data) {
  return {
    company: {
      id: data.companyId,
      name: data.companyName,
      regNum: data.companyRegistrationNumber,
      country: {
        id: data.companyCountry.id,
        code: data.companyCountry.code,
        name: data.companyCountry.name,
      },
      logo: apiToLogo(data.companyName, data.companyLogoUrl),
    },
    user: apiToInvitationUser(data),
  }
}

function apiToInvitationUser(data) {
  return {
    name: data.inviterName,
    email: data.inviterEmail,
  }
}

function invitationToApi(data) {
  return {
    invitedCompanyName: data.name,
    invitedCompanyCountryId: data.country.id,
    invitedCompanyRegistrationNumber: data.regNum,
    collaboratorEmail: data.email,
    message: data.message,
  }
}

function apiToHoldingTree(data) {
  return {
    name: data.name,
    id: data.id,
    country: data.country,
    addressText: data.addressText,
    currency: data.currency,
    ownCompanyAliases: data.ownCompanyAliases,
    regNum: data.registrationNumber,
    parent: data.parent,
    children: data.children.map(apiToHoldingTree),
    logo: apiToLogo(data.name, data.logoUrl),
  }
}


export {
  // API TO MODEL
  apiToCompanyShort,
  apiToCompany,
  apiToContact,
  apiToAlias,
  apiToPersonnel,
  apiToVat,
  apiToCompanyProperty,
  apiToLogo,
  // MODEL TO API
  companyToApi,
  aliasToApi,
  vatToApi,
  personnelToApi,
  companyPropertyToApi,
  // INVITATION
  apiToReceivedInvitation,
  apiToSentInvitation,
  invitationToApi,
  // HOLDING TREE
  apiToHoldingTree,
}



