import authService from '@/services/authService'
import router from '@/router'

const USER_DATA_STORATGE_KEY = 'ud'
const AUTH_TOKEN_STORATGE_KEY = 'at'


const state = {
  user: null,
  token: null,
}

const getters = {
  user: (state) => state.user,
  token: (state) => state.token,
}

const mutations = {
  setToken(state, payload) { state.token = payload },
  setUser(state, payload) { state.user = payload },
  clearUser(state) {
    state.user = null
    state.token = null
  },
}

const actions = {
  loadUserCash({commit, dispatch}) {
    const token = sessionStorage.getItem(AUTH_TOKEN_STORATGE_KEY)
    if (token) {
      commit('setToken', token)
    }
    const userData = JSON.parse(sessionStorage.getItem(USER_DATA_STORATGE_KEY))
    if (userData) {
      commit('setUser', userData)
      dispatch('userNotificationsCountTimer', true)
    }
  },
  register({commit}, formData) {
    return authService.register(formData)
  },
  login({commit, dispatch}, credentials) {
    return authService.login(credentials)
      .then(userData => {
        dispatch('resetCompanyStore')
        dispatch('userNotificationsCountTimer', true)
        sessionStorage.setItem(USER_DATA_STORATGE_KEY, JSON.stringify(userData))
        commit('setUser', userData)
      })
  },
  logout({dispatch}) {
    return authService.logout()
      .finally(() => {
        dispatch('clearout')
        dispatch('userNotificationsCountTimer', false)
      })
  },
  clearout({commit}) {
    commit('clearUser')
    commit('clearInvitations')
    router.push('/login')
    sessionStorage.clear()
  },
  setToken({commit}, token) {
    commit('setToken', token)
    sessionStorage.setItem(AUTH_TOKEN_STORATGE_KEY, token)
  },
  reloadUser({commit}) {
    return authService.getUser()
      .then(payload => {
        commit('setUser', payload)
        sessionStorage.setItem(USER_DATA_STORATGE_KEY, JSON.stringify(payload))
      })
  },
  confirmAccount({commit}, payload) {
    let query = payload.c || ''
    return authService.confirmAccount(query)
  },
}


export default {
  state,
  getters,
  mutations,
  actions,
}



