function numToDec(value, decNumber) {
  const config = {
    minimumFractionDigits: decNumber,
    maximumFractionDigits: decNumber,
  }

  return (+value).toLocaleString('en-US', config)
}

export default function(value) {
  return numToDec(value, 2)
}



