import UploadForm from './UploadForm/index'
import RcTable from './RecognizedCompaniesTable/index'
import UcTable from './UnrecognizedCompaniesTable/index'
import SummaryTable from './Summary/index'

export default {
  name: 'UploadPage',
  components: {
    UploadForm,
    RcTable,
    UcTable,
    SummaryTable,
  },
  data() {
    return {
      loading: false,
      uploadData: null,
      historyComponent: this.$route.params.historyComponent ? this.$route.params.historyComponent : null,
    }
  },
  computed: {
    summary() {
      return this.$store.getters.importSummary
    },
  },
  created() {
    if (this.$route.params.uploadData) {
      this.uploadData = this.$route.params.uploadData
    }
  },
  methods: {
    saveRecognisedRecords() {
      this.$store.dispatch('saveValid')
        .then(() => {
          this.$customMsg.showSuccessMessage(this.$t('Saved records successfully'))
          this.$store.commit('clearImport')
        })
        .catch(errRes => {
          this.$customMsg.showErrorMessage(this.$t(errRes.messageDetail))
        })
        .finally(() => {
          this.loading = false
          if (this.historyComponent !== null) { this.$router.push(this.historyComponent) }
        })
    },
    onUpload() {
      this.loading = true
      this.$store.dispatch('loadSummary')
        .then(() => {
          this.loading = false
        })
    },
    cancel() {
      this.$store.commit('clearImport')
    },
  },
}



