<template>
  <div class="btn-group btn-group-sm bulk-selector" role="group">
    <button type="button" class="btn btn-secondary">
      <input type="checkbox" ref="checkbox" :checked="areSelected" @change="toggleAll">
    </button>

    <button v-if="totalSelectedCount" type="button" class="btn btn-secondary">
      <span>{{totalSelectedCount}} {{ totalSelectedCount > 1 ? 'Records' : 'Record' }} selected</span>
    </button>

    <b-dropdown v-if="totalSelectedCount && $slots.default" text="Bulk actions">
      <slot></slot>
    </b-dropdown>
  </div>
</template>


<script>
var vueComponentOptions = {}

var vueComponentFn = function(tableStore) {
  vueComponentOptions.props = {
    items: {
      type: Array,
      required: true,
    },
  }

  vueComponentOptions.computed = {
    itemsCount() {
      return this.items.length
    },
    totalSelectedCount() {
      return tableStore.getters.selectedItems.length
    },
    pageSelectedCount() {
      return this.items.reduce(function(count, item) {
        if (tableStore.getters.isItemSelected(item)) { count++ }
        return count
      }, 0)
    },
    areSelected() {
      if ((this.itemsCount - this.pageSelectedCount) === 0) {
        // All selected
        this.checkHalf(false)
        return true
      } else if (this.totalSelectedCount === 0) {
        // None selected
        this.checkHalf(false)
        return false
      } else {
        // Some selected
        this.checkHalf(true)
        return false
      }
    },
  }


  vueComponentOptions.methods = {
    checkHalf(toggle) {
      if (this.$refs.checkbox) { this.$refs.checkbox.indeterminate = toggle }
    },
    toggleAll(event) {
      const payload = {
        items: this.items,
        areSelected: event.target.checked,
      }
      tableStore.mutations.setItemsSelection(payload)
    },
  }

  return vueComponentOptions
}

vueComponentFn.options = vueComponentOptions

export default vueComponentFn

</script>



