import ReconciliationsTable from './ReconciliationsTable/index'
import CreateReconciliationModal from './CreateReconciliationModal/index'

export default {
  name: 'ReconciliationsPage',
  components: {
    ReconciliationsTable,
    CreateReconciliationModal,
  },
  data() {
    return {
      loading: false,
      activeTabId: 0,
      createModalOpened: false,
      startRCForId: null,
    }
  },
  created() {
    if (this.$route.params.startReconciliationFor) {
      this.startRCForId = this.$route.params.startReconciliationFor
      setTimeout(() => {
        this.createModalOpened = true
      }, 500)
    }
  },
  methods: {
    onTabChange(index) {
      this.activeTabId = index
    },
  },
}



