import axios from 'axios'
import {
  createReconciliationToApi,
  reconciliationToApi,
  companyIdToApi,
  apiToRecSummary,
  apiToReconciliation,
  delMultipleReconToApi,
  reconciliationStatusToApi,
} from './converters/reconciliationConverters'

const RECONCILIATION_API = '/Reconciliations/'

export default {

  createReconciliation(payload, selectedCompany) {
    var postData = createReconciliationToApi(payload, selectedCompany)
    return axios.post(RECONCILIATION_API, postData)
    // .then(apiToSentInvitation)
  },

  getReconciliation(payload, selectedCompany) {
    var getData = reconciliationToApi(payload, selectedCompany)
    return axios.get(RECONCILIATION_API + getData.id + '/Companies/' + getData.companyId)
      .then(apiToReconciliation)
  },

  editSelReconciliationStatus(payload, selectedCompany) {
    var postData = reconciliationStatusToApi(payload, selectedCompany)
    return axios.patch(RECONCILIATION_API + postData.id + '/Companies/' + postData.companyId, postData.data)
  },

  getReconciliationSummary(payload, selectedCompany) {
    var getData = reconciliationToApi(payload, selectedCompany)
    return axios.get('/ReconciliationSummary/' + getData.id + '/Companies/' + getData.companyId)
      .then(apiToRecSummary)
  },

  getOpenReconciliations(payload, selectedCompany) {
    var getData = companyIdToApi(payload, selectedCompany)
    return axios.get(RECONCILIATION_API + 'Companies/' + getData.companyId + '?statusId=1')
      .then(data => data.map(apiToReconciliation))
  },

  getFinishedReconciliations(payload, selectedCompany) {
    var getData = companyIdToApi(payload, selectedCompany)
    return axios.get(RECONCILIATION_API + 'Companies/' + getData.companyId + '?statusId=2')
      .then(data => data.map(apiToReconciliation))
  },

  getRejectedReconciliations(payload, selectedCompany) {
    var getData = companyIdToApi(payload, selectedCompany)
    return axios.get(RECONCILIATION_API + 'Companies/' + getData.companyId + '?statusId=3')
      .then(data => data.map(apiToReconciliation))
  },
  deleteReconciliation(payload) {
    return axios.delete(RECONCILIATION_API + payload.id)
  },
  deleteMultipleReconciliation(payload) {
    let delString = delMultipleReconToApi(payload)
    return axios.delete(RECONCILIATION_API + delString)
  },
}



