// import RelatedPartiesForm from '../RelatedPartiesForm'
import Datepicker from 'vuejs-datepicker'
import {bg, en} from 'vuejs-datepicker/dist/locale/index'
import CompanyCard from '@/components-app/CompanyCard'
import RelatedPartiesModal from '../RelatedPartiesModal/index'

export default {
  components: {
    // RelatedPartiesForm,
    Datepicker,
    CompanyCard,
    RelatedPartiesModal,
  },
  created() {
  },
  data() {
    return {
      sort: {
        sortedBy: 'toDate',
        isSortDescending: false,
      },
      loading: false,
      itemsPerPage: 10,
      currentPage: 1,
      tableFields: [ 'name', 'detailId', 'fromDate', 'toDate' ],
      locales: {
        en: en,
        bg: bg,
      },
      editData: {
        id: null,
        fromDate: null,
        toDate: null,
        description: '',
      },
      showRPModal: false,
      formData: {
        fromDate: null,
        toDate: null,
      },
      optionsPerPage: [
        { value: 10, text: '10' },
        { value: 25, text: '25' },
        { value: 50, text: '50' },
        { value: 100, text: '100' },
      ],
    }
  },
  computed: {
    noContacts() {
      return !this.$store.getters.contacts.length
    },
    companyRelatedParties() {
      return this.$store.getters.companyRelatedParties.relatedParties
    },
    count() {
      return this.$store.getters.companyRelatedParties.totalCount || 0
    },
    numberOfPages() {
      return Math.ceil(this.count / this.itemsPerPage)
    },
    startIndex() {
      return ((this.currentPage - 1) * this.itemsPerPage) + 1
    },
    endIndex() {
      return this.currentPage * this.itemsPerPage
    },
  },
  methods: {
    openRPModal() {
      this.$refs.relatedPartiesModal && this.$refs.relatedPartiesModal.resetForm()
      this.showRPModal = true
    },
    onAddRelatedParties() {
      this.showRPModal = false
      this.loadRelatedParties()
    },
    onRPModalOk(event) {
      event.preventDefault()
      this.$refs.relatedPartiesModal.submit()
    },
    getSortClass(sort) {
      let sortClass = 'sort '
      if (sort === this.sort.sortedBy) {
        sortClass += this.sort.isSortDescending ? 'sort-asc' : 'sort-desc'
      }
      return sortClass
    },
    setSort(sort) {
      if (sort !== this.sort.sortedBy) {
        this.sort.sortedBy = sort
        this.sort.isSortDescending = false
      } else {
        this.sort.isSortDescending = !this.sort.isSortDescending
      }
      this.loadRelatedParties()
    },
    editRP(current) {
      this.editData.id = current.detailId
      this.editData.fromDate = current.fromDate
      this.editData.toDate = current.toDate
      this.editData.description = current.description
    },
    deleteRP(party) {
      this.$store.dispatch('deleteRelatedParty', party.detailId)
        .then(() => {
          this.currentPage = 1
          this.loadRelatedParties()
        })
    },
    confirmEdit() {
      this.$store.dispatch('editRelatedParty', this.editData)
        .then(() => {
          this.currentPage = 1
          this.cancelEdit()
          this.loadRelatedParties()
        })
    },
    cancelEdit() {
      this.editData.id = null
    },
    loadRelatedParties() {
      let payload = {
        startDate: this.formData.fromDate,
        endDate: this.formData.toDate,
        startIndex: this.startIndex,
        endIndex: this.endIndex,
      }

      if (this.sort.sortedBy !== '') {
        payload.sort = this.sort.sortedBy
        payload.isSortDescending = this.sort.isSortDescending
      }

      this.loading = true
      this.$store.dispatch('loadSCRelatedParties', payload)
        .finally(() => {
          this.loading = false
        })
    },
    sendDates() {
      if (this.formData.fromDate !== null && this.formData.toDate !== null && this.formData.fromDate.getTime() > this.formData.toDate.getTime()) {
        this.$customMsg.showErrorMessage(this.$t('End date is before start date'))
      } else {
        this.currentPage = 1
        this.loadRelatedParties()
      }
    },
    loadPage(page) {
      this.currentPage = page
      this.loadRelatedParties()
    },
  },
}



