import axios from 'axios'
import store from '@/store'


axios.defaults.baseURL = process.env.API_V1_URL
// axios.defaults.headers.post['Content-Type'] = 'application/json'
// axios.defaults.headers.patch['Content-Type'] = 'application/json'
// axios.defaults.headers.put['Content-Type'] = 'application/json'


// REQUEST
axios.interceptors.request.use(function(config) {
  config.headers = config.headers || {}

  // AUTH
  const token = store.getters.token
  if (token) {
    config.headers['Token'] = token
  }
  // config.withCredentials = true

  return config
})


// RESPONSE

function successHandler(response) {
  var headers = response.headers

  if (headers.token) {
    store.dispatch('setToken', headers.token)
  }

  return (response.data.data) ? response.data.data : response.data
}

function errorHandler(errorObj) {
  var response = errorObj.response

  if (response.status === 401) {
    // The user is not logged-in
    store.dispatch('clearout')
    return Promise.reject(response.data.errors[0])
  }

  if (response.data && response.data.errors) {
    var error = response.data.errors[0]
    // return Promise.reject(new Error(error.message + ' ' + error.code + ': ' + error.messageDetail))
    // Tested !!! Working promises as expected, not entering .then - only entering .catch
    return Promise.reject(error)
  }

  return Promise.reject(errorObj)
}

axios.interceptors.response.use(successHandler, errorHandler)



