import axios from 'axios'
import {apiToRecord, recordsFormDataToApi, initialDataToApi} from './converters/recordConverters'
import {apiToContact} from './converters/companyConverters'

export default {

  getCurrentRecords(payload, selectedCompany) {
    var postData = recordsFormDataToApi(payload, selectedCompany)
    return axios.get('/ReportTransactions/Companies/' + postData.companyId + '/RefCompanies/' + postData.refCompanyId + '/StatementTypes/' + postData.statementTypeId + '/StartDate/' + postData.startDate + '/EndDate/' + postData.endDate + '?payables=true&receivables=true&matched=true&unmatched=true')
      .then(data => data.map(apiToRecord))
  },

  getRecordCompanyList(payload, selectedCompany) {
    var postData = initialDataToApi(payload, selectedCompany)
    return axios.get('/ReportingOppositeCompanies/' + postData.companyId + '/StatementTypes/' + postData.statementTypeId + '/StartDate/' + postData.startDate + '/EndDate/' + postData.endDate)
      .then(data => data.map(apiToContact))
  },
}



