import collaboratorService from '@/services/collaboratorService'
import {thenHandler} from '../storeUtils'
import findIndex from 'lodash.findindex'

const INITIAL_STATE = {
  collaborators: [],
}

var state = {...INITIAL_STATE}

const getters = {
  collaborators: (state) => state.collaborators,
}


const mutations = {
  setCollaborators(state, payload) { state.collaborators = payload },
  addCollaborator(state, payload) { state.collaborators.push(payload) },
  updateCollaborator(state, payload) {
    const index = findIndex(state.collaborators, item => item.id === payload.id)
    state.collaborators[index] = payload
  },
  removeCollaborator(state, payload) {
    const index = state.collaborators.indexOf(payload)
    state.collaborators.splice(index, 1)
  },
}


const actions = {
  resetCollaborators({ state }) {
    Object.assign(state, INITIAL_STATE)
  },

  loadSCCollaborators({ commit, getters }) {
    return collaboratorService.getCompanyCollaborators(getters.selectedCompany, getters.user)
      .then(thenHandler(commit, 'setCollaborators'))
  },
  addSCCollaborator({ commit, getters }, formData) {
    return collaboratorService.create(getters.selectedCompany, formData, getters.user)
      .then(thenHandler(commit, 'addCollaborator'))
  },
  updateCollaborator({ commit }, newModel) {
    return collaboratorService.update(newModel)
      .then(() => {
        commit('updateCollaborator', newModel)
        return true
      })
  },
  removeCollaborator({ commit }, model) {
    return collaboratorService.remove(model)
      .then(() => {
        commit('removeCollaborator', model)
      })
  },
}


export default {
  state,
  mutations,
  actions,
  getters,
}



