import {STATEMENT_TYPES} from '@/nomenclaturesConstants'
import dateUtils from '@/helpers/dateUtils'
import {apiToLogo} from './companyConverters'

// Reconciliations

function createReconciliationToApi(payload, selectedCompany) {
  let output = []
  payload.forEach((data) => {
    output.push({
      companyId: selectedCompany.id,
      refCompanyId: data.company.id,
      assignedUserEmail: data.assignee ? data.assignee.email : '',
      statementTypeId: data.statement.id,
      startDate: data.fromDate,
      endDate: data.toDate,
      isDisclosed: data.disclosed,
      deadlineDate: data.ddl,
    })
  })
  return output
}

function companyIdToApi(data, selectedCompany) {
  return {
    companyId: selectedCompany.id,
  }
}

function reconciliationToApi(data, selectedCompany) {
  return {
    id: data,
    companyId: selectedCompany.id,
  }
}

function listToApiHelper(data, selectedCompany, queryString) {
  return {
    companyId: selectedCompany.id,
    refCompanyId: data.selectedReconciliation.refCompany.id,
    statementTypeId: data.selectedReconciliation.statement.id,
    startDate: data.selectedReconciliation.startDate ? dateUtils.formatDate(data.selectedReconciliation.startDate) : '1900-01-01',
    endDate: data.selectedReconciliation.endDate ? dateUtils.formatDate(data.selectedReconciliation.endDate) : null,
    queryString: queryString,
  }
}

function exceptionListToApi(data, selectedCompany) {
  let queryString = '?unmatched=true'
  if (data.areOUT) {
    queryString += '&payables=true'
  }
  if (data.areIN) {
    queryString += '&receivables=true'
  }
  return listToApiHelper(data, selectedCompany, queryString)
}

function matchedListToApi(data, selectedCompany) {
  let queryString = '?matched=true'
  queryString += data.areIN ? '&receivables=true' : '&payables=true'
  return listToApiHelper(data, selectedCompany, queryString)
}

function autoMatchListToApi(data, selectedCompany) {
  let queryString = '?'
  if (data.areOUT) {
    queryString += 'payables=true'
  }
  if (data.areIN) {
    if (queryString !== '?') {
      queryString += '&'
    }
    queryString += 'receivables=true'
  }
  return listToApiHelper(data, selectedCompany, queryString)
}

function manualMatchListToApi(data, selectedCompany) {
  let ids = []
  data.selectedExceptions.forEach((exception) => {
    ids.push(exception.id)
  })
  return {
    companyId: selectedCompany.id,
    statementTypeId: data.selectedReconciliation.statement.id,
    payload: {
      ids: ids,
    },
    queryString: data.selectedReconciliation.statement.id === 1 ? '?endDate=' + dateUtils.formatDate(data.selectedReconciliation.endDate) : '',
  }
}

function apiToRecordList(data) {
  return {
    id: data.id,
    accType: data.accountText,
    ref: data.referenceText,
    viewDocDate: new Date(data.documentDate),
    viewPostingDate: new Date(data.dueDate),
    docCurrency: data.currencyCode,
    docAmountStr: data.documentAmount,
    fxRate: data.currentReportFxRate,
    amountInRepCurrency: Number(data.amountInReportCurrency),
    amountInRepCurrencyStr: data.amountInReportCurrency,
    description: data.description,
    isOwn: data.isOwn,
  }
}

function apiToMatchedList(data) {
  let output = []
  data.forEach((current) => {
    if (!output[current.setId]) {
      output[current.setId] = {
        id: current.setId,
        matchingType: {
          id: current.statusId,
          label: current.statusDescription,
        },
        transactions: [apiToRecordList(current)],
      }
    } else {
      output[current.setId].transactions.push(apiToRecordList(current))
    }
  })
  return output.filter(() => { return true })
}

function apiToReconciliation(data) {
  return {
    id: data.id,
    refCompany: {
      id: data.refCompanyId,
      name: data.refCompanyName,
      logo: apiToLogo(data.refCompanyName, data.oppositeCompanyProfileImageUrl),
      country: {
        code: data.oppositeCompanyCountryCode,
      },
    },
    statusId: data.statusId,
    status: {
      id: data.statusId,
      description: data.statusDescription,
    },
    context: STATEMENT_TYPES.filter(statement => {
      return statement.id === data.statusId
    })[0].context,
    refCompanyStatus: {
      id: data.refCompanyStatusId,
      description: data.refCompanyStatusDescription,
    },
    isInitiatorCompany: data.isInitiatorCompany,
    startDate: new Date(data.startDate),
    endDate: new Date(data.endDate),
    dld: new Date(data.deadlineDate),
    statement: STATEMENT_TYPES.filter(statement => {
      return statement.id === data.statementTypeId
    })[0],
    isDisclosed: data.isDisclosed,
    matchedAmount: data.matchedAmount,
    matchedCount: data.matchedCount,
    unMatchedAmount: data.unMatchedAmount,
    unMatchedCount: data.unMatchedCount,
  }
}

function apiToRecSummary(data) {
  return {
    in: {
      exceptions: {
        me: {
          amount: data.receivables.unMatched.amount,
          count: data.receivables.unMatched.transactionsCount,
        },
        they: {
          amount: data.receivables.unMatched.refAmount,
          count: data.receivables.unMatched.refTransactionsCount,
        },
      },
      matched: {
        me: {
          amount: data.receivables.matched.amount,
          count: data.receivables.matched.transactionsCount,
        },
        they: {
          amount: data.receivables.matched.refAmount,
          count: data.receivables.matched.refTransactionsCount,
        },
      },
    },
    out: {
      exceptions: {
        me: {
          amount: data.payables.unMatched.amount,
          count: data.payables.unMatched.transactionsCount,
        },
        they: {
          amount: data.payables.unMatched.refAmount,
          count: data.payables.unMatched.refTransactionsCount,
        },
      },
      matched: {
        me: {
          amount: data.payables.matched.amount,
          count: data.payables.matched.transactionsCount,
        },
        they: {
          amount: data.payables.matched.refAmount,
          count: data.payables.matched.refTransactionsCount,
        },
      },
    },
  }
}

function apiToMessageThread(data) {
  let messages = []
  if (data.messages && data.messages.length !== 0) {
    data.messages.forEach(message => {
      messages.push({
        id: message.id,
        date: new Date(message.createdOn),
        from: {
          fullName: message.user.fullName,
          profileImageUrl: message.user.profileImageUrl,
          email: message.user.email,
        },
        body: message.body,
        isRead: message.isRead,
      })
    })
  }
  return {
    thread: {
      id: data.id,
      name: data.title,
    },
    messages: messages,
  }
}

function delMultipleReconToApi(data) {
  let payload = ''
  data.forEach((current) => {
    payload += ((payload.length === 0 ? '?' : '&') + 'ids=' + current.id)
  })
  return payload
}

function reconciliationStatusToApi(data, selectedCompany) {
  let payload = {
    data: {
      statusId: data.statusId,
    },
    companyId: selectedCompany.id,
    id: data.id,
  }
  return payload
}

export {
  apiToMessageThread,
  apiToRecordList,
  apiToMatchedList,
  matchedListToApi,
  exceptionListToApi,
  autoMatchListToApi,
  manualMatchListToApi,
  createReconciliationToApi,
  reconciliationToApi,
  apiToReconciliation,
  companyIdToApi,
  apiToRecSummary,
  delMultipleReconToApi,
  reconciliationStatusToApi,
}



