import dateUtils from '@/helpers/dateUtils'


// * * * API TO MODEL * * *

function apiToImportReceipt(data) {
  return {
    id: data.id,
    statementType: {
      id: data.statementTypeId,
    },
    fromDate: new Date(data.startDate),
    toDate: new Date(data.endDate),
  }
}

function apiToSummary(data) {
  var summary = {
    recordsCount: {},
  }

  summary.recognizedCompaniesSets = data.validTransactionsSummary.map(apiToRecognizedCompanySet)
  summary.recordsCount.validRecognizedCompany = countSummary(summary.recognizedCompaniesSets)

  summary.unrecognizedCompaniesSets = data.unknownRefCompanyTransactionsSummary.map(apiToUnrecognizedCompanySet)
  summary.recordsCount.validUnrecognizedCompany = countSummary(summary.unrecognizedCompaniesSets)

  summary.recordsCount.invalid = data.invalidTransactionsCount ? data.invalidTransactionsCount : 0
  summary.recordsCount.outOfScope = data.outOfScopeTransactionsCount ? data.outOfScopeTransactionsCount : 0
  summary.recordsCount.removed = data.userCanceledTransactionsCount ? data.userCanceledTransactionsCount : 0

  return summary
}

function countSummary(sets) {
  return sets.reduce(function(count, set) {
    return count + set.payables.count + set.receivables.count
  }, 0)
}

function apiToRecognizedCompanySet(data) {
  return {
    ...apiToUnrecognizedCompanySet(data),
    currency: {
      id: data.docCurrencyId,
      code: data.docCurrencyISOCode,
    },
  }
}

function apiToUnrecognizedCompanySet(data) {
  return {
    company: {
      id: data.companyId,
      name: data.companyName,
    },
    receivables: {
      amount: +data.receivablesAmount,
      count: +data.receivablesCount,
    },
    payables: {
      amount: +data.payablesAmount,
      count: +data.payablesCount,
    },
  }
}

function apiToRecord(data) {
  return {
    ...apiToErrorsAndLevel(data.errors),
    line: data.fileRowIndex,
    // isOwn: data.isOwn,
    account: data.accountText,
    ref: data.referenceText,
    isReceivable: data.isReceivable,
    payer: data.payerName,
    receiver: data.receiverName,
    docDate: new Date(data.documentDate),
    postingDate: new Date(data.postingDate),
    dueDate: new Date(data.dueDate),
    currency: {
      id: data.currencyId,
      code: data.currencyCode,
    },
    docAmount: data.documentAmount,
    desc: data.description,
  }
}

function apiToErrorsAndLevel(errorsArr) {
  var highestErrorLevel = 0

  const errors = errorsArr
    .map(function(error) {
      highestErrorLevel = (error.level > highestErrorLevel) ? error.level : highestErrorLevel

      return {
        level: {
          id: error.level,
          label: error.levelName,
        },
        type: {
          id: error.typeId,
          label: error.typeName,
        },
        desc: error.description,
      }
    })
    .sort(function(a, b) {
      return a.level.id - b.level.id
    })

  return {
    highestErrorLevel: highestErrorLevel,
    errors: errors,
  }
}


// * * * MODEL TO API * * *

function excelImportToApi(formData) {
  const jsonData = {
    companyid: formData.company.id,
    statementTypeid: formData.statementType.id,
    startDate: formData.fromDate ? dateUtils.prepareForUTCString(formData.fromDate) : null,
    endDate: formData.toDate ? dateUtils.prepareForUTCString(formData.toDate) : null,
  }

  var fd = new FormData()
  fd.append('data', JSON.stringify(jsonData))
  fd.append('file', formData.file)

  return fd
}


export {
  // API TO MODEL
  apiToImportReceipt,
  apiToSummary,
  apiToRecord,
  // MODEL TO API
  excelImportToApi,
}



