export default {
  data() {
    return {
      toggle: false,
      loading: false,
    }
  },
  methods: {
    open() {
      this.toggle = true
      document.body.classList.add('fpm-open-body')
    },
    close() {
      this.toggle = false
      document.body.classList.remove('fpm-open-body')
      this.$emit('close')
    },
  },
}



