import objUtils from '@/helpers/objUtils'

import MyCompanyForm from '@/components-app/forms/MyCompanyForm'

import Alias from './cp-forms/Alias'
import CorpTax from './cp-forms/CorpTax'
import Audit from './cp-forms/Audit'
import Vat from './cp-forms/Vat'
import Personnel from './cp-forms/Personnel'
import CpPair from './cp-forms/CpPair'
import AvatarChangeModal from '@/components-app/modals/AvatarChangeModal'
import Avatar from '@/components-lib/Avatar'

export default {
  components: {
    MyCompanyForm,
    Alias,
    CorpTax,
    Audit,
    Vat,
    Personnel,
    CpPair,
    AvatarChangeModal,
    Avatar,
  },
  data() {
    return {
      showAliasForm: false,
      showCorpTax: false,
      showAudit: false,
      showVatForm: false,
      showPersonnelForm: false,
      showTaxForm: false,
      showSoftwareForm: false,
      showMiscForm: false,
      loading: false,
    }
  },
  computed: {
    company() {
      return this.$store.getters.selectedCompany || {}
    },
    hasAnyCorpTaxData() {
      return this.company.corpTax ? objUtils.hasAnyData(this.company.corpTax) : false
    },
    hasAnyAuditData() {
      return this.company.audit ? objUtils.hasAnyData(this.company.audit) : false
    },
  },
  methods: {
    reloadSelCompany() {
      this.loading = true
      this.$store.dispatch('reloadSelCompany', this.company.id)
        .then(() => {
          this.loading = false
        })
    },
    deleteCompany() {
      this.loading = true
      this.$store.dispatch('deleteSelCompany', this.company.id)
        .then(() => {
          this.loading = false
          this.$router.push('Home')
        })
    },
    openChangeLogoModal() {
      this.$refs.logoModal.open()
    },
    uploadNewLogo(file) {
      this.loading = true
      this.$store.dispatch('uploadSCLogo', file)
        .then(() => {
          this.$customMsg.showSuccessMessage(this.$t('Successfully changed company logo'))
        })
        .catch(errRes => {
          this.$customMsg.showErrorMessage(this.$t(errRes.messageDetail))
        })
        .finally(() => {
          this.$store.dispatch('reloadSelCompany', this.company.id)
            .then(() => {
              this.loading = false
            })
        })
    },
    deleteLogo() {
      this.loading = true
      this.$store.dispatch('deleteCSLogo')
        .then(() => {
          this.$customMsg.showSuccessMessage(this.$t('Successfully deleted company logo'))
        })
        .catch(errRes => {
          this.$customMsg.showErrorMessage(this.$t(errRes.messageDetail))
        })
        .finally(() => {
          this.$store.dispatch('reloadSelCompany', this.company.id)
            .then(() => {
              this.loading = false
            })
        })
    },
  },
}



