import importFPMMixin from '../../UploadPage/importFPMMixin'
import CreateReconciliationForm from './CreateReconciliationForm/'
import CreateReconciliationTable from './CreateReconciliationTable/'

export default {
  name: 'CreateReconciliationModal',
  components: {
    CreateReconciliationForm,
    CreateReconciliationTable,
  },
  props: {
    startRCForId: {},
  },
  mixins: [importFPMMixin],
  data() {
    return {
      formActive: true,
      formData: {},
    }
  },
  methods: {
    load() {
      return Promise.resolve()
    },
    close() {
      this.formActive = true
      this.$refs.CrRecForm.resetForm()
      this.$emit('input', null)
    },
    closeModal() {
      this.formActive = false
      this.$emit('input', null)
      this.$refs.modal.close()
    },
    backToForm() {
      this.formActive = true
    },
    formSubmitted(formData) {
      this.formData = formData
      this.formActive = false
    },
  },
}



