import ExceptionsTable from './ExceptionsTable/index'
import ExceptionsCalcTable from './ExceptionsCalcTable/index'
import dateUtils from '@/helpers/dateUtils'
import tableTools from '@/components-lib/table-tools'
import FullPageModal from '@/components-app/modals/FullPageModal/index'

let tableStore = tableTools.TableStore()

export default {
  name: 'ExceptionsModal',
  components: {
    FullPageModal,
    ExceptionsTable: ExceptionsTable(tableStore),
    ExceptionsCalcTable: ExceptionsCalcTable(tableStore),
  },
  props: {
    IN: {
      type: Boolean,
      required: true,
    },
    OUT: {
      type: Boolean,
      required: true,
    },
    open: {
      type: Boolean,
      required: true,
    },
  },
  data() {
    return {
      hasExceptions: false,
      loading: false,
    }
  },
  computed: {
    selectedCompany() {
      return this.$store.getters.selectedCompany
    },
    exceptionsRecords() {
      return this.$store.getters.exceptionsRecords
    },
    periodStr() {
      const reconciliation = this.$store.getters.selectedReconciliation
      const startDate = reconciliation.startDate ? new Date(reconciliation.startDate) : null
      const endDate = reconciliation.endDate ? new Date(reconciliation.endDate) : null
      return dateUtils.beautifyDates(startDate, endDate)
    },
    selectedReconciliation() {
      return this.$store.getters.selectedReconciliation
    },
    selectedExceptions() {
      return tableStore.getters.selectedItems
    },
    title() {
      var arr = []
      if (this.IN) { arr.push('Receivables') }
      if (this.OUT) { arr.push('Payables') }
      return arr.join(', ')
    },
  },
  methods: {
    close() {
      this.$emit('close')
    },
    onMatch() {
      var self = this
      self.loading = true
      var payload = {
        selectedExceptions: self.selectedExceptions,
        selectedReconciliation: self.selectedReconciliation,
      }
      self.$store.dispatch('manualMatch', payload)
        .then(() => {
          self.loadExceptions()
        })
        .finally(() => {
          self.loading = false
        })
    },
    autoMatch() {
      var self = this
      self.loading = true
      var payload = {
        areIN: self.IN,
        areOUT: self.OUT,
        selectedReconciliation: this.selectedReconciliation,
      }
      self.$store.dispatch('matchAuto', payload)
        .then(() => {
          self.loadExceptions()
        })
        // .catch(() => {
        //   self.showError()
        // })
        .finally(() => {
          self.loading = false
        })
    },
    exportFile() {
      var self = this
      self.loading = true
      const payload = {
        areIN: self.IN,
        areOUT: self.OUT,
        matched: false,
        exceptions: true,
      }
      self.$store.dispatch('exportRecords', payload)
        // .then(() => {
        //   self.showSuccess()
        // })
        // .catch(() => {
        //   self.showError()
        // })
        .finally(() => {
          self.loading = false
        })
    },
    loadExceptions() {
      var self = this
      self.loading = true
      var payload = {
        areIN: self.IN,
        areOUT: self.OUT,
        selectedReconciliation: this.selectedReconciliation,
      }

      self.$store.dispatch('loadExceptionsRecords', payload)
        .then((exceptions) => {
          tableStore.commit('setItems', exceptions)
          self.hasExceptions = !!exceptions.length
        })
        .finally(() => {
          self.loading = false
        })
    },
  },
  watch: {
    open(value) {
      if (value) {
        this.$refs.modal.open()
        this.loadExceptions()
      } else {
        this.$refs.modal.close()
        this.hasExceptions = false
      }
    },
  },
}



