import CompanyCard from '@/components-app/CompanyCard'
import tableTools from '@/components-lib/table-tools'
import comparators from '@/helpers/comparators'
import setItemsPerPageMixin from '@/components-lib/table-tools/tableStoreMixin'
import CompanySelect from '@/components-app/selects/CompanySelect'

var vueComponentOptions = {}

var vueComponentFn = function(tableStore) {
  vueComponentOptions.name = 'ReceivedInvitationsTable'

  vueComponentOptions.mixins = [setItemsPerPageMixin(tableStore)]

  vueComponentOptions.props = ['invitations']

  vueComponentOptions.directives = {
    Sort: tableTools.SortDirective(tableStore),
  }

  vueComponentOptions.components = {
    CompanyCard,
    CompanySelect,
    InputFilter: tableTools.InputFilter(tableStore),
    SelectFilter: tableTools.SelectFilter(tableStore),
    DateFilter: tableTools.DateFilter(tableStore),
  }

  vueComponentOptions.data = function() {
    return {
      companiesModels: [],
      loading: false,
      itemsPerPage: 10,
      showFilters: false,
      compare: {
        string: comparators['string'],
        numAsString: comparators['numAsString'],
        number: comparators['number'],
        day: comparators['day'],
      },
    }
  }

  vueComponentOptions.mounted = function() {
    this.setStore()
  }

  vueComponentOptions.computed = {
    myCompanies() {
      return this.$store.getters.myCompanies
    },
  }

  vueComponentOptions.methods = {
    setStore() {
      tableStore.commit('setItems', this.invitations)
    },
    toggleFilters: function() {
      this.showFilters = !this.showFilters
      if (!this.showFilters) {
        tableStore.commit('removeAllFilters')
      }
    },
    accept(index) {
      const invitation = this.invitations[index]
      const company = this.companiesModels[index]
      if (company) {
        this.loadStore('acceptContactInvitation', {company, invitation})
      } else {
        this.$customMsg.showErrorMessage('Please select company')
      }
    },
    reject(index) {
      const invitation = this.invitations[index]
      this.loadStore('rejectContactInvitation', invitation)
    },
    loadStore(action, payload) {
      this.loading = true
      this.$store.dispatch(action, payload)
        .catch(error => {
          this.$customMsg.showErrorMessage(error.messageDetail)
        })
        .finally(() => {
          this.loading = false
          this.$emit('reload')
        })
    },
  }

  vueComponentOptions.watch = {
    invitations() {
      this.setStore()
    },
  }

  return vueComponentOptions
}

vueComponentFn.options = vueComponentOptions

let store = tableTools.TableStore('ReceivedInvitationsTable')

export default vueComponentFn(store)



