import tableTools from '@/components-lib/table-tools'
import comparators from '@/helpers/comparators'
import tableStoreMixin from '@/components-lib/table-tools/tableStoreMixin'
import runAfterSCisSetMixin from '../../runAfterSCisSetMixin'
import CompanyCard from '@/components-app/CompanyCard'

let tableStore = tableTools.TableStore()

export default {
  name: 'OpenReconciliationsTable',
  mixins: [runAfterSCisSetMixin, tableStoreMixin(tableStore)],
  directives: {
    Sort: tableTools.SortDirective(tableStore),
  },
  components: {
    CompanyCard,
    InputFilter: tableTools.InputFilter(tableStore),
    SelectFilter: tableTools.SelectFilter(tableStore),
    DateFilter: tableTools.DateFilter(tableStore),
    BulkSelector: tableTools.BulkSelector(tableStore),
    CheckboxSelect: tableTools.CheckboxSelect(tableStore),
    MarkedFilter: tableTools.MarkedFilter(tableStore),
  },
  props: {
    recType: {},
  },
  created() {
    this.runAfterSCisSet(this.loadOpenReconciliations)
  },
  computed: {
    allItems() {
      return tableStore.getters.getItems
    },
    selectedReconciliations() {
      return tableStore.getters.selectedItems
    },
  },

  data() {
    return {
      reconciliations: null,
      editActiveForId: null,
      loading: false,
      itemsPerPage: 5,
      showFilters: false,
      compare: {
        string: comparators['string'],
        numAsString: comparators['numAsString'],
        number: comparators['number'],
        day: comparators['day'],
      },
    }
  },

  methods: {
    startUpload(reconciliation) {
      let uploadData = {
        statementID: reconciliation.statement.id,
        startDate: reconciliation.startDate,
        endDate: reconciliation.endDate,
      }
      let historyComponent = this.$route.path
      this.$router.push({name: 'Upload', params: { uploadData: uploadData, historyComponent: historyComponent }})
    },
    loadOpenReconciliations() {
      this.loading = true
      this.$store.dispatch('load' + this.recType + 'Reconciliations')
        .then(() => {
          this.reconciliations = this.$store.getters[this.recType + 'Reconciliations']
          this.loading = false
        })
    },
    deleteReconciliation(reconciliation) {
      this.loading = true
      this.$store.dispatch('deleteReconciliation', reconciliation)
        .then(() => {
          this.$customMsg.showSuccessMessage(this.$t('Successfully deleted reconciliation.'))
        })
        .catch((error) => {
          this.$customMsg.showErrorMessage(this.$t('Error') + ' : ' + error.messageDetail)
        })
        .finally(() => {
          this.loadOpenReconciliations()
        })
    },
    deleteSelectedReconciliations() {
      this.loading = true
      this.$store.dispatch('deleteMultipleReconciliation', this.selectedReconciliations)
        .then(() => {
          this.$customMsg.showSuccessMessage(this.$t('Successfully deleted reconciliation.'))
        })
        .catch((error) => {
          this.$customMsg.showErrorMessage(this.$t('Error') + ' : ' + error.messageDetail)
        })
        .finally(() => {
          this.loadOpenReconciliations()
        })
    },
    toggleFilters() {
      this.showFilters = !this.showFilters
      if (!this.showFilters) {
        tableStore.commit('removeAllFilters')
      }
    },
  },

  watch: {
    reconciliations(value) {
      tableStore.commit('setItems', value)
    },
  },
}



