<template>
    <ul>
        <TreeLeaf v-for="item in items" :key="item[state.idProp]" :node="item" :state="state" :preselectedCompany="preselectedCompany"></TreeLeaf>
    </ul>
</template>


<script>
  export default {
    name: 'TreeNode',
    components: {
      TreeLeaf: () => import('./TreeLeaf'),
    },
    props: ['nodes', 'state', 'preselectedCompany'],
    computed: {
      items() {
        return this.nodes
      },
    },
  }
</script>



