import tableTools from '@/components-lib/table-tools'
import Row from './CollaboratorsTableRow/index'
import {ROLES} from '@/nomenclaturesConstants'

let tableStore = tableTools.TableStore()

export default {
  name: 'CollaboratorsTable',
  components: {
    Row,
  },
  directives: {
    Sort: tableTools.SortDirective(tableStore),
  },
  data() {
    return {
      loading: false,
      collaborators: [],
    }
  },
  computed: {
    pageItems() {
      tableStore.commit('setItems', this.$store.getters.collaborators)
      return tableStore.getters.pageItems()
    },
  },
  methods: {
    reloadCollaborators() {
      this.loading = true
      this.$store.dispatch('loadSCCollaborators')
        .finally(() => {
          this.loading = false
        })
    },
  },

  watch: {
    pageItems: {
      handler(val) {
        this.collaborators = []
        let currentUserRole = val.filter((current) => { return current.isCurrent })[0].role.id
        val.forEach((current) => {
          let isLastSU = (current.role.id === 1 && this.pageItems.filter((col) => { return col.role.id === 1 }).length === 1)
          let canBeDeleted = current.role.id !== 1 || (current.role.id === 1 && this.pageItems.filter((col) => { return col.role.id === 1 }).length > 1)
          let currentUser = current.isCurrent
          let permittedRoles = []
          ROLES.forEach((role) => {
            if (currentUserRole <= role.id) { permittedRoles.push(role.id) }
          })
          let canBeEdited = (permittedRoles.indexOf(current.role.id) !== -1)
          let newObj = {
            isLastSU: isLastSU,
            canBeDeleted: canBeDeleted,
            permittedRoles: permittedRoles,
            currentUser: currentUser,
            currentUserRole: currentUserRole,
            canBeEdited: canBeEdited,
          }
          this.collaborators.push(Object.assign({}, current, newObj))
        })
      },
      deep: true,
    },
  },
}



