import invitationService from '@/services/contactService'
import {thenHandler} from '../storeUtils'
import findIndex from 'lodash.findindex'

const state = {
  invitations: [],
}

const getters = {
  receivedInvitations: (state) => state.invitations,
}

const mutations = {
  setUserReceivedInvitations(state, payload) { state.invitations = payload },
  removeInvitation(state, payload) {
    const index = findIndex(state.invitations, item => item.id === payload.id)
    state.invitations.splice(index, 1)
  },
  clearInvitations(state) {
    state.invitations = []
  },
}

const actions = {
  loadReceivedInvitations({ commit }) {
    return invitationService.getUserReceivedInvitations()
      .then(thenHandler(commit, 'setUserReceivedInvitations'))
  },
  acceptContactInvitation({ commit }, {company, invitation}) {
    return invitationService.accept(company, invitation)
      .then(thenHandler(commit, 'removeInvitation'))
  },
  rejectContactInvitation({ commit }, invitation) {
    return invitationService.reject(invitation)
      .then(thenHandler(commit, 'removeInvitation'))
  },
}


export default {
  state,
  getters,
  mutations,
  actions,
}



