import merge from 'lodash.merge'
import basePropMixin from '../../ProfilePage/cp-forms/basePropMixin'
import {ROLES} from '@/nomenclaturesConstants'


const vueComponentConfig = {
  props: {
    cProp: {
      required: false,
    },
  },
  data() {
    return {
      cpModel: Object.assign({}, this.cProp),
      roles: ROLES,
      editMode: true,
      loading: false,
    }
  },
  methods: {
    save() {
      this.loading = true
      this.$store.dispatch('addSCCollaborator', this.cpModel)
        .then(() => {
          this.$emit('done')
          this.loading = false
        })
    },
  },
}

export default merge({}, basePropMixin, vueComponentConfig)



