<template>
  <div class="tree">
    <TreeNode v-if="items.length" :nodes="items" :state="_self" :preselectedCompany="preselectedCompany"></TreeNode>
  </div>
</template>


<script>
  import TreeNode from './TreeNode'

  export default {
    name: 'Tree',
    components: {
      TreeNode,
    },
    props: {
      nodes: {
        required: true,
      },
      childrenProp: {
        default: 'children',
      },
      idProp: {
        default: 'id',
      },
      labelProp: {
        default: 'name',
      },
      preselectedCompany: {},
    },
    data() {
      return {
        deselectFunc: null,
      }
    },
    methods: {
      selectNode(item, deselectFunc) {
        if (this.deselectFunc) {
          this.deselectFunc()
        }
        this.$emit('changeSelected', item)
        this.deselectFunc = deselectFunc
      },
      expandAll() {
        this.$emit('expandAll')
      },
      collapseAll() {
        this.$emit('collapseAll')
      },
    },
    computed: {
      items() {
        return this.nodes || []
      },
    },
  }
</script>



