import tableStoreMixin from '@/components-lib/table-tools/tableStoreMixin'

var vueComponentOptions = {}

var vueComponentFn = function(tableStore) {
  vueComponentOptions.name = 'ExceptionsCalcTable'

  vueComponentOptions.mixins = [tableStoreMixin(tableStore)]

  vueComponentOptions.props = {
    exceptions: {
      required: true,
      default: [],
    },
    ownCompany: {
      required: true,
    },
    oppCompanyName: {
      required: true,
    },
  }

  vueComponentOptions.computed = {
    stats() {
      var stats = {
        own: {
          count: 0,
          amount: 0,
        },
        opp: {
          count: 0,
          amount: 0,
        },
      }
      // TODO: what if this.exceptions is null or []
      return this.exceptions.reduce((acc, exception) => {
        var side = stats[exception.isOwn ? 'own' : 'opp']
        side.count++
        side.amount += (+exception.amountInRepCurrency)
        side.amountStr = side.amount.toLocaleString('en-US', { minimumFractionDigits: 2 })
        return stats
      }, stats)
    },
    balance() {
      return this.stats.own.amount - this.stats.opp.amount
    },
    disableMatch() {
      return this.balance || !this.exceptions || !this.exceptions.length
    },
  }

  vueComponentOptions.methods = {
    match() {
      if (this.disableMatch) { return }
      this.$emit('match')
    },
  }

  return vueComponentOptions
}

vueComponentFn.options = vueComponentOptions

export default vueComponentFn



