<template>
  <div class="heading-text">
    {{ companiesStr }},
    {{ statementType.label }},
    {{ dates }}
  </div>
</template>


<script>
import dateUtils from '@/helpers/dateUtils'

export default {
  props: ['contractor'],
  computed: {
    statementType() {
      return this.$store.getters.statementType
    },
    dates() {
      const importReceipt = this.$store.getters.importReceipt
      var dates = dateUtils.formatDate(importReceipt.toDate)
      if (+importReceipt.statementType.id > 1) {
        dates = dateUtils.formatDate(importReceipt.fromDate) + ' - ' + dates
      }
      return dates
    },
    companiesStr() {
      var str = this.$store.getters.selectedCompany.name
      if (this.contractor) {
        str = str + ' - ' + this.contractor
      }
      return str
    },
  },
}
</script>



