import axios from 'axios'
import {relatedPartyToApi, apiToRelatedParty, relatedPartiesPayloadToQuery, relatedPartiesPatchDataToApi} from './converters/relatedPartiesConverters'

const RELATED_PARTIES_API = '/RelatedParties'

export default {
  getCompanyRelatedParties(company, payload) {
    return axios.get(RELATED_PARTIES_API + '/Companies/' + company.id + relatedPartiesPayloadToQuery(payload))
      .then(data => {
        data.relatedParties = data.relatedParties.map(apiToRelatedParty)
        return data
      })
  },
  addRelatedParty(company, payload) {
    let postData = relatedPartyToApi(company, payload)
    return axios.post(RELATED_PARTIES_API, postData)
      .then(data => apiToRelatedParty(data))
  },
  deleteRelatedParty(payload) {
    return axios.delete(RELATED_PARTIES_API + '/' + payload)
  },
  editRelatedParty(payload) {
    let patchData = relatedPartiesPatchDataToApi(payload)
    return axios.patch(RELATED_PARTIES_API + '/Details/' + payload.id, patchData)
  },
}



