import tableTools from '@/components-lib/table-tools'
import comparators from '@/helpers/comparators'
import tableStoreMixin from '@/components-lib/table-tools/tableStoreMixin'
import Datepicker from 'vuejs-datepicker'
import {en, bg} from 'vuejs-datepicker/dist/locale'

let tableStore = tableTools.TableStore()

export default {
  name: 'FxRatesTable',
  mixins: [tableStoreMixin(tableStore)],
  directives: {
    Sort: tableTools.SortDirective(tableStore),
  },
  props: {
    fxRates: {},
    averageFxRates: {},
    selectedDateProp: {},
  },
  components: {
    Datepicker,
    InputFilter: tableTools.InputFilter(tableStore),
    SelectFilter: tableTools.SelectFilter(tableStore),
    DateFilter: tableTools.DateFilter(tableStore),
    BulkSelector: tableTools.BulkSelector(tableStore),
    CheckboxSelect: tableTools.CheckboxSelect(tableStore),
    MarkedFilter: tableTools.MarkedFilter(tableStore),
  },
  computed: {
    allItems() {
      return tableStore.getters.getItems
    },
    selectedCompany() {
      return this.$store.getters.selectedCompany
    },
    selectedDate: {
      get() {
        return this.selectedDateProp
      },
      set(newValue) {
        this.$emit('changeSelectedDate', newValue)
      },
    },
  },

  data() {
    return {
      editActiveForId: null,
      loading: false,
      internalRate: 0,
      itemsPerPage: 5,
      showFilters: false,
      compare: {
        string: comparators['string'],
        numAsString: comparators['numAsString'],
        number: comparators['number'],
        day: comparators['day'],
      },
      locales: {
        en: en,
        bg: bg,
      },
    }
  },

  methods: {
    haveManual(rate) {
      return rate.manual !== null
    },
    showInvModal() {
      this.$emit('showInvModal')
    },
    haveAuto(rate) {
      return rate.auto !== null
    },
    toggleFilters() {
      this.showFilters = !this.showFilters
      if (!this.showFilters) {
        tableStore.commit('removeAllFilters')
      }
    },
    deleteFxRate(fxRate) {
      this.$store.dispatch('removeFxRate', fxRate)
        .then(() => {
          this.$emit('deleteFxRate', fxRate)
        })
        .catch(errRes => {
          this.$customMsg.showErrorMessage(this.$t(errRes.messageDetail))
        })
    },
    editFxRate(fxRate) {
      this.internalEditRate = fxRate.manual.rate
      this.editActiveForId = fxRate.id
    },
    cancelEdit() {
      this.editActiveForId = null
    },
    saveEdit() {
      let rate = this.pageItems.filter((current) => {
        return current.id === this.editActiveForId
      })[0]
      rate.manual.rate = this.internalEditRate
      this.$store.dispatch('editFxRate', rate)
        .then(() => { this.$emit('reload') })
      this.editActiveForId = null
    },
  },

  watch: {
    fxRates(value) {
      tableStore.commit('setItems', value)
    },
  },
}



