import Avatar from '@/components-lib/Avatar'

export default {
  name: 'ReconciliationMessageRow',
  components: {
    Avatar,
  },
  data() {
    return {
      messageText: '',
    }
  },
  props: {
    'recId': {},
  },
  computed: {
    user() {
      return this.$store.getters.user
    },
  },
  methods: {
    submit() {
      let payload = {
        recId: this.recId,
        message: this.messageText,
      }
      this.$store.dispatch('sendReconciliationMessage', payload)
        .then(() => {
          this.messageText = ''
          this.$emit('reload')
        })
    },
  },
}



