import collaborator from './collaboratorStore'
import company from './companyStore'
import contact from './contactStore'
import importStore from './importStore'
import recordsStore from './recordsStore'
import fxStore from './fxStore'
import reconciliationsStore from './reconciliationsStore'
import reconciliationStore from './reconciliationStore'


const state = {
  ...collaborator.state,
  ...company.state,
  ...contact.state,
  ...importStore.state,
  ...recordsStore.state,
  ...fxStore.state,
  ...reconciliationsStore.state,
  ...reconciliationStore.state,
}

const getters = {
  ...collaborator.getters,
  ...company.getters,
  ...contact.getters,
  ...importStore.getters,
  ...recordsStore.getters,
  ...fxStore.getters,
  ...reconciliationsStore.getters,
  ...reconciliationStore.getters,
}

const mutations = {
  ...collaborator.mutations,
  ...company.mutations,
  ...contact.mutations,
  ...importStore.mutations,
  ...recordsStore.mutations,
  ...fxStore.mutations,
  ...reconciliationsStore.mutations,
  ...reconciliationStore.mutations,
}

const actions = {
  ...collaborator.actions,
  ...company.actions,
  ...contact.actions,
  ...importStore.actions,
  ...recordsStore.actions,
  ...fxStore.actions,
  ...reconciliationsStore.actions,
  ...reconciliationStore.actions,
}


export default {
  state,
  getters,
  mutations,
  actions,
}



