<template>
  <cp-form :save="save"
           :remove="remove"
           :cancel="cancel"
           :validate="validate"
           :edit-mode="!cProp.name"
           @edit="onEdit">

    <div class="cp-row">
      <label>{{$t('Responsibility')}}:</label>
      <VueCustomSelect
        :options="responsibilityKeys"
        :value="cpModel"
        @input="onInput"
        name="customSelect"
        :class="[{ 'is-invalid': errors.has('responsibility')}]"
        :disabled="!editMode"
        v-validate="'required'"
        :loading="loading"
        :hasError="fields.customSelect && !fields.customSelect.valid && fields.customSelect.touched">
      </VueCustomSelect>
    </div>


    <div class="cp-row">
      <label>{{$t('Name')}}:</label>
      <input
             type="text"
             class="form-control"
             v-model="cpModel.name"
             name="name"

             v-validate="'required'"
             :class="[{ 'is-invalid': errors.has('name')}]"
             :disabled="!editMode" />
    </div>


    <div class="cp-row">
      <label>{{$t('Email')}}:</label>
      <input
             type="text"
             class="form-control"
             v-model="cpModel.email"
             name="email"

             v-validate="'required'"
             :class="[{ 'is-invalid': errors.has('email')}]"
             :disabled="!editMode" />
    </div>


    <div class="cp-row">
      <label>{{$t('Phone')}}:</label>
      <input
             type="text"
             class="form-control"
             v-model="cpModel.phone"
             name="phone"

             v-validate="'required'"
             :class="[{ 'is-invalid': errors.has('phone')}]"
             :disabled="!editMode" />
    </div>

  </cp-form>
</template>


<script>
import merge from 'lodash.merge'
import basePropMixin from './basePropMixin'
import VueCustomSelect from '@/components-lib/vue-custom-select/VueCustomSelect'

const vatVueConfig = {
  components: {
    VueCustomSelect,
  },
  created() {
    // TODO: ?? does it work, shoud use $ref na CpForm??
    this.loading = true
    this.$store.dispatch('loadResponsibilityKeys')
      .finally(() => {
        this.loading = false
      })
  },
  props: {
    cProp: {
      required: false,
      default() {
        return { key: { id: '' } }
      },
    },
  },
  data() {
    return {
      cpModel: Object.assign({}, this.cProp),
      editMode: !this.cProp.name,
    }
  },
  computed: {
    responsibilityKeys() {
      return this.$store.getters.responsibilityKeys
    },
  },
  methods: {
    save() {
      const action = this.cProp.id ? 'updateSCPersonnel' : 'createSCPersonnel'
      return this.$store.dispatch(action, this.cpModel)
        .then(() => {
          this.$emit('success')
        })
    },
    remove() {
      return this.$store.dispatch('deleteSCPersonnel', this.cProp)
        .then(() => {
          this.$emit('remove')
        })
    },
    onInput(data) {
      this.cpModel.key = data.key
      data.key.id = (data.key.id === 'input' ? null : data.key.id)
      this.cpModel.customKey = data.customKey
    },
  },
  watch: {
    cProp: {
      handler() {
        this.cpModel = Object.assign({}, this.cProp)
      },
      deep: true,
    },
  },
}

export default merge({}, basePropMixin, vatVueConfig)
</script>



