import NotificationPanel from '@/components-app/notifications/NotificationPanel'

export default {
  components: {
    NotificationPanel,
  },
  data() {
    return {
      loading: false,
      companyId: null,
      currentTime: null,
      currentNotification: 1,
      notificationsPerLoad: 10,
    }
  },
  computed: {
    notifications() {
      return this.$store.getters.companyNotifications
    },
    nextCompanyNotifications() {
      return this.$store.getters.nextCompanyNotifications
    },
    finalResult() {
      return this.nextCompanyNotifications.length === 0
    },
    payload() {
      let time = new Date(this.currentTime)
      return {
        companyId: this.companyId,
        currentTime: time.toISOString(),
        startIndex: this.currentNotification,
        endIndex: this.currentNotification + this.notificationsPerLoad - 1,
      }
    },
    panel_style() {
      return {
        height: this.windowHeight / 2 + 'px',
      }
    },
  },
  methods: {
    showModal(companyId) {
      this.currentNotification = 1
      this.companyId = companyId
      this.currentTime = Date.now()
      this.loadCompanyNotification()
      this.$refs.NotificationModalRef.show()
    },
    reloadCompanies() {
      this.$store.dispatch('loadMyCompanies')
      this.$store.dispatch('loadUserNotificationsCount')
    },
    loadMore() {
      this.currentNotification += this.notificationsPerLoad
      this.$store.dispatch('loadNextCompanyNotifications', this.payload)
        .then(() => {
          this.loading = false
        })
    },
    loadCompanyNotification() {
      this.loading = true
      this.$store.dispatch('loadCompanyNotifications', this.payload)
        .then(() => {
          this.$store.dispatch('markCompanyNotificationsOld', this.payload)
          this.loadMore()
        })
    },
    markCompanyNotificationsRead() {
      this.loading = true
      this.$store.dispatch('markCompanyNotificationsRead', this.payload)
        .then(() => {
          this.loading = false
        })
    },
    loadNextCompanyNotification() {
      this.loading = true
      if (!this.finalResult) {
        this.$store.dispatch('addCompanyNotifications', this.nextCompanyNotifications)
          .then(() => {
            this.loadMore()
          })
      }
    },
    handleScroll(e) {
      if (e.target.scrollHeight - e.target.scrollTop - e.target.clientHeight < 100 &&
        !this.loading && !this.finalResult) {
        this.loadNextCompanyNotification()
      }
    },
    goToCompanyNotifications() {
      this.showPanel = !this.showPanel
      this.currentNotification = 1
      this.$router.push({name: 'Notifications', params: { type: this.companyId }})
    },
    toggleCompanyNotification(current) {
      this.$store.dispatch('switchCompanyNotificationReadStatus', current.id)
    },
  },
}




