import axios from 'axios'
import {apiToCollaborator, collaboratorToApi} from './converters/collaboratorConverters'

const COLLABORATORS_API = '/Collaborators/'


export default {
  getCompanyCollaborators(company, user) {
    return axios.get(COLLABORATORS_API + 'Companies/' + company.id)
      .then(data => data.map((current) => apiToCollaborator(current, user)))
  },
  create(company, modelData, user) {
    modelData.company = company
    var postData = collaboratorToApi(modelData)

    return axios.post(COLLABORATORS_API, postData)
      .then(data => apiToCollaborator(data, user))
  },
  update(model) {
    var postData = collaboratorToApi(model)
    return axios.put(COLLABORATORS_API, postData)
  },
  remove(model) {
    return axios.delete(COLLABORATORS_API + 'Companies/' + model.company.id + '?userEmail=' + encodeURIComponent(model.email))
  },
}



