import authService from '@/services/authService'
import AvatarChangeModal from '@/components-app/modals/AvatarChangeModal'
import Avatar from '@/components-lib/Avatar'

export default {
  data() {
    return {
      loading: false,
      internalData: {},
      editActive: false,
      uploadedFile: null,
    }
  },
  components: {
    Avatar,
    AvatarChangeModal,
  },
  computed: {
    user() {
      return this.$store.getters.user
    },
  },
  mounted() {
    this.resetInternalData()
  },
  methods: {
    reloadUser() {
      setTimeout(() => {
        this.$store.dispatch('reloadUser')
          .then(() => {
            this.resetInternalData()
            this.loading = false
          })
      }, 500)
    },
    openChangeAvatarModal() {
      this.$refs.modal.open()
    },
    uploadNewAvatar(file) {
      this.loading = true
      authService.uploadAvatar(file)
        .then(() => {
          this.$customMsg.showSuccessMessage(this.$t('Successfully changed user avatar'))
        })
        .catch(errRes => {
          this.$customMsg.showErrorMessage(errRes.messageDetail)
        })
        .finally(() => {
          this.reloadUser()
        })
    },
    deleteAvatar(file) {
      this.loading = true
      authService.deleteAvatar()
        .then(() => {
          this.$customMsg.showSuccessMessage(this.$t('Successfully deleted user avatar'))
        })
        .catch(errRes => {
          this.$customMsg.showErrorMessage(errRes.messageDetail)
        })
        .finally(() => {
          this.reloadUser()
        })
    },
    resetInternalData() {
      this.internalData = Object.assign({}, this.$store.getters.user)
    },
    switchEdit() {
      if (this.editActive) { this.resetInternalData() }
      this.editActive = !this.editActive
    },
    submit() {
      let payload = {
        firstName: this.internalData.firstName,
        lastName: this.internalData.lastName,
        phone: this.internalData.phone,
      }
      this.loading = true
      authService.updateUser(payload)
        .then(res => {
          this.$customMsg.showSuccessMessage(this.$t('Updated account info for') + ': ' + res.email)
        })
        .catch(errRes => {
          this.$customMsg.showErrorMessage(errRes.messageDetail)
        })
        .finally(() => {
          this.reloadUser()
          this.editActive = !this.editActive
        })
    },
  },
}



