// import { MultiSelect } from 'vue-search-select'
import Datepicker from 'vuejs-datepicker'
import {en, bg} from 'vuejs-datepicker/dist/locale'
import runAfterSCisSetMixin from '../../../runAfterSCisSetMixin'
import merge from 'lodash.merge'
import {STATEMENT_TYPES} from '@/nomenclaturesConstants'
import { Validator } from 'vee-validate'
import CompanySelect from '@/components-app/selects/CompanySelect'

const checkSelect = {
  validate(value) {
    return !!value.length
  },
}
Validator.extend('checkSelect', checkSelect)

const INITIAL_FORM_DATA = {
  selectedCompanies: [],
  selectedStatements: [],
  fromDate: '',
  toDate: '',
}

export default {
  name: 'CreateReconciliationForm',
  components: {
    Datepicker,
    CompanySelect,
  },
  mixins: [runAfterSCisSetMixin],
  props: {
    startRCForId: {},
  },
  created() {
    this.runAfterSCisSet(this.loadContacts)
    this.resetForm()
  },
  data() {
    return {
      locales: {
        en: en,
        bg: bg,
      },
      loading: true,
      statementTypes: STATEMENT_TYPES,
      formData: {},
      lastSelectItem: {},
      show: true,
      formSubmittedOnce: false,
    }
  },
  computed: {
    hideFromDate() {
      return this.formData.selectedStatements.length === 1 && this.formData.selectedStatements[0].id === 1
    },
    contacts() {
      return this.$store.getters.contacts
    },
    haveContacts() {
      return !!this.contacts.length
    },
  },
  methods: {
    selectCompany() {
      if (this.startRCForId) {
        this.contacts.forEach((contact) => {
          if (contact.id === this.startRCForId) {
            this.formData.selectedCompanies.push(contact)
          }
        })
      }
    },
    resetForm() {
      this.formData = merge({}, INITIAL_FORM_DATA)
    },
    onSubmit(evt) {
      this.formSubmittedOnce = true
      evt.preventDefault()
      this.$refs.reconciliationCompanySelect.validate().then((selectResult) => {
        this.$validator.validateAll().then((result) => {
          if (result && selectResult) {
            this.$emit('formSubmitted', this.formData)
          }
        })
      })
    },
    reValidateOnChange() {
      if (this.formSubmittedOnce) {
        setTimeout(() => {
          this.$refs.reconciliationCompanySelect.validate()
          this.$validator.validateAll()
        }, 50)
      }
    },
    onReset(evt) {
      evt.preventDefault()
      /* Reset our form values */
      this.resetForm()
      /* Trick to reset/clear native browser form validation state */
      this.show = false
      this.$nextTick(() => { this.show = true })
    },
    loadContacts() {
      this.loadStore('loadSCContacts')
    },
    loadStore(action) {
      this.loading = true
      this.$store.dispatch(action)
        .finally(() => {
          this.loading = false
          this.selectCompany()
        })
    },
  },
}



