import authService from '@/services/authService'

export default {
  data() {
    return {
      loading: false,
      userLanguage: this.$t('lang'),
      options: [
        { value: null, disabled: true, text: 'Please select language' },
        { value: 'EN', text: 'English' },
        { value: 'БГ', text: 'Български' },
      ],
    }
  },
  methods: {
    submit() {
      this.loading = true
      let updateData = [{
        key: 'language',
        value: this.$i18n.locale,
      }]
      authService.updateUserSettings(updateData)
        .then(res => {
          this.$emit('success')
        })
        .catch(errRes => {
          this.errMsg = this.$t(errRes.messageDetail)
        })
        .finally(() => {
          this.loading = false
        })
    },
    languageChange(lang) {
      switch (lang) {
        case 'EN':
          this.$i18n.locale = 'en'
          break
        case 'БГ':
          this.$i18n.locale = 'bg'
          break
        default:
          this.$i18n.locale = 'en'
      }
    },
  },
  watch: {
    '$i18n.locale': function() {
      this.userLanguage = this.$t('lang')
    },
  },
}



