import runAfterSCisSetMixin from '../../../runAfterSCisSetMixin'
import Datepicker from 'vuejs-datepicker'
import {en, bg} from 'vuejs-datepicker/dist/locale'
import CompanyCard from '@/components-app/CompanyCard'
import tableTools from '@/components-lib/table-tools'
import comparators from '@/helpers/comparators'
import tableStoreMixin from '@/components-lib/table-tools/tableStoreMixin'

let tableStore = tableTools.TableStore()

export default {
  name: 'CreateReconciliationTable',
  components: {
    CompanyCard,
    Datepicker,
    InputFilter: tableTools.InputFilter(tableStore),
    SelectFilter: tableTools.SelectFilter(tableStore),
    DateFilter: tableTools.DateFilter(tableStore),
    BulkSelector: tableTools.BulkSelector(tableStore),
    CheckboxSelect: tableTools.CheckboxSelect(tableStore),
    MarkedFilter: tableTools.MarkedFilter(tableStore),
  },
  mixins: [runAfterSCisSetMixin, tableStoreMixin(tableStore)],
  created() {
    this.runAfterSCisSet(this.loadCollaborators)
    this.setTableItems()
    tableStore.commit('setItems', this.tableItems)
  },
  props: {
    formData: {},
  },
  data() {
    return {
      locales: {
        en: en,
        bg: bg,
      },
      loading: false,
      itemsPerPage: 20,
      showFilters: false,
      compare: {
        string: comparators['string'],
        numAsString: comparators['numAsString'],
        number: comparators['number'],
        day: comparators['day'],
      },
      tableItems: [],
      exportData: {
        disclosed: {},
        assignee: {},
        ddl: {},
      },
    }
  },
  computed: {
    collaborators() {
      return this.$store.getters.collaborators
    },
    selectAssigneeOptions() {
      return this.collaborators.map((contact) => {
        return {
          text: contact.name,
          value: contact,
        }
      })
    },
  },
  methods: {
    setTableItems() {
      let items = []
      let id = 0
      this.formData.selectedCompanies.forEach(company => {
        this.formData.selectedStatements.forEach(statement => {
          items.push({
            id: id,
            company: company,
            statement: statement,
            fromDate: this.formData.fromDate,
            toDate: this.formData.toDate,
          })
          id++
        })
      })
      this.tableItems = items
    },
    loadCollaborators() {
      this.loading = true
      this.$store.dispatch('loadSCCollaborators')
        .then(() => {
          this.loading = false
        })
    },
    toggleFilters: function() {
      this.showFilters = !this.showFilters
      if (!this.showFilters) {
        tableStore.commit('removeAllFilters')
      }
    },
    deleteItem(id) {
      this.tableItems = this.tableItems.filter((obj) => {
        return obj.id !== id
      })
      tableStore.commit('setItems', this.tableItems)
    },
    goStepBack() {
      this.$emit('back')
    },
    CreateReconciliations() {
      let data = []
      this.tableItems.forEach(item => {
        data.push({
          id: item.id,
          company: item.company,
          statement: item.statement,
          fromDate: item.fromDate,
          toDate: item.toDate,
          disclosed: this.exportData.disclosed[item.id] || false,
          assignee: this.exportData.assignee[item.id] || null,
          ddl: this.exportData.ddl[item.id] || null,
        })
      })
      this.$store.dispatch('createReconciliation', data)
        .then((response) => {
          this.$customMsg.showSuccessMessage(this.$t('Successfully created ') + response.length + ' ' + this.$t(response.length === 1 ? 'reconciliation' : 'reconciliations'))
        })
        .catch((error) => {
          this.$customMsg.showErrorMessage(this.$t('Error') + ' : ' + error.messageDetail)
        })
        .finally(() => {
          this.$emit('close')
        })
    },
  },
}



