import CompanyCard from '@/components-app/CompanyCard'
import NotificationModal from './NotificationModal/index.vue'

export default {
  components: {
    CompanyCard,
    NotificationModal,
  },
  props: ['companies'],
  methods: {
    showCompanyNotifications(companyId) {
      this.$refs.NotificationModal.showModal(companyId)
    },
    goToUploads(companyId) {
      this.$router.push({ path: `/companies/${companyId}/upload` })
    },
    goToReconciliations(companyId) {
      this.$router.push({ path: `/companies/${companyId}/reconciliations` })
    },
  },
}



