import Vue from 'vue'
import Router from 'vue-router'

import {requireUser} from './guard'

import authRouter from './authRouter'
import companyRouter from './companyRouter'
import prototypeRouter from './prototypeRouter'

// Pages
// import HomePage from '@/pages/HomePage'
import CompaniesPage from '@/pages/CompaniesPage/index'
import AddCompanyPage from '@/pages/AddCompanyPage'
import ReceivedInvitationsPage from '@/pages/ReceivedInvitationsPage'
import MatchingToolPage from '@/pages/MatchingToolPage'
import CompanyPage from '@/pages/company/CompanyPage'
import UserPage from '@/pages/UserPage/index'
import PrototypePage from '@/pages/prototypes/PrototypePage'
import NotificationsPage from '@/pages/Notifications'
import ConfirmAccount from '@/pages/auth/ConfirmAccount'


Vue.use(Router)


export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: CompaniesPage,
      meta: {
        pageId: 'home-page',
        pageClass: '',
      },
      beforeEnter: requireUser,
    },
    {
      path: '/ConfirmAccount',
      name: 'ConfirmAccount',
      component: ConfirmAccount,
      meta: {
        pageId: 'confirm-account-page',
        pageClass: '',
      },
    },
    ...authRouter,
    {
      path: '/user',
      name: 'UserPage',
      component: UserPage,
      meta: {
        pageId: 'user-page',
        pageClass: '',
      },
      beforeEnter: requireUser,
    },
    // {
    //   path: '/companies/:id/upload/invalid-tx',
    //   name: 'InvalidTxTP',
    //   component: InvalidTxTP,
    //   props: true,
    //   meta: {
    //     pageId: 'invalid-tx-tp',
    //     pageClass: 'tp',
    //   },
    //   beforeEnter: requireUser,
    // },
    {
      path: '/companies/:id',
      name: 'CompanyPage',
      component: CompanyPage,
      props: true,
      redirect: '/companies/:id/profile',
      meta: {
        pageId: 'company-page',
        pageClass: '',
      },
      beforeEnter: requireUser,
      children: companyRouter,
    },
    {
      path: '/add-company',
      name: 'AddCompany',
      component: AddCompanyPage,
      meta: {
        pageId: 'add-company-page',
        pageClass: '',
      },
      beforeEnter: requireUser,
    },
    {
      path: '/received-invitations',
      name: 'ReceivedInvitations',
      component: ReceivedInvitationsPage,
      meta: {
        pageId: 'received-invitations-page',
        pageClass: '',
      },
      beforeEnter: requireUser,
    },
    {
      path: '/notifications/:type',
      name: 'Notifications',
      props: true,
      component: NotificationsPage,
      meta: {
        pageId: 'notifications-page',
        pageClass: '',
      },
      beforeEnter: requireUser,
    },
    {
      path: '/matching-tool',
      name: 'MatchingTool',
      component: MatchingToolPage,
      meta: {
        pageId: 'matching-tool-page',
        pageClass: '',
      },
      beforeEnter: requireUser,
    },
    {
      path: '/prototype',
      name: 'Prototype',
      component: PrototypePage,
      redirect: '/prototype/company',
      meta: {
        pageId: '',
        pageClass: 'prototype-page',
      },
      beforeEnter: requireUser,
      children: prototypeRouter,
    },
    {
      path: '*',
      redirect: '/',
    },
  ],
  mode: 'history',
  linkExactActiveClass: 'active',
})



