import AutoFxSettings from './AutoFxSettings/'
import FxRates from './FxRates/'

export default {
  name: 'FxPage',
  components: {
    FxRates,
    AutoFxSettings,
  },
  data() {
    return {
      activeTab: 0,
      selectedDate: new Date(Date.now()),
    }
  },
  methods: {
    onTabChange(index) {
      this.activeTab = index
    },
    onDateChange(date) {
      this.selectedDate = date
    },
  },
}



