function apiToUserNotification(data) {
  return data
}

function companyNotificationsToApi(data) {
  return {
    id: data.companyId,
    data: 'currentTime=' + data.currentTime + '&startIndex=' + data.startIndex + '&endIndex=' + data.endIndex,
  }
}

function userNotificationsToApi(data) {
  return 'currentTime=' + data.currentTime + '&startIndex=' + data.startIndex + '&endIndex=' + data.endIndex
}

export {
  apiToUserNotification,
  userNotificationsToApi,
  companyNotificationsToApi,
}



