import recordsService from '@/services/recordsService'

const INITIAL_STATE = {
  currentRecords: [],
  recordCompanyList: [],
}

var state = {...INITIAL_STATE}

var getters = {
  getCurrentRecords(state) {
    return state.currentRecords
  },
  getRecordCompanyList(state) {
    return state.recordCompanyList
  },
}

const mutations = {
  setCurrentRecords(state, payload) { state.currentRecords = payload },
  setRecordCompanyList(state, payload) { state.recordCompanyList = payload },
}

function thenHandler(commit, commitName) {
  return function(res) {
    commit(commitName, res)
    return res
  }
}

const actions = {
  resetRecords({state}) {
    Object.assign(state, INITIAL_STATE)
  },
  loadCurrentRecords({ commit, state }, payload) {
    return recordsService.getCurrentRecords(payload, state.selectedCompany)
      .then(thenHandler(commit, 'setCurrentRecords'))
  },
  loadRecordCompanyList({ commit, state }, payload) {
    return recordsService.getRecordCompanyList(payload, state.selectedCompany)
      .then(thenHandler(commit, 'setRecordCompanyList'))
  },
}

export default {
  state,
  getters,
  mutations,
  actions,
}



