<template>
  <b-dropdown size="sm" class="m-2" ref="dropdown" :text="selection">
    <b-dropdown-item v-for="option in options" :key="option.label" >
      <a role="button" @click="select(option)">{{ option.label }}</a>
    </b-dropdown-item>
  </b-dropdown>
</template>


<script>
var key = 'marked' + Date.now()

var vueComponentOptions = {}

var vueComponentFn = function(store) {
  vueComponentOptions.data = function() {
    var self = this
    var options = [
      {
        label: 'All',
        fn() {
          self.removeFilter()
        },
      },
      {
        label: 'Selected',
        fn() {
          self.filter(true)
        },
      },
      {
        label: 'Not selected',
        fn() {
          self.filter(false)
        },
      },
    ]

    return {
      selection: options[0].label,
      options,
    }
  }

  vueComponentOptions.methods = {
    select(option) {
      option.fn()
      this.selection = option.label
    },
    filter(value) {
      var comparatorFn = (function(fValue) {
        return function(item) {
          return store.getters.isItemSelected(item) === fValue
        }
      })(value)

      const payload = {
        key: key,
        comparator: comparatorFn,
      }
      store.commit('addFilter', payload)
    },
    removeFilter() {
      store.commit('removeFilter', key)
    },
  }

  return vueComponentOptions
}

vueComponentFn.options = vueComponentOptions

export default vueComponentFn

</script>



