import tableTools from '@/components-lib/table-tools'
import comparators from '@/helpers/comparators'
import tableStoreMixin from '@/components-lib/table-tools/tableStoreMixin'
import EditInput from './EditInput/'

var vueComponentOptions = {}

var vueComponentFn = function(tableStore) {
  vueComponentOptions.name = 'ExceptionsTable'

  vueComponentOptions.mixins = [tableStoreMixin(tableStore)]
  vueComponentOptions.directives = {
    Sort: tableTools.SortDirective(tableStore),
  }

  vueComponentOptions.components = {
    InputFilter: tableTools.InputFilter(tableStore),
    SelectFilter: tableTools.SelectFilter(tableStore),
    DateFilter: tableTools.DateFilter(tableStore),
    BulkSelector: tableTools.BulkSelector(tableStore),
    CheckboxSelect: tableTools.CheckboxSelect(tableStore),
    MarkedFilter: tableTools.MarkedFilter(tableStore),
    EditInput: EditInput,
  }

  vueComponentOptions.data = function() {
    return {
      itemsPerPage: 10,
      showFilters: false,
      loading: false,
      compare: {
        string: comparators['string'],
        numAsString: comparators['numAsString'],
        number: comparators['number'],
        day: comparators['day'],
      },
    }
  }

  vueComponentOptions.created = function() {
    var self = this
    self.loading = true
    self.$store.dispatch('loadCurrencies')
      .finally(() => {
        self.loading = false
      })
  }

  vueComponentOptions.computed = {
    allItems() {
      return this.$store.getters.exceptions
    },
    selectedCompany() {
      return this.$store.getters.selectedCompany
    },
    isBalanceSheet() {
      return this.$store.getters.selectedReconciliation.statement.id === 1
    },
  }

  vueComponentOptions.methods = {
    deleteSelected() {
      var self = this
      if (confirm('Are you shure?') === true) {
        var selectedExceptions = tableStore.getters.selectedItems
        self.loading = true
        self.$store.dispatch('deleteExceptions', selectedExceptions)
          .then(() => {
            self.showSuccess()
          })
          .catch(() => {
            self.showError()
          })
          .finally(() => {
            self.loading = false
          })
      }
    },
    toggleFilters() {
      this.showFilters = !this.showFilters
      if (!this.showFilters) {
        tableStore.commit('removeAllFilters')
      }
    },
    setFxRate(event, newValue, record) {
      var self = this
      self.loading = true
      var payload = {
        rate: newValue,
        record: record,
      }
      self.$store.dispatch('setRecordFxRate', payload)
        .then(() => {
          self.showSuccess()
          return self.$store.dispatch('reloadExceptions')
        })
        .then((exceptions) => {
          tableStore.commit('setItems', exceptions)
        })
        .catch((error) => {
          self.showError()
          return error
        })
        .finally(() => {
          self.loading = false
        })
    },
  }

  return vueComponentOptions
}

vueComponentFn.options = vueComponentOptions

export default vueComponentFn



