import ReceivedInvitationsTable from './ReceivedInvitationsTable/index'

export default {
  components: {
    ReceivedInvitationsTable,
  },
  created() {
    this.loadData()
  },
  data() {
    return {
      loading: false,
    }
  },
  computed: {
    receivedInvitations() {
      return this.$store.getters.receivedInvitations
    },
  },
  methods: {
    goToHome() {
      this.$router.push('Home')
    },
    loadData() {
      this.loading = true
      this.$store.dispatch('loadReceivedInvitations')
        .then(() => {
          this.$store.dispatch('loadMyCompanies')
            .finally(() => {
              this.loading = false
            })
        })
    },
  },
}



