import Datepicker from 'vuejs-datepicker'
import {en, bg} from 'vuejs-datepicker/dist/locale'
import {FX_RATES_TYPES} from '@/nomenclaturesConstants'
import { ModelSelect } from 'vue-search-select'

export default {
  components: {
    ModelSelect,
    Datepicker,
  },
  props: {
    averageFxRates: {},
  },
  data() {
    return {
      locales: {
        en: en,
        bg: bg,
      },
      formError: '',
      formData: {
        currencyId: null,
        recordType: this.averageFxRates ? 2 : 1,
      },
      convertedCurrencies: [],
    }
  },
  mounted() {
    this.$store.dispatch('loadCurrencies')
      .then(loadedItems => this.currencyToOptions(loadedItems))
  },
  computed: {
    formRecordTypeDaily() {
      return this.formData.recordType === 1
    },
    fxRecordsTypes() {
      let types = []
      Object.keys(FX_RATES_TYPES).map((key) => {
        types.push(FX_RATES_TYPES[key])
      })
      return types
    },
    formHasError() {
      return !!this.formError.length
    },
  },
  methods: {
    currencyToOptions(input) {
      let output = []
      input.forEach(currency => {
        output.push({
          value: currency.id,
          text: currency.code,
        })
      })
      this.convertedCurrencies = output
    },
    resetForm() {
      this.formError = ''
      this.formData = {
        currencyId: null,
        recordType: 1,
      }
      this.updateParent()
    },
    showError(error) {
      this.formError = error.messageDetail
    },
    updateParent() {
      this.$emit('updateFormData', this.formData)
    },
  },
}



