import Datepicker from 'vuejs-datepicker'
import {en, bg} from 'vuejs-datepicker/dist/locale'
import merge from 'lodash.merge'
import {STATEMENT_TYPES} from '@/nomenclaturesConstants'
import CompanySelect from '@/components-app/selects/CompanySelect'

const vueComponentConfig = {
  components: {
    Datepicker,
    CompanySelect,
  },
  data() {
    return {
      locales: {
        en: en,
        bg: bg,
      },
      initialDataFilled: false,
      formData: {},
      statementTypes: STATEMENT_TYPES,
    }
  },
  computed: {
    CompanyList() { return this.$store.getters.getRecordCompanyList },
    CompanyListLength() { return this.CompanyList.length },
  },
  methods: {
    loadCompanyList() {
      this.$store.dispatch('loadRecordCompanyList', this.formData)
    },
    reValidateOnChange() {
      this.initialDataFilled = this.formData.statementType && this.formData.toDate && (this.formData.statementType.id === 1 || this.formData.fromDate)

      if (this.initialDataFilled) {
        this.checkToDate()
        setTimeout(() => {
          this.$validator.validateAll().then((result) => {
            if (result) {
              this.loadCompanyList()
            }
          })
        }, 100)
      }
    },
    checkToDate() {
      if (this.formData.statementType && this.formData.statementType.id === 1) {
        return true
      } else {
        this.$validator.errors.remove('toDate')
        if (Date.parse(this.formData.fromDate) > Date.parse(this.formData.toDate)) {
          this.$validator.errors.add({
            field: 'toDate',
            msg: this.$t('To(date) must be greater than From(date)'),
          })
        }
        if (!this.$validator.validate('toDate')) {
          this.$validator.errors.add({
            field: 'toDate',
            msg: this.$t('Please, select a date'),
          })
        }
      }
    },
    validateBeforeSubmit() {
      this.checkToDate()
      this.initialDataFilled = true
      this.$refs.recordsCompanySelect.validate()
      this.$validator.validateAll().then((result) => {
        this.$refs.recordsCompanySelect.validate().then((selectResult) => {
          if (result && selectResult) {
            this.loading = true
            this.$emit('success', this.formData)
          }
        })
      })
    },
  },
}

export default merge({}, vueComponentConfig)



