export default {
  props: {
    inputValue: {
      required: true,
      type: [String, Number],
    },
    placeholder: {},
    onSubmit: {
      required: true,
      type: Function,
    },
    formatter: {
      type: Function,
      default(value) {
        return value
      },
    },
    editMode: {
      default: false,
    },
    id: {},
  },
  data() {
    return {
      edit: this.editMode,
    }
  },
  computed: {
    showValue() {
      return this.formatter(this.inputValue) // Here you can apply | filters : )
    },
    modelValue() {
      return this.inputValue
    },
  },
  methods: {
    submit(event) {
      var self = this
      var value = event.target.elements['eInput'].value

      if (this.inputValue === value) {
        self.edit = false
        return
      }

      var result = self.onSubmit(event, value, self.id)

      Promise.resolve(result)
        .then(() => {
          self.edit = false
          return true
        })
        .catch((error) => {
          self.edit = true
          return error
        })
    },
    cancel() {
      this.edit = false
    },
  },
}



