import {requireUser} from './guard'

// Pages
// import SettingsPage from '@/pages/company/SettingsPage'
import FxPage from '@/pages/company/FxPage'
import UploadPage from '@/pages/company/UploadPage/index'
import ContactsPage from '@/pages/company/ContactsPage/index'
import CollaboratorsPage from '@/pages/company/CollaboratorsPage/index'
import ProfilePage from '@/pages/company/ProfilePage/index'
import RecordsPage from '@/pages/company/RecordsPage/index'
import ReconciliationsPage from '@/pages/company/ReconciliationsPage'
import ReconciliationPage from '@/pages/company/ReconciliationsPage/ReconciliationPage'


export default [
  {
    path: 'reconciliations',
    name: 'Reconciliations',
    component: ReconciliationsPage,
    meta: {
      pageId: 'reconciliations-page',
      pageClass: '',
    },
    beforeEnter: requireUser,
  },
  {
    path: 'reconciliation/:recId',
    name: 'Reconciliation',
    component: ReconciliationPage,
    props: true,
    meta: {
      pageId: 'reconciliation-page',
      pageClass: '',
    },
    beforeEnter: requireUser,
  },
  {
    path: 'upload',
    name: 'Upload',
    component: UploadPage,
    meta: {
      pageId: 'upload-page',
      pageClass: '',
    },
    beforeEnter: requireUser,
  },
  {
    path: 'contacts',
    name: 'Contacts',
    component: ContactsPage,
    meta: {
      pageId: 'contacts-page',
      pageClass: '',
    },
    beforeEnter: requireUser,
  },
  {
    path: 'fx',
    name: 'FX',
    component: FxPage,
    meta: {
      pageId: 'fx-page',
      pageClass: '',
    },
    beforeEnter: requireUser,
  },
  // {
  //   path: 'settings',
  //   name: 'Settings',
  //   component: SettingsPage,
  //   meta: {
  //     pageId: 'settings-page',
  //     pageClass: '',
  //   },
  //   beforeEnter: requireUser,
  // },
  {
    path: 'profile',
    name: 'CompanyProfile',
    component: ProfilePage,
    meta: {
      pageId: 'profile-page',
      pageClass: '',
    },
    beforeEnter: requireUser,
  },
  {
    path: 'collaborators',
    name: 'Collaborators',
    component: CollaboratorsPage,
    meta: {
      pageId: 'collaborators-page',
      pageClass: '',
    },
    beforeEnter: requireUser,
  },
  {
    path: 'records',
    name: 'Records',
    component: RecordsPage,
    meta: {
      pageId: 'records-page',
      pageClass: '',
    },
    beforeEnter: requireUser,
  },
]



