import axios from 'axios'
import {apiToImportReceipt, apiToSummary, apiToRecord, excelImportToApi} from './converters/importConverters'

const IMPORTS_API = '/Imports/'


export default {
  importExcel(company, formData) {
    formData.company = company
    const postData = excelImportToApi(formData)

    return axios.post(IMPORTS_API + 'ExcelTransactions', postData)
      .then(apiToImportReceipt)
  },
  saveValidRecords(importReceipt) {
    return axios.post(IMPORTS_API + importReceipt.id + '/ValidRecords')
  },
  removeValidSet(importReceipt, set) {
    return axios.delete(IMPORTS_API + importReceipt.id + '/RefCompanies/' + set.company.id + '/Currencies/' + set.currency.id)
  },
  removeValidRecord(importReceipt, record) {
    return axios.delete(IMPORTS_API + importReceipt.id + '/RowIndex/' + record.line)
  },
  getSummary(importReceipt) {
    return axios.get(IMPORTS_API + importReceipt.id + '/Records/Summary')
      .then(apiToSummary)
  },
  getRecognizedCompanyRecords(importReceipt, set) {
    return axios.get(IMPORTS_API + importReceipt.id + '/ValidRecords/RefCompanies/' + set.company.id + '/Currencies/' + set.currency.id)
      .then(arr => arr.map(apiToRecord))
  },
  getUnrecognizedCompanyRecords(importReceipt, set) {
    return axios.get(IMPORTS_API + importReceipt.id + '/ValidRecords/UnknownRefCompanies/' + set.company.name)
      .then(arr => arr.map(apiToRecord))
  },
  getInvalidRecords(importReceipt, set) {
    return axios.get(IMPORTS_API + importReceipt.id + '/InvalidRecords')
      .then(arr => arr.map(apiToRecord))
  },
  getOutOfScopeRecords(importReceipt, set) {
    return axios.get(IMPORTS_API + importReceipt.id + '/OutOfScopeRecords')
      .then(arr => arr.map(apiToRecord))
  },
}



