import reconciliationsService from '@/services/reconciliationsService'

const INITIAL_STATE = {
  selectedReconciliation: null,
  selectedReconciliationSummary: [],
  OpenReconciliations: [],
  FinishedReconciliations: [],
  RejectedReconciliations: [],
}

var state = {...INITIAL_STATE}

var getters = {}

// Generate getters for constants
Object.keys(state).reduce((obj, key) => {
  obj[key] = (state) => state[key]
  return obj
}, getters)

const mutations = {
  setSelectedReconciliation(state, payload) {
    state.selectedReconciliation = payload
  },
  setSelectedReconciliationSummary(state, payload) {
    state.selectedReconciliationSummary = payload
  },
  setOpenReconciliations(state, payload) {
    state.OpenReconciliations = payload
  },
  setFinishedReconciliations(state, payload) {
    state.FinishedReconciliations = payload
  },
  setRejectedReconciliations(state, payload) {
    state.RejectedReconciliations = payload
  },
}

function thenHandler(commit, commitName) {
  return function(res) {
    commit(commitName, res)
    return res
  }
}

const actions = {
  resetReconciliations({state}) {
    Object.assign(state, INITIAL_STATE)
  },
  createReconciliation({ commit, state }, payload) {
    return reconciliationsService.createReconciliation(payload, state.selectedCompany)
    // .then(thenHandler(commit, 'addCompany'))
  },

  loadSelReconciliation({ commit, state }, payload) {
    if (state.selectedReconciliation && state.selectedReconciliation.id === payload) {
      return Promise.resolve(state.selectedReconciliation)
    }
    return reconciliationsService.getReconciliation(payload, state.selectedCompany)
      .then(thenHandler(commit, 'setSelectedReconciliation'))
  },

  editSelReconciliationStatus({ commit, state }, payload) {
    return reconciliationsService.editSelReconciliationStatus(payload, state.selectedCompany)
  },

  loadSelReconciliationSummary({ commit, state }, payload) {
    return reconciliationsService.getReconciliationSummary(payload, state.selectedCompany)
      .then(thenHandler(commit, 'setSelectedReconciliationSummary'))
  },

  loadOpenReconciliations({ commit, state }, payload) {
    return reconciliationsService.getOpenReconciliations(payload, state.selectedCompany)
      .then(thenHandler(commit, 'setOpenReconciliations'))
  },
  loadFinishedReconciliations({ commit, state }, payload) {
    return reconciliationsService.getFinishedReconciliations(payload, state.selectedCompany)
      .then(thenHandler(commit, 'setFinishedReconciliations'))
  },
  loadRejectedReconciliations({ commit, state }, payload) {
    return reconciliationsService.getRejectedReconciliations(payload, state.selectedCompany)
      .then(thenHandler(commit, 'setRejectedReconciliations'))
  },
  deleteReconciliation({ commit }, payload) {
    return reconciliationsService.deleteReconciliation(payload)
  },
  deleteMultipleReconciliation({ commit }, payload) {
    return reconciliationsService.deleteMultipleReconciliation(payload)
  },
}

export default {
  state,
  getters,
  mutations,
  actions,
}



