import debounce from 'lodash.debounce'

export default function(store) {
  return {
    computed: {
      pageItems() {
        return store.getters.pageItems(this.itemsPerPage)
      },
      totalRows() {
        return store.getters.totalRows()
      },
      pages() {
        return store.getters.totalPages(this.itemsPerPage)
      },
      page: {
        get: () => {
          return store.getters.page()
        },
        set: (pageNumber) => {
          store.mutations.setPage(pageNumber)
        },
      },
    },
    methods: {
      setItemsPerPage: debounce(function(event) {
        var count = +event.target.value
        store.mutations.setPage(1)
        if (!isNaN(count) && count > 0) {
          this.itemsPerPage = count
        } else if (this.itemsPerPage === 10 && count === 0) {
          // Edge case: itemsPerPage is already 10, but the desplayed value is deleted. Just setting the itemsPerPage again to 10 won't trigger rerendering of the text field, so we need to trigger it manually.
          this.itemsPerPage = 1
          this.itemsPerPage = 10
        } else {
          this.itemsPerPage = 10
        }
      }, 300),
    },
  }
}



