import user from './userStore'
import invitation from './invitationStore'


const state = {
  ...user.state,
  ...invitation.state,
}

const getters = {
  ...user.getters,
  ...invitation.getters,
}

const mutations = {
  ...user.mutations,
  ...invitation.mutations,
}

const actions = {
  ...user.actions,
  ...invitation.actions,
}


export default {
  state,
  getters,
  mutations,
  actions,
}



