import Multiselect from 'vue-multiselect'
import LogoFlag from '@/components-app/LogoFlag'

export default {
  components: {
    Multiselect,
    LogoFlag,
  },
  data() {
    return {
      loading: false,
    }
  },
  props: {
    currentCompanyId: {},
    allowNone: {
      type: Boolean,
      default: false,
    },
    noneLabel: {
      type: String,
    },
    companies: {},
    disabled: {
      type: Boolean,
      default: false,
    },
    value: {},
    multiple: {
      type: Boolean,
      default: false,
    },
    componentClass: {},
    componentLabel: {},
  },
  computed: {
    internalCompanies() {
      let temp = []

      if ((this.currentCompanyId === undefined || this.currentCompanyId === null) && !this.allowNone) {
        return this.companies
      }

      if (this.allowNone) {
        temp.push({
          id: -1,
          name: '',
        })
      }

      this.companies.forEach((company) => {
        if (this.currentCompanyId !== company.id) {
          temp.push(company)
        }
      })
      return temp
    },
    internalValue: {
      get() {
        return this.value
      },
      set(newValue) {
        this.$emit('input', newValue)
      },
    },
  },
  methods: {
    companyLabel(option) {
      let aliases = ''
      if (!option.aliases) { return `${option.name}` }
      if (option.aliases.length === 0) { return `${option.name}` }
      option.aliases.forEach((alias) => {
        aliases += alias.aname + ', '
      })
      aliases = aliases.slice(0, -2)
      return `${option.name}( ${aliases} )`
    },
    validate() {
      return this.$validator.validateAll()
    },
    sendChange() {
      this.$emit('change')
    },
  },
  watch: {
    companies: {
      handler(value) {
        if (value && !value.length) {
          this.internalValue = this.multiple ? {} : []
        }
      },
      deep: true,
    },
  },
}



