import Multiselect from 'vue-multiselect'

export default {
  components: {
    Multiselect,
  },
  data() {
    return {
      toggle: false,
      errMsg: null,
      formData: {
        date: '',
        months: [],
        output: '',
        id: null,
      },
      options: [
        { name: '1st', value: 1 },
        { name: '2nd', value: 2 },
        { name: '3rd', value: 3 },
        { name: '4th', value: 4 },
        { name: '5th', value: 5 },
        { name: '6th', value: 6 },
        { name: '7th', value: 7 },
        { name: '8th', value: 8 },
        { name: '9th', value: 9 },
        { name: '10th', value: 10 },
        { name: '11th', value: 11 },
        { name: '12th', value: 12 },
        { name: '13th', value: 13 },
        { name: '14th', value: 14 },
        { name: '15th', value: 15 },
        { name: '16th', value: 16 },
        { name: '17th', value: 17 },
        { name: '18th', value: 18 },
        { name: '19th', value: 19 },
        { name: '20th', value: 20 },
        { name: '21th', value: 21 },
        { name: '22th', value: 22 },
        { name: '23th', value: 23 },
        { name: '24th', value: 24 },
        { name: '25th', value: 25 },
        { name: '26th', value: 26 },
        { name: '27th', value: 27 },
        { name: '28th', value: 28 },
        { name: '29th', value: 29 },
        { name: '30th', value: 30 },
        { name: '31th', value: 31 },
        { name: 'Last day', value: 0 },
      ],
      months: [
        { name: 'January', value: 'Jan' },
        { name: 'February', value: 'Feb', mark: true },
        { name: 'March', value: 'Mar' },
        { name: 'April', value: 'Apr', mark: true },
        { name: 'May', value: 'May' },
        { name: 'June', value: 'Jun', mark: true },
        { name: 'July', value: 'Jul' },
        { name: 'August', value: 'Aug' },
        { name: 'September', value: 'Sep', mark: true },
        { name: 'October', value: 'Oct' },
        { name: 'November', value: 'Nov', mark: true },
        { name: 'December', value: 'Dec' },
      ],
    }
  },
  props: {
    disabled: {
      type: Boolean,
      default: false,
    },
    value: {},
    singleMonth: {
      type: Boolean,
      default: false,
    },
  },
  created() {
    this.formData = this.objectToForm(this.value)
  },
  computed: {
    output() {
      let string = ''
      string += this.formData.date ? 'Every ' + this.formData.date.name + ' of ' : 'No date selected'
      if (this.formData.date && this.formData.months && this.formData.months.length) {
        this.formData.months.forEach((month) => {
          string += month.name + ', '
        })
        string = string.slice(0, -2)
      } else if (this.singleMonth && this.formData.date) {
        string += this.formData.months.name
      }
      return string
    },
  },
  methods: {
    confirm(evt) {
      evt.preventDefault()
      this.$validator.validateAll()
        .then(result => {
          if (result) {
            return Promise.resolve()
          }
          return Promise.reject(new Error('Please, fill the required fields.'))
        })
        .then(() => {
          this.toggle = false
          this.formData.output = this.formToObject(this.formData)
          this.$emit('input', this.formData.output)
        })
        .catch(() => {
          // TODO Show Error or something
        })
    },
    objectToForm(obj) {
      let output = {}
      output.months = []
      if (obj) {
        if (!this.singleMonth) {
          obj.months.split('').forEach((flag, index) => {
            if (Number(flag)) {
              output.months.push(this.months[index])
            }
          })
        } else {
          obj.months.split('').forEach((flag, index) => {
            if (Number(flag)) {
              output.months = this.months[index]
            }
          })
        }
        output.date = (obj.dayOfMonth === 0) ? this.options[31] : this.options[obj.dayOfMonth - 1]
        output.id = obj.id
        output.companyId = obj.companyId
      }
      return output
    },
    formToObject(formData) {
      let output = {}
      output.id = formData.id
      output.companyId = formData.companyId
      output.dayOfMonth = formData.date.value
      output.months = ''
      this.months.forEach(month => {
        if (!this.singleMonth) {
          output.months += (formData.months.filter((m) => {
            return m.name === month.name
          }).length) ? '1' : '0'
        } else {
          output.months += formData.months.name === month.name ? '1' : '0'
        }
      })
      return output
    },
    open() {
      this.toggle = true
    },
    close() {
      this.toggle = false
      this.$emit('close')
    },
  },
  watch: {
    formData: {
      handler(value) {
        let disable = false
        if (value.date && value.date.value === 31) {
          disable = true
        }
        this.months.forEach((month) => {
          month.disabled = month.mark && disable
        })
        if (value.date && (value.date.value === 30 || value.date.value === 29)) {
          disable = true
          this.months[1].disabled = true
        }
        if (disable) {
          if (!this.singleMonth) {
            value.months.forEach((current, index) => {
              if (current.disabled) {
                value.months.splice(index, 1)
              }
            })
          } else {
            value.months = value.months.disabled ? this.months[0] : value.months
          }
        }
      },
      deep: true,
    },
    value: {
      handler(val) {
        this.formData = this.objectToForm(val)
      },
      deep: true,
    },
  },
}



