function compareDays(dateA, dateB) {
  return compareMonths(dateA, dateB) && dateA.getDate() === dateB.getDate()
}

function compareMonths(dateA, dateB) {
  return compareYears(dateA, dateB) && dateA.getMonth() === dateB.getMonth()
}

function compareYears(dateA, dateB) {
  return dateA.getFullYear() === dateB.getFullYear()
}

function searchStringInString(stringA, stringB) {
  return stringA.toLowerCase().indexOf(stringB.toLowerCase()) > -1
}

function matchExactString(stringA, stringB) {
  return stringA === stringB
}

export default {
  number(numA, numB) {
    return +numA === +numB
  },
  // When comparing dates in ms it can be tricky if the dates have time different then 00:00:00:000. If they have, then same dates will give different results.
  modifNumber(numA, numB, mod) {
    // eslint-disable-next-line no-eval
    return eval('+numA' + mod + '+numB')
  },
  rangeNumber(num, begin, end) {
    // TODO: Shoud = be hardcoded??
    return begin <= num && num <= end
  },
  string(stringA, stringB) {
    return stringA && searchStringInString(stringA, stringB)
  },
  exactString(stringA, stringB) {
    return stringA && matchExactString(stringA, stringB)
  },
  numAsString(num, string) {
    return num !== null && searchStringInString('' + num, string)
  },
  day(dateA, dateB) {
    return compareDays(dateA, dateB)
  },
  month(dateA, dateB) {
    return compareMonths(dateA, dateB)
  },
  year(dateA, dateB) {
    return compareYears(dateA, dateB)
  },
}



