import FullPageModal from '@/components-app/modals/FullPageModal/index'
import ModalHead from './ImportFPMHead'


export default {
  props: ['value'],
  components: {
    FullPageModal,
    ModalHead,
  },
  data() {
    return {
      contractorName: '',
      loading: false,
    }
  },
  watch: {
    value(newSet) {
      if (newSet) {
        this.$refs.modal.open()
        this.loading = true
        this.load(newSet)
          .finally(() => {
            this.loading = false
          })
      }
    },
  },
  methods: {
    close() {
      if (this.showFilters) { this.showFilters = false }
      this.$emit('input', null)
    },
  },
}



