import UserProfileForm from './UserProfileForm'
import UserSettings from './UserSettings'
import PasswordChangeForm from './PasswordChangeForm'

export default {
  components: {
    UserProfileForm,
    UserSettings,
    PasswordChangeForm,
  },
  data() {
    return {
      tabIndex: 0,
    }
  },
  created() {
    this.switchTab()
  },
  methods: {
    changeRoute(index) {
      switch (index) {
        case 0:
          this.$router.push({hash: '#profile'})
          break
        case 1:
          this.$router.push({hash: '#settings'})
          break
        case 2:
          this.$router.push({hash: '#password'})
          break
      }
    },
    switchTab() {
      switch (this.$route.hash) {
        case '#profile':
          this.tabIndex = 0
          break
        case '#settings':
          this.tabIndex = 1
          break
        case '#password':
          this.tabIndex = 2
          break
        default:
          this.tabIndex = 0
          break
      }
    },
    reset() {
      this.tabIndex = 0
    },
  },
  watch: {
    '$route'() {
      this.switchTab()
    },
  },
}



