<template>
  <form @submit.prevent="submit" class="my-company-form">

    <div class="alert alert-danger text-center" v-if="errMsg">
      {{ errMsg }}
    </div>

    <div class="image-preview" v-if="logo.image && logo.image.length > 0">
      <img class="preview" :src="logo.image" width="200">
    </div>

    <div class="form-group">
      <label for="name">{{$t('Company Name')}}:</label>
      <input type="text"
             class="form-control"
             id="name"
             name="name"
             v-validate="'required'"
             :class="[{ 'is-invalid': errors.has('name')}]"
             v-model="formData.name"
             :disabled="!editMode" />
      <!--<small class="form-text text-muted">{{ $t("Please enter the complete legal name as per registration") }}.</small>-->
    </div>

    <div class="form-group">
      <label for="regId">{{$t('Registration ID')}}:</label>
      <input type="text"
             class="form-control"
             id="regId"
             v-model="formData.regNum"
             :disabled="!editMode" />
    </div>

    <CountrySelect
      ref="myCompanyCountrySelect"
      v-model="formData.country"
      :disabled="!editMode"
      componentClass="form-group"
    />

    <div class="form-group">
      <label for="currency">{{$t('Reporting Currency')}}:</label>
      <multiselect
        id="currency"
        name="currency"
        v-validate="'required'"
        :class="[{ 'is-invalid': errors.has('currency')}]"
        v-model="formData.currency"
        :plain="true"
        :disabled="!editMode"
        :options="currencies"
        :custom-label="currenciesLabel"
      >
      </multiselect>
    </div>

    <div class="form-group">
      <label for="address">{{$t('Address')}}:</label>
      <input type="text"
             class="form-control"
             id="address"
             v-model="formData.address"
             :disabled="!editMode" />
    </div>

    <CompanySelect
      v-model="formData.parent"
      :companies="myCompanies"
      :disabled="!editMode"
      componentClass="form-group"
      :componentLabel="$t('Parent company') + ':'"
      :currentCompanyId="currentCompanyId"
      :allowNone="true"
      :noneLabel="$t('No parent')"
    />

    <div class="form-group" v-if="formData.parent">
      <label>{{$t('From date')}}:</label>
      <Datepicker
        v-model="formData.fromDate"
        name="fromDate"
        :bootstrap-styling="true"
        :language="locales[$i18n.locale]"
        :calendar-button="true"
        calendar-button-icon="fa fa-calendar"
        v-validate="formData.parent ? 'required' : '' "
      >
      </Datepicker>
      <div class="text-danger" v-if="errors.has('fromDate')">{{ $t("Please, select a starting date") }}.</div>
    </div>

    <div class="text-center py-3" v-if="editMode">
      <button type="submit" class="btn btn-primary">{{$t('Save')}}</button>
      <button type="button" class="btn btn-light" @click="cancelEdit">{{$t('Cancel')}}</button>
    </div>

    <div class="text-center py-3" v-if="!editMode">
      <button type="button" class="btn btn-info" @click="edit">{{$t('Edit')}}</button>
    </div>

    <loading :loading="loading"></loading>

  </form>
</template>


<script>
import Multiselect from 'vue-multiselect'
import CountrySelect from '@/components-app/selects/CountrySelect'
import CompanySelect from '@/components-app/selects/CompanySelect'
import Datepicker from 'vuejs-datepicker'
import {bg, en} from 'vuejs-datepicker/dist/locale'

const INITIAL_COMPANY_DATA = {
  name: null,
  regNum: null,
  country: null,
  currency: null,
  address: null,
  parent: null,
  logo: null,
}

// TODO: check user's role
// if he is not allowed to change parent

export default {
  components: {
    CompanySelect,
    Multiselect,
    CountrySelect,
    Datepicker,
  },
  created() {
    this.loading = true

    var currenciesPromise = this.$store.dispatch('loadCurrencies')
    var myCompaniesPromise = this.$store.dispatch('loadMyCompanies')

    Promise.all([currenciesPromise, myCompaniesPromise])
      .finally(() => {
        this.loading = false
      })
  },
  props: {
    company: {
      default() {
        return INITIAL_COMPANY_DATA
      },
    },
    logo: {
      default() {
        return {
          file: null,
          formData: null,
        }
      },
    },
  },
  data() {
    return {
      formData: Object.assign({}, this.company),
      editMode: !this.company.name,
      errMsg: null,
      loading: false,
      locales: {
        en: en,
        bg: bg,
      },
    }
  },
  computed: {
    currentCompanyId() {
      return this.company.id ? this.company.id : null
    },
    currencies() {
      return this.$store.getters.currencies ? this.$store.getters.currencies : []
    },
    myCompanies() {
      return this.$store.getters.myCompanies ? this.$store.getters.myCompanies : []
    },
  },
  methods: {
    currenciesLabel(option) {
      return `${option.code} - ${option.name}`
    },
    validate() {
      this.$refs.myCompanyCountrySelect.validate()
      return this.$validator.validateAll()
    },
    submit() {
      var self = this
      let textForMessage = ''
      self.loading = true
      self.validate()
        .then(result => {
          if (result) {
            const action = self.company.name ? 'updateSelCompany' : 'createCompany'
            textForMessage = self.company.name ? 'updated' : 'created'
            if (!self.company.name) {
              self.formData.logoData = self.logo.formData
            }
            return self.$store.dispatch(action, self.formData)
          }
          return Promise.reject(new Error('Please, fill the required fields.'))
        })
        .then(company => {
          self.$customMsg.showSuccessMessage('Successfully ' + textForMessage + ' ' + company.name + ' company.')
          self.$emit('success', company)
        })
        .catch(errRes => {
          self.errMsg = self.$t(errRes.messageDetail)
        })
        .finally(() => {
          self.loading = false
        })
    },
    cancelEdit() {
      this.formData = Object.assign({}, this.company)
      this.$emit('cancel')
      this.editMode = false
      this.errMsg = null
    },
    edit() {
      this.editMode = true
    },
  },
  watch: {
    company(newCompany) {
      this.editMode = !newCompany.name
      this.formData = Object.assign({}, newCompany)
      this.errMsg = null
    },
  },
}
</script>



