import NotificationPanel from '@/components-app/notifications/NotificationPanel'

export default {
  components: {
    NotificationPanel,
  },
  data() {
    return {
      showPanel: false,
      loading: false,
      currentTime: null,
      currentNotification: 1,
      notificationsPerLoad: 10,
      windowHeight: 0,
    }
  },
  computed: {
    notifications() {
      return this.$store.getters.userNotifications
    },
    nextUserNotifications() {
      return this.$store.getters.nextUserNotifications
    },
    finalResult() {
      return this.nextUserNotifications.length === 0
    },
    notificationsCount() {
      return this.$store.getters.userNotificationsCount
    },
    payload() {
      let time = new Date(this.currentTime)
      return {
        currentTime: time.toISOString(),
        startIndex: this.currentNotification,
        endIndex: this.currentNotification + this.notificationsPerLoad - 1,
      }
    },
    panel_style() {
      return {
        height: this.windowHeight / 2 + 'px',
      }
    },
  },
  created() {
    this.$store.dispatch('loadUserNotificationsCount')
  },
  mounted() {
    this.$nextTick(function() {
      window.addEventListener('resize', this.getWindowHeight)
      this.getWindowHeight()
    })
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.getWindowHeight)
  },
  methods: {
    getWindowHeight() {
      this.windowHeight = document.documentElement.clientHeight
    },
    switchUserNotification() {
      this.$store.dispatch('seeNotifications')
      this.showPanel = !this.showPanel
      if (this.showPanel) {
        this.currentTime = Date.now()
        this.loadUserNotification()
      } else {
        this.currentNotification = 1
      }
    },
    loadMore() {
      this.currentNotification += this.notificationsPerLoad
      this.$store.dispatch('loadNextUserNotifications', this.payload)
        .then(() => {
          this.loading = false
        })
    },
    loadUserNotification() {
      this.loading = true
      this.$store.dispatch('loadUserNotifications', this.payload)
        .then(() => {
          this.$store.dispatch('markUserNotificationsOld')
          this.loadMore()
        })
    },
    markUserNotificationsRead() {
      this.loading = true
      this.$store.dispatch('markUserNotificationsRead')
        .then(() => {
          this.loading = false
        })
    },
    loadNextUserNotifications() {
      this.loading = true
      if (!this.finalResult) {
        this.$store.dispatch('addUserNotifications', this.nextUserNotifications)
          .then(() => {
            this.loadMore()
          })
      }
    },
    goToUserNotifications() {
      this.showPanel = !this.showPanel
      this.currentNotification = 1
      this.$router.push({name: 'Notifications', params: { type: 'user' }})
    },
    handleScroll(e) {
      if (e.target.scrollHeight - e.target.scrollTop - e.target.clientHeight < 100 &&
        !this.loading && !this.finalResult) {
        this.loadNextUserNotifications()
      }
    },
    toggleUserNotification(current) {
      this.$store.dispatch('switchUserNotificationReadStatus', current.id)
    },
  },
  watch: {
    showPanel(toggle) {
      if (toggle) {
        document.body.classList.add('notification_open')
      } else {
        document.body.classList.remove('notification_open')
      }
    },
  },
}



