import CompanySelect from '@/components-app/selects/CompanySelect'
import CompanyCard from '@/components-app/CompanyCard'

export default {
  components: { CompanySelect, CompanyCard },
  created() {
    this.$store.dispatch('loadMyCompanies')
  },
  data() {
    return {
      loading: false,
    }
  },
  computed: {
    myCompanies() {
      return this.$store.getters.myCompanies
    },
    contacts() {
      return this.$store.getters.contacts
    },
    company() {
      return this.$store.getters.selectedCompany || {}
    },
    myNotContactedCompanies() {
      let contactsIds = []
      this.contacts.forEach(contact => {
        contactsIds.push(contact.id)
      })
      return this.myCompanies.filter((current) => {
        return !contactsIds.includes(current.id) && current.id !== this.company.id
      })
    },
  },
  methods: {
    calcAliases(current) {
      let companyAliases = ''
      current.forEach((alias) => {
        companyAliases += alias.aname + ', '
      })
      companyAliases = companyAliases.slice(0, -2)
      return companyAliases !== '' ? companyAliases : this.$t('no aliases')
    },
    submit() {
      this.loading = true
      let contactsIds = []
      this.myCompanies.forEach(current => {
        if (current.checked) { contactsIds.push(current.id) }
      })
      this.$store.dispatch('inviteMyCompanyContacts', contactsIds)
        .then((invitation) => {
          this.$customMsg.showSuccessMessage(this.$t('Invitation successfully send.'))
          this.$emit('success')
        })
        .catch(errRes => {
          this.errMsg = this.$t(errRes.messageDetail)
        })
        .finally(() => {
          this.loading = false
        })
    },
  },
}




