import vue from 'vue'

let messagesOptions = {
  duration: 2500,
  action: {
    text: 'X',
    onClick: (e, toastObject) => {
      toastObject.goAway(0)
    },
  },
}

let showMessage = (message, options) => {
  vue.toasted.show(message, options)
}

export default {
  install: (Vue) => {
    Vue.prototype.$customMsg = {
      showErrorMessage(message) {
        let options = Object.assign({}, messagesOptions, {
          type: 'error',
          duration: 5000,
        })
        showMessage(message, options)
      },
      showSuccessMessage(message) {
        let options = Object.assign({}, messagesOptions, {
          type: 'success',
        })
        showMessage(message, options)
      },
      showInfoMessage(message) {
        let options = Object.assign({}, messagesOptions, {
          type: 'info',
        })
        showMessage(message, options)
      },
    }
  },
}



