import ExceptionsModal from './ExceptionsModal/'
import MatchedModal from './MatchedModal/'

export default {
  name: 'ReconciliationSummaryTable',
  components: {
    ExceptionsModal,
    MatchedModal,
  },
  data() {
    return {
      openExceptionsModal: false,
      openMatchedModal: false,
      isIN: false,
      isOUT: false,
      loading: false,
    }
  },
  props: ['recId'],
  computed: {
    selectedReconciliation() {
      return this.$store.getters.selectedReconciliation
    },
    company() {
      return this.$store.getters.selectedCompany || {}
    },
    summary() {
      return this.$store.getters.selectedReconciliationSummary
    },
    statementContext() {
      return {
        IN: this.selectedReconciliation.context ? this.selectedReconciliation.context.IN : '',
        OUT: this.selectedReconciliation.context ? this.selectedReconciliation.context.OUT : '',
      }
    },
  },
  methods: {
    autoMatch(areIN, areOUT) {
      var self = this
      self.loading = true
      var payload = {
        areIN: areIN,
        areOUT: areOUT,
        selectedReconciliation: this.selectedReconciliation,
      }
      self.$store.dispatch('matchAuto', payload)
        .then(() => {
          this.loadReconciliation()
        })
        .finally(() => {
          self.loading = false
        })
    },
    showExceptions(isIN, isOUT) {
      this.isIN = !!isIN
      this.isOUT = !!isOUT
      this.openExceptionsModal = true
    },
    showMatched(isIN) {
      this.isIN = !!isIN
      this.openMatchedModal = true
    },
    onClose() {
      if (!this.loading) {
        this.loadReconciliation()
        this.openExceptionsModal = false
        this.openMatchedModal = false
      }
    },
    loadReconciliation() {
      this.loading = true
      return Promise.all([
        this.$store.dispatch('loadSelReconciliation', this.recId),
        this.$store.dispatch('loadSelReconciliationSummary', this.recId),
      ])
        .finally(() => {
          this.loading = false
        })
    },
  },
}



