import CompaniesTable from './CompaniesTable/index'


export default {
  components: {
    CompaniesTable,
  },
  created() {
    this.loading = true
    this.$store.dispatch('loadMyCompanies')
      .finally(() => {
        this.loading = false
      })
  },
  data() {
    return {
      loading: false,
    }
  },
  computed: {
    myCompanies() {
      return this.$store.getters.myCompanies
    },
  },
}



