import dateUtils from '@/helpers/dateUtils'

// RECORDS

function apiToRecord(data) {
  return {
    id: data.id,
    accType: data.accountText,
    setId: data.setId,
    ref: data.referenceText,
    viewDocDate: new Date(data.documentDate),
    viewPostingDate: new Date(data.postingDate),
    fxRate: data.currentReportFxRate,
    docCurrency: data.currencyCode,
    docAmountStr: data.documentAmount,
    amountInRepCurrencyStr: data.amountInReportCurrency,
    description: data.description,
    isOwn: data.isOwn,

  }
}

function recordsFormDataToApi(data, selectedCompany) {
  return {
    companyId: selectedCompany.id,
    statementTypeId: data.statementType.id,
    refCompanyId: data.selectedContact.id,
    startDate: data.fromDate ? dateUtils.formatDate(data.fromDate) : '1900-01-01',
    endDate: data.toDate ? dateUtils.formatDate(data.toDate) : null,
  }
}

function initialDataToApi(data, selectedCompany) {
  return {
    companyId: selectedCompany.id,
    statementTypeId: data.statementType.id,
    startDate: data.fromDate ? dateUtils.formatDate(data.fromDate) : null,
    endDate: data.toDate ? dateUtils.formatDate(data.toDate) : null,
  }
}

export {
  apiToRecord,
  recordsFormDataToApi,
  initialDataToApi,
}



