import Datepicker from 'vuejs-datepicker'
import {en, bg} from 'vuejs-datepicker/dist/locale'
import {STATEMENT_TYPES} from '@/nomenclaturesConstants'

export default {
  components: {
    Datepicker,
  },
  data() {
    return {
      locales: {
        en: en,
        bg: bg,
      },
      formData: {},
      statementTypes: STATEMENT_TYPES,
      isFileCorrect: null,
      formSubmitted: false,
      errMsg: null,
      loading: false,
    }
  },
  props: {
    uploadData: {},
  },
  created() {
    if (this.uploadData !== null) {
      setTimeout(() => {
        this.formData = {
          fromDate: this.uploadData.startDate,
          toDate: this.uploadData.endDate,
        }
        this.statementTypes.forEach((statement) => {
          if (statement.id === this.uploadData.statementID) {
            this.formData.statementType = statement
          }
        })
      },500)
    }
  },
  methods: {
    checkFile() {
      this.isFileCorrect = false
      this.$validator.errors.remove('excelFile')
      if (typeof this.formData.file === 'undefined') {
        this.$validator.errors.add({
          field: 'excelFile',
          msg: this.$t('No file selected'),
        })
      } else {
        let ext = this.formData.file.name.substr(this.formData.file.name.lastIndexOf('.') + 1)
        if (ext === 'xls' || ext === 'xlsx') {
          this.isFileCorrect = true
        } else {
          this.$validator.errors.add({
            field: 'excelFile',
            msg: this.$t('Please, select an Excel file'),
          })
        }
      }
    },
    reValidateOnChange() {
      if (this.formSubmitted) {
        setTimeout(() => {
          this.checkFile()
          this.$validator.validateAll()
        }, 100)
      }
    },
    validateBeforeSubmit() {
      setTimeout(() => {
        this.checkFile()
        this.formSubmitted = true
        this.$validator.validateAll().then((result) => {
          if (result && this.isFileCorrect) {
            // Sending data to store
            this.loading = true

            this.$store.dispatch('uploadExcel', this.formData)
              .then(() => {
                this.$emit('success')
              })
              .catch(errRes => {
                this.errMsg = this.$t(errRes.messageDetail)
              })
              .finally(() => {
                this.loading = false
              })
          }
        })
      }, 100)
    },
  },
}



