import {ROLES} from '@/nomenclaturesConstants'

export default {
  name: 'CollaboratorsTableRow',
  props: {
    collaborator: {
      required: true,
    },
    pageItems: { },
  },
  data() {
    return {
      model: Object.assign({}, this.collaborator),
      roles: ROLES,
      editMode: false,
      loading: false,
    }
  },
  methods: {
    save() {
      if (this.collaborator.permittedRoles.indexOf(this.model.role.id) === -1) {
        this.$customMsg.showErrorMessage(this.$t('You do not have enough permissions to change to this role!'))
      } else {
        return this.$store.dispatch('updateCollaborator', this.model)
          .then(() => {
            this.$emit('reload')
          })
          .then(() => {
            this.editMode = false
          })
      }
    },
    edit() {
      if (this.collaborator.isLastSU) {
        this.$customMsg.showErrorMessage(this.$t('Last SuperUser cannot be edited!'))
      }
      this.editMode = !this.collaborator.isLastSU
    },
    cancel() {
      this.model = Object.assign({}, this.collaborator)
      this.editMode = false
    },
    remove() {
      if (this.collaborator.canBeDeleted) {
        return this.$store.dispatch('removeCollaborator', this.model)
          .then(() => {
            this.$emit('reload')
          })
      } else {
        this.$customMsg.showErrorMessage(this.$t('This user cannot be deleted!'))
      }
    },
  },
}



