<template>
  <cp-form :save="save"
           :remove="remove"
           :cancel="cancel"
           :validate="validate"
           :edit-mode="!cProp.value"
           @edit="onEdit">

    <div class="cp-row cp-pair">

      <VueCustomSelect
        :options="keys"
        :value="cpModel"
        @input="onInput"
        name="customSelect"
        :class="[{ 'is-invalid': errors.has('key')}]"
        :disabled="!editMode"
        v-validate="'required'"
        :loading="loading"
        :hasError="fields.customSelect && !fields.customSelect.valid && fields.customSelect.touched">
      </VueCustomSelect>

      <textarea class="form-control"
                v-model="cpModel.value"
                name="value"
                v-validate="'required'"
                :class="[{ 'is-invalid': errors.has('value')}]"
                :disabled="!editMode"
                rows="3">
      </textarea>
    </div>

  </cp-form>
</template>


<script>
import merge from 'lodash.merge'
import basePropMixin from './basePropMixin'

import stringUtils from '@/helpers/stringUtils'
import {COMPANY_PROPERTY_CATEGORIES} from '@/nomenclaturesConstants'
import VueCustomSelect from '@/components-lib/vue-custom-select/VueCustomSelect'

const cpVueConfig = {
  components: {
    VueCustomSelect,
  },
  created() {
    if (!this.cProp.category) {
      this.cpModel.category = {
        id: COMPANY_PROPERTY_CATEGORIES[this.cat],
      }
    }

    // TODO: ?? does it work, shoud use $ref na CpForm??
    this.loading = true
    var action = 'load' + stringUtils.capitalizeFirstLetter(this.storeKeysName)
    this.$store.dispatch(action)
      .finally(() => {
        this.loading = false
      })
  },
  props: {
    cProp: {
      required: false,
      default() {
        return {key: {}}
      },
    },
    cat: {
      required: true,
    },
    loadingProp: {
      default: false,
    },
  },
  data() {
    return {
      cpModel: Object.assign({}, this.cProp),
      storeKeysName: this.cat + 'Keys',
      editMode: !this.cProp.value,
      loading: false,
    }
  },
  computed: {
    keys() {
      return this.$store.getters[this.storeKeysName]
    },
  },
  methods: {
    save() {
      const action = this.cProp.value ? 'updateSCCPPair' : 'createSCCPPair'
      return this.$store.dispatch(action, this.cpModel)
        .then(() => {
          this.$emit('success')
          this.loading = true
        })
    },
    remove() {
      return this.$store.dispatch('deleteSCCPPair', this.cProp)
        .then(() => {
          this.$emit('remove')
        })
    },
    onInput(data) {
      this.cpModel.key = data.key
      data.key.id = (data.key.id === 'input' ? null : data.key.id)
      this.cpModel.customKey = data.customKey
    },
  },
  watch: {
    cProp: {
      handler() {
        this.cpModel = Object.assign({}, this.cProp)
        this.loading = false
      },
      deep: true,
    },
    loadingProp(val) {
      this.loading = val
    },
  },
}

export default merge({}, basePropMixin, cpVueConfig)
</script>



